import React, { Component } from 'react'
import './ChronicOrderTable.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'
import * as UDOR from '../../Resource/UDORDER0'

class Chronicordertable extends Component {
    state = {
        UDORDER: [],
        whiteBlackListParser: [],
        preHID: null,
        preCID: null
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        this.setState({ preHID: HID, preCID: CID })
        this.getUDORDER(HID, CID)

    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preHID, preCID } = this.state
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        if (preHID !== HID || preCID !== CID) {
            this.setState({ preHID: HID, preCID: CID })
            this.getUDORDER(HID, CID)
        }
    }
    getUDORDER(HID, CID) {
        ApiGithub.getGitHubJson("WhiteBlackList.json")
            .then(data => {
                let parser = data.data;
                this.setState({ whiteBlackListParser: parser })
            })
            .catch(err => { console.log(err) })
        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                this.setState({ UDORDER: data.data })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        // this.setState({ UDORDER: UDOR.data })
    }
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    select_parser = (input, parser) => {
        if (parser === undefined) return undefined;
        return parser.find(x =>
            input.match(x.parser_name)
        )
    }
    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let excludeBlackListResult = [];
        const UDGNAME_condition = mappings.find(item => item.key === "UDGNAME");
        const UDRPNAME_condition = mappings.find(item => item.key === "UDRPNAME");
        if (inputs === undefined) return []
        inputs.forEach(input => {
            const UDGNAME_re = new RegExp(UDGNAME_condition.regex_str, UDGNAME_condition.regex_flags)
            const UDGNAME_match = input.UDGNAME.match(UDGNAME_re)

            const UDRPNAME_re = new RegExp(UDRPNAME_condition.regex_str, UDRPNAME_condition.regex_flags)
            const UDRPNAME_match = input.UDRPNAME.match(UDRPNAME_re)

            if (UDGNAME_match === null && UDRPNAME_match === null) {
                excludeBlackListResult.push(input);
            }
        });

        return excludeBlackListResult;
    }
    celltable() {
        function changegridnumber(key) {
            switch (key) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state } = this.props
        const number = changegridnumber(chronic_state)
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div key={index} className='cell'></div>
            )

        }
        return arr
    }
    render() {
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'ordertableoneday'
                case 1:
                    return 'ordertablethirdday'
                case 2:
                    return 'ordertableoneweek'
                case 3:
                    return 'ordertabletwoweek'
                default:
                    return null
            }
        }
        function ListData(UDORDER0, interval, pickerdate) {
            function startTimeFormat(st, sttimeString) {
                if (sttimeString < st) {
                    return st
                }
                return sttimeString
            }
            function endTimeFormat(et, ettimestring) {
                if (et < ettimestring) {
                    return et
                }
                return ettimestring
            }
            function shiftWeight(startTime, st, et) {
                const width = document.body.clientWidth - 220
                const shiftTimeStampWidth = width * (startTime - st) / (et - st)
                return shiftTimeStampWidth

            }
            function WeightWidth(startTime, EndTime, st, et) {
                const width = document.body.clientWidth - 220
                const TimeStampWidth = width * (EndTime - startTime) / (et - st)
                return TimeStampWidth
            }

            function formatTimer(pickerdate, interval) {
                const endTime = pickerdate + (31 * 60 * 60 * 1000)
                if (interval === 0) {
                    return {
                        startTime: endTime - (24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                if (interval === 1) {
                    return {
                        startTime: endTime - (3 * 24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                if (interval === 2) {
                    return {
                        startTime: endTime - (7 * 24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                return {
                    startTime: endTime - (14 * 24 * 60 * 60 * 1000),
                    endTime: endTime
                }
            }
            function formatWidth(startTime, endTime, elementStart, elementEnd) {
                function formatStart(elementStart, startTime) {
                    if (elementStart <= startTime) {
                        return startTime
                    }
                    return elementStart
                }
                function formatEnd(elementEnd, endTime) {
                    if (elementEnd <= endTime) {
                        return elementEnd
                    }
                    return endTime
                }
                const width = document.body.clientWidth - 220
                const widthRate = width / (endTime - startTime)
                const start = formatStart(elementStart, startTime)
                const end = formatEnd(elementEnd, endTime)
                return (end - start) * widthRate
            }
            function formatStartPosition(startTime, endTime, elementStart) {
                const width = document.body.clientWidth - 220
                if (elementStart <= startTime) {
                    return 0
                }
                const widthRate = width / (endTime - startTime)
                return (elementStart - startTime) * widthRate
            }
            function formatUDORDER0(UDORDER0, timer) {
                var arraylist = []
                const startTime = timer.startTime
                const endTime = timer.endTime
                for (let index = 0; index < UDORDER0.length; index++) {
                    const element = UDORDER0[index];
                    const elementStart = moment(element.UDBGNDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    const elementEnd = moment(element.UDENDDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    if (endTime < elementStart || elementEnd < startTime) {
                        continue
                    }
                    const shift = formatStartPosition(startTime, endTime, elementStart)
                    const width = formatWidth(startTime, endTime, elementStart, elementEnd)
                    arraylist.push({
                        "width": width,
                        "shift": shift,
                        "element": element
                    })
                }
                return arraylist
            }
            const timer = formatTimer(pickerdate, interval)
            if (UDORDER0 === undefined || UDORDER0 === null) {
                return []
            }
            if (UDORDER0.length === 0) {
                return []
            }
            const formatUDORDER = formatUDORDER0(UDORDER0, timer)
            return formatUDORDER
        }



        const { chronic_state, PickerDate } = this.props
        // console.log(UDORDER0)
        // console.log(ListData(UDORDER0, chronic_state, PickerDate))

        //filter white list

        const UDORDER0 = this.state.UDORDER
        const parser = this.state.whiteBlackListParser
        const selected_parser = this.select_parser("Black List", parser)

        let filteredData = [];
        if (selected_parser !== undefined) {
            filteredData = this.parse(UDORDER0, selected_parser)
        }

        const datalist = ListData(filteredData, chronic_state, PickerDate)
        // const datalist = ListData(UDORDER0, chronic_state, PickerDate)

        const Tablecell = datalist.map(function (value, index, array) {
            function formatString(value) {
                if (value === undefined || value === null) {
                    return "--"
                }
                return value.toString().trim()
            }
            // console.log(value.width)
            return (
                <div key={index} style={{ padding: "10px 0px", paddingLeft: value.shift + "px" }}>
                    <div style={{ width: value.width + "px", height: "20px", backgroundColor: "#3B97E1", color: "black", padding: "0px 5px", textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{formatString(value.element.UDGNAME)}</div>
                </div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", position: 'relative' }}>
                <div style={{ background: "#CCCCCC", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>醫藥囑</t2>
                </div>
                <div className={changegridstyle(chronic_state)}>{this.celltable()}</div>
                <div style={{ position: 'absolute', left: 140, height: "250px", overflow: "hidden" }}>
                    {Tablecell}
                </div>
            </div>
        );
    }
}

export default Chronicordertable