import React, { Component } from 'react';
import Treatmentordertable from './Treatmentordertable'
import { DatePicker, Select, Input, Button } from 'antd';
import * as ApiGithub from '../../API/ApiGithub'
import moment from 'moment'
import './Treatmentorder.scss'

class Treatmentorderlayout extends Component {
    state = {
        // PickerDateRange: [moment().subtract(6, 'days'), moment()],
        parser: { "mappings": [] },
        DateRangeMode: {},
        class: "全部",
        SelectMode: 0,
        InputMode: '',
    }

    componentDidMount() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Treatment");
                parser.mappings = [{ "key": "全部", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Treatment OK " + JSON.stringify(parser))
                this.setState({
                    parser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    // onChange = (date, dateString) => {
    //     this.setState({
    //         PickerDateRange: date
    //     })
    // }

    onChangeClass = (value) => {
        this.setState({ class: value })
    }

    handleChange = (value) => {
        this.setState({ SelectMode: value })
    }

    onChangeinputKey = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    Search_bar = () => {
        const { RangePicker } = DatePicker;
        const { Option } = Select;

        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 140px auto", gridColumnGap: "10px", marginBottom: "5px" }}>
                {/* <RangePicker defaultValue={this.state.PickerDateRange} format={"YYYY/MM/DD"}
                    locale={"zh_TW"} placeholder={["請輸入日期", "請輸入日期"]} onChange={this.onChange} allowClear={false} /> */}
                <Select defaultValue={this.state.class} style={{ width: 140 }} onChange={this.onChangeClass}>
                    {this.state.parser.mappings.map((mapping, index) => {
                        return <Option key={index} value={mapping.key}>{mapping.key}</Option>
                    })}
                </Select>
                <Select defaultValue={0} style={{ width: 140 }} onChange={this.handleChange}>
                    <Option value={0}>全部</Option>
                    <Option value={1}>生效中</Option>
                    <Option value={2}>失效/已結束/取消</Option>
                </Select>
                <Input placeholder="輸入藥囑關鍵字" value={this.state.InputMode} onChange={this.onChangeinputKey} />
            </div >
        );

    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部"] = []

        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部"].push(input)
        });
        return allResult;
    }

    onClickSearch = () => {
        const { TRTMNT01 } = this.props
        const parse = this.parse;

        const activeList = TRTMNT01.filter(element => element.ORSTATUS === "80")
        console.log("activeList " + JSON.stringify(activeList));

        let clusteredData = {}
        if (this.state.parser !== undefined) {
            clusteredData = parse(activeList, this.state.parser)
        }

        //filter data by selected class
        let filteredData = clusteredData[this.state.class];

        const ListData = this.SortDataList(filteredData)

        return ListData
    }

    SortDataList = (DataList) => {
        // console.log(JSON.stringify(DataList))
        const { PickerDateRange } = this.props
        const { InputMode, SelectMode } = this.state

        if (SelectMode !== 0) {
            /*
            預設
            全部 SelectMode == 0
            已生效 SelectMode == 1, < 現在時間 <
            失效 SelectMode == 2, < 現在時間
            */
            DataList = DataList.filter(data => {
                const createST = moment(data.ORBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const createET = moment(data.ORENDDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const nowDate = moment().valueOf()

                switch (SelectMode) {
                    case 1:
                        return createST <= nowDate && nowDate <= createET
                        break;
                    case 2:
                        return createET < nowDate
                        break;
                    default: return null;
                }
            })
        }

        if (InputMode !== '') {
            DataList = DataList.filter(data => {
                return data.ORPROCED.match(InputMode) !== null
            })
        }

        var DataFormat = []

        const st = PickerDateRange[0]
        const et = PickerDateRange[1] + (24 * 60 * 60 * 1000)

        DataList.forEach(element => {
        	console.log(element)
            const time = moment(element.ORBGNDT.toString().trim(), 'YYYY-MM-DD').valueOf()
            console.log(time)
            if (st <= time && time <= et) {
                DataFormat.push(element)
            }
        })
        return DataFormat
    }

    render() {
        const { searchbar, caseinfo } = this.props

        const filterData = this.onClickSearch()

        return (
            <div>
                <div className={searchbar ? "order_style" : "crossection_style"}>
                    <t1>Treatment Order</t1>
                </div>
                {searchbar ? this.Search_bar() : null}
                <Treatmentordertable caseinfo={caseinfo} TRTMNT01={filterData} ></Treatmentordertable>
            </div>
        );
    }
}

export default Treatmentorderlayout
