import React, { Component } from 'react'
import { Select } from 'antd';
import Chronicvitalchart from './Chronic_vital_chart'
import { statecolor, simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'
import { formatxArrayhalf, formatendtime } from '../../Function/Timeformat'
import './Chronic_vital.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import Crosssectionvitalsignchart from '../CrossSectionView/Crossectionvitalsignchart'


class Vitalonchronic extends Component {
    state = {
        check_state_one: 0,
        check_state_three: 0,
        check_state_oneweek: 0,
        check_state_twoweek: 0,
        statussource: "CM",
        CenterMonitorValue: []
    }

    componentDidMount() {
        const startTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf() - (60 * 60 * 1000)).format("YYYYMMDDHHmmss")
        const endTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf()).format("YYYYMMDDHHmmss")
        const PHISTNUM = this.props.caseinfo.HISID.toString().trim()
        this.getVitalSignData(startTime, endTime, PHISTNUM)
    }

    getVitalSignData(startTime, EndTime, Phistnum) {
        ApiProvider.getPatientwardvalueMedian(Phistnum, startTime, EndTime)
            .then(data => {
                console.log(data.data)
                this.setState({
                    CenterMonitorValue: data.data
                })
            })
            .catch(err => {
                console.log(err)
                this.setState({
                    CenterMonitorValue: null
                })
            })
    }

    switchdata(value_interval, sourcefrom) {
        console.log(sourcefrom)
        const { defaultdate } = this.props
        function changevitaldays(vitaldays) {
            if (vitaldays === 3) {
                return 18
            }
            else {
                return 24
            }
        }

        function Interval_switch(interval) {
            if (interval === 0) {
                return 1
            }
            if (interval === 1) {
                return 3
            }
        }
        const interval = Interval_switch(value_interval)
        const starttime = formatendtime(defaultdate) - (interval * 24 * 60 * 60 * 1000)
        if (sourcefrom === 'NIS') {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationnisdata(datatime, changevitaldays(interval))
            console.log(data)
            return data
        }
        else {

            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationCentermonitordata(datatime, changevitaldays(interval))
            return data
        }
    }
    Interval_grid_box() {
        var gridarray = []
        console.log(this.props.select_interval)
        if (this.props.select_interval === 0) {
            for (let index = 0; index < 24; index++) {
                const number = index + 1
                if (number === this.state.check_state_one) {
                    gridarray.push(<div key={index} style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
                }
                else gridarray.push(<div key={index} style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
            }
        }
        if (this.props.select_interval === 1) {
            for (let index = 0; index < 18; index++) {
                const number = index + 1
                if (number === this.state.check_state_three) {
                    gridarray.push(<div key={index} style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_three(number)}></div>)
                }
                else gridarray.push(<div key={index} style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_three(number)}></div>)
            }
        }
        if (this.props.select_interval === 2) {
            for (let index = 0; index < 21; index++) {
                const number = index + 1
                if (number === this.state.check_state_oneweek) {
                    gridarray.push(<div key={index} style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_oneweek(number)}></div>)
                }
                else gridarray.push(<div key={index} style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_oneweek(number)}></div>)
            }
        }
        if (this.props.select_interval === 3) {
            for (let index = 0; index < 14; index++) {
                const number = index + 1
                if (number === this.state.check_state_twoweek) {
                    gridarray.push(<div key={index} style={{ width: "100%", height: 360, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_twoweek(number)}></div>)
                }
                else gridarray.push(<div key={index} style={{ width: "100%", height: 360 }} onClick={() => this.onClickCheckgrid_twoweek(number)}></div>)
            }
        }
        return gridarray
    }

    onClickCheckgrid_one(number) {
        console.log(number)
        const { PickerDate } = this.props
        const ST = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000) - (24 * 60 * 60 * 1000)
        const endtime = ST + (number * 60 * 60 * 1000)
        const starttime = endtime - 60 * 60 * 1000
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ check_state_one: number })
        this.getVitalSignData(start, end, Phistnum)
    }
    onClickCheckgrid_three(number) {
        console.log(number)
        const { PickerDate } = this.props
        const ST = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000)
        const endtime = ST + ((number * 4) * 60 * 60 * 1000)
        const starttime = endtime - (4 * 60 * 60 * 1000)
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ chackstate_three: number })
        this.getVitalSignData(start, end, Phistnum)
    }

    onClickCheckgrid_oneweek(number) {
        const { PickerDate } = this.props
        const ST = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
        const endtime = ST + ((number * 8) * 60 * 60 * 1000)
        const starttime = endtime - (8 * 60 * 60 * 1000)
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ check_state_oneweek: number })
        this.getVitalSignData(start, end, Phistnum)
    }
    onClickCheckgrid_twoweek(number) {
        const { PickerDate } = this.props
        const ST = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000) - (14 * 24 * 60 * 60 * 1000)
        const endtime = ST + ((number * 24) * 60 * 60 * 1000)
        const starttime = endtime - (24 * 60 * 60 * 1000)
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ check_state_twoweek: number })
        this.getVitalSignData(start, end, Phistnum)
    }

    sourcehandleChange = (value) => {
        this.setState({ statussource: value })
    }
    render() {
        function centervaluetoNull(value) {
            function FilterList(DataList, type) {
                const filterData = DataList.filter(function (value, index, arr) {
                    return value.type === type
                })
                if (filterData.length === 0) {
                    return null
                }
                console.log(filterData)
                return filterData[0].Mid
            }
            if (value === undefined || value === null) {
                return {
                    hr: null,
                    rr: null,
                    bt: null,
                    spo2: null,
                    abpd: null,
                    abpm: null,
                    abps: null,
                    nbpm: null,
                    nbps: null,
                    nbpd: null
                }
            }
            return {
                hr: FilterList(value, "hr"),
                rr: FilterList(value, "rr"),
                bt: FilterList(value, "bt"),
                spo2: FilterList(value, "spo2"),
                abpd: FilterList(value, "abpd"),
                abpm: FilterList(value, "abpm"),
                abps: FilterList(value, "abps"),
                nbpd: FilterList(value, "nbpd"),
                nbps: FilterList(value, "nbps"),
                nbpm: FilterList(value, "nbpm"),
            }
        }
        function centervalueNull(value) {
            if (value === undefined || value === null) {
                return "--"
            }
            return value
        }
        function Interval_grid(number) {
            switch (number) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    return null
            }
        }
        function formatPickerTimer(pickerdate, interval) {
            const end = pickerdate + (31 * 60 * 60 * 1000)
            if (interval === 0) {
                return {
                    startTime: end - (24 * 60 * 60 * 1000),
                    endTime: end
                }
            }
            if (interval === 1) {
                return {
                    startTime: end - (3 * 24 * 60 * 60 * 1000),
                    endTime: end
                }
            }
            if (interval === 2) {
                return {
                    startTime: end - (7 * 24 * 60 * 60 * 1000),
                    endTime: end
                }
            }
            else {
                return {
                    startTime: end - (14 * 24 * 60 * 60 * 1000),
                    endTime: end
                }
            }
        }

        const { Option } = Select;
        const { source } = this.props
        const pickerTimer = formatPickerTimer(this.props.PickerDate, this.props.select_interval)
        const width = window.screen.availWidth - 171
        const centervalue = centervaluetoNull(this.state.CenterMonitorValue)
        return (
            <div>
                <div style={{ display: "flex", alignItems: "center", height: "50px" }}>
                    <t1>生命徵象</t1>
                    <Select defaultValue={this.state.statussource} style={{ width: 200, marginLeft: "20px" }} onChange={this.sourcehandleChange}>
                        <Option value={'CM'}>Center Monitor</Option>
                        <Option value={'NIS'}>NIS</Option>
                    </Select>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr" }}>
                    <div style={{ display: "grid", gridTemplateRows: "repeat(6,60px)" }}>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <t4 style={{ display: "flex", justifyContent: "center", color: statecolor("HR") }}>{"HR"}</t4>
                                <t1 style={{ display: "flex", justifyContent: "center", color: statecolor("HR") }}>{centervalueNull(centervalue.hr)}</t1>
                            </div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>NBP</div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.nbpd)}/{centervalueNull(centervalue.nbps)}</div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>({centervalueNull(centervalue.nbpm)})</div>
                            </div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>ABP</div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.abpd)}/{centervalueNull(centervalue.abps)}</div>
                                <div style={{ fontSize: "1rem", color: "#C12528", display: "flex", justifyContent: "center" }}>({centervalueNull(centervalue.abpm)})</div>
                            </div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <div style={{ fontSize: "1rem", color: "#4773B8", width: "100px", display: "flex", justifyContent: "center" }}>
                                    SpO<sub style={{ bottom: "-1em" }}>2</sub>
                                </div>
                                <div style={{ fontSize: "1rem", color: "#4773B8", width: "100px", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.spo2)}%</div>
                            </div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <div style={{ fontSize: "1rem", color: "#7F3DB5", width: "100px", display: "flex", justifyContent: "center" }}>BT</div>
                                <div style={{ fontSize: "1rem", color: "#7F3DB5", width: "100px", display: "flex", justifyContent: "center" }}>--</div>
                            </div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div>
                                <div style={{ fontSize: "1rem", color: "#DE7531", width: "100px", display: "flex", justifyContent: "center" }}>RR</div>
                                <div style={{ fontSize: "1rem", color: "#DE7531", width: "100px", display: "flex", justifyContent: "center" }}>{centervalueNull(centervalue.rr)}</div>
                            </div>
                        </div>
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "repeat(6,60px)", position: "relative" }}>
                        <div style={{ position: "absolute", height: "340px", width: width - 50, top: 5, left: 50 }} >
                            <div style={{ display: "grid", gridTemplateColumns: "repeat(" + Interval_grid(this.props.select_interval) + ",1fr)" }}>
                                {this.Interval_grid_box()}
                            </div>
                        </div>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"hr_svg_" + source}
                            id_key={"hr_id_" + source}
                            type={"hr"}
                            source={this.state.statussource}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={100}
                            upper={160}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor="#779F50"
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={0}
                            max={200}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"nbp_svg" + source}
                            id_key={"nbp_id" + source}
                            type={"nbp"}
                            source={this.state.statussource}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={55}
                            upper={75}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor={"#C12528"}
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={0}
                            max={100}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"abp_svg" + source}
                            id_key={"abp_id" + source}
                            type={"abp"}
                            source={this.state.statussource}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={55}
                            upper={75}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor={"#C12528"}
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={0}
                            max={100}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"spo2_svg_" + source}
                            id_key={"spo2_id_" + source}
                            type={"spo2"}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={90}
                            upper={100}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor={"#4773B8"}
                            colorshadow={"#4773B8"}
                            min={60}
                            max={100}
                            source={this.state.statussource}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"bt_svg_" + source}
                            id_key={"bt_id_" + source}
                            type={"bt"}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            source={this.state.statussource}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={36}
                            upper={38}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor={"#7F3DB5"}
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={33}
                            max={43}>
                        </Crosssectionvitalsignchart>
                        <Crosssectionvitalsignchart
                            CID={this.props.caseinfo.CASEID}
                            HID={this.props.caseinfo.HISID}
                            PickerDate={this.props.PickerDate}
                            svg_id={"rr_svg_" + source}
                            id_key={"rr_id_" + source}
                            type={"rr"}
                            startTime={pickerTimer.startTime}
                            endTime={pickerTimer.endTime}
                            height={50}
                            width={width}
                            interval={this.props.select_interval}
                            lower={20}
                            upper={60}
                            axisBot={false}
                            axisTop={false}
                            circlrcolor={"#DE7531"}
                            colorshadow={"rgba(126, 211, 33, 0.5)"}
                            min={0}
                            max={100}
                            source={this.state.statussource}>
                        </Crosssectionvitalsignchart>
                    </div>
                </div>
            </div>
        );
    }
}

export default Vitalonchronic