import React, { Component } from 'react';
import Examnonlabviewtable from './ExamNonlabtablecell'
import { DatePicker, Input, Button, Select, Modal } from 'antd';
import moment from 'moment';
import searchlogo from '../../Image/svg/Search.svg'
import * as ApiGithub from '../../API/ApiGithub'
import InsReportModal from '../ExamReportModal'

// var mode_class, mode_state;
class Nonlabview extends Component {
    state = {
        ClassMode: "全部",
        StateMode: 0,
        InputMode: null,
        parser: { "mappings": [] },
        modalVisible: false,
        modalReport: ""
    }

    componentDidMount() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "NonLabExamReport");
                parser.mappings = [{ "key": "全部", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson ExamReportNonLab OK " + JSON.stringify(parser))
                this.setState({
                    parser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    Inputvaluesearch = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    onChangeState = (value) => {
        this.setState({ StateMode: value })
    }

    onChangeClass = (value) => {
        this.setState({ ClassMode: value })
    }

    showModal = (report) => {
        console.log("showModal :" + JSON.stringify(report))
        this.setState({
            modalVisible: true,
            modalReport: report
        });
    };

    handleOk = e => {
        this.setState({
            modalVisible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            modalVisible: false,
        });
    };

    render() {
        var statetime = {
            "Starttime": (+new Date()),
            "Endtime": (+new Date())
        }
        // const datearray = []
        const { Option } = Select;
        // function onChange(value, dateString) {
        //     statetime.Starttime = (+new Date(dateString[0]))
        //     statetime.Endtime = (+new Date(dateString[1]))
        //     datearray.push(statetime.Starttime)
        //     datearray.push(statetime.Endtime)
        // }
        // function Inputchange() {
        //     const value = {
        //         "date": datearray,
        //         "keyword": document.getElementById("keywordsearch").value
        //     }
        //     console.log(value)
        // }
        const { PickerDateRange, ORDLIST, RESSECT } = this.props
        const { modalVisible, modalReport } = this.state
        const showModal = this.showModal
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        return (
            <div>
                <t1>報告清單</t1>
                <div style={{ display: 'grid', gridTemplateColumns: "130px 130px auto", gridColumnGap: "5px", marginTop: "10px" }}>
                    <Select defaultValue={this.state.ClassMode} placeholder="選擇檢驗類別" style={{ width: 120 }} onChange={this.onChangeClass}>
                        {this.state.parser.mappings.map((mapping, index) => {
                            return <Option key={index} value={mapping.key}>{mapping.key}</Option>
                        })}
                    </Select>
                    <Select defaultValue={this.state.StateMode} placeholder="選擇項目狀態" style={{ width: 120 }} onChange={this.onChangeState}>
                        <Option value={0}>全部</Option>
                        <Option value={1}>已開單</Option>
                        <Option value={2}>已簽收</Option>
                        <Option value={3}>已發報告</Option>
                    </Select>
                    <Input id="keywordsearch" placeholder="輸入關鍵字" onChange={this.Inputvaluesearch} />
                    {/* <div onClick={() => this.Inputvaluesearch()} style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
                <Examnonlabviewtable PickerDateRange={PickerDateRange} ORDLIST={ORDLIST} RESSECT={RESSECT} selectedClass={this.state.ClassMode} parser={this.state.parser} StateMode={this.state.StateMode} InputMode={this.state.InputMode} visible={modalVisible} showModal={showModal} handleOk={handleOk} handleCancel={handleCancel}></Examnonlabviewtable>
                <Modal
                    title={"檢驗結果"}
                    visible={modalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1000}
                    footer={null}>
                    <InsReportModal report={modalReport}></InsReportModal>
                </Modal>
            </div>
        );
    }
}

export default Nonlabview
