import React, { Component } from 'react';
import Vitalsign from './Vitalsign';
import Controlbar from '../Controlbar';
import Crossection_io from './Crossection_io'
import Labviewlayout from './Labview'
import Nonlabviewlayout from './Nonlabview'
import GAS from './GAS'
import RTbreathe from './RTbreathe'
import Prescriptionorderlayout from '../Prescriptionorder/Prescriptionorder'
import Treatmentorderlayout from '../Treatmentorder/Treatmentorder'
import Crossection_schedule from './Crosssectionschedule'
import Crosssection_todo from './Crosssection_todo'
import './Crosssectionlayout.scss'
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

class Crosssectionlayout extends Component {
    state = {
        Cross_item: [0, 1, 2, 3, 4, 5],
        RESDGLU1: [],
        RESLAB02: [],
        RESLAB01: [],
        RESDBGAS: [],
        RESLAB0C: [],
        RESSECT: [],
        UDORDER0: [],
        TRTMNTQ1: [],

        PickerDate: moment(moment().format('YYYYMMDD')).valueOf(),

        SelectInterval: 1,

        preCID: null,
        preHID: null
    }
    componentDidMount() {
        const CID = this.props.caseinfo.CASEID
        const HID = this.props.caseinfo.HISID
        const TRDT = moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD').valueOf()
        this.setState({
            preCID: CID,
            preHID: HID
        })
        this.NIS_API_IO(this.state.PickerDate, CID, HID, TRDT)
    }
    componentDidUpdate() {
        const CID = this.props.caseinfo.CASEID
        const HID = this.props.caseinfo.HISID
        const TRDT = moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD').valueOf()
        const { preCID, preHID } = this.state
        if (preCID !== CID || preHID !== HID) {
            this.setState({
                preCID: CID,
                preHID: HID
            })
            this.NIS_API_IO(this.state.PickerDate, CID, HID, TRDT)
        }
    }
    NIS_API_IO(Datetime, CID, HID, TRDT) {
        function selectMonthInterval(Datetime) {
            const timestampInterval = new Date().getTime() - Datetime
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const TransinDate = moment(TRDT).format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(Datetime)
        ApiProvider.getStoredProcedureRESDGLU1(HID, TransinDate)
            .then(data => {
                this.setState({
                    RESDGLU1: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESDGLU1 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB02(HID, TransinDate)
            .then(data => {
                this.setState({ RESLAB02: data.data })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB02 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB01(HID, TransinDate)
            .then(data => {
                this.setState({
                    RESLAB01: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB01 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESDBGAS(HID, TransinDate)
            .then(data => {
                this.setState({
                    RESDBGAS: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESDBGAS NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB0C(HID, TransinDate)
            .then(data => {
                this.setState({
                    RESLAB0C: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESLAB0C NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESSECT(HID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                this.setState({
                    RESSECT: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err))
            });

        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureUDORDER0 NG " + JSON.stringify(err))
            });
        ApiProvider.getStoredProcedureTRTMNTQ1(HID, CID)
            .then(data => {
                this.setState({
                    TRTMNTQ1: data.data
                })
            })
            .catch(err => {
                console.log("getStoredProcedureTRTMNTQ1 NG " + JSON.stringify(err))
            });
    }

    callbackFunction = (item_array) => {
        this.setState({ Cross_item: item_array })
    }

    callbackDateFunction = (PickerTimeStamp) => {
        console.log(PickerTimeStamp)
        this.setState({ PickerDate: PickerTimeStamp })
    }

    display_item(number) {
        const item = this.state.Cross_item
        var filter = item.some(function (item, index, array) {
            return item === number
        });
        return filter
    }
    render() {
        const crosssectioncontrollist = [
            {
                "name": "生命徵象/輸出入",
                "scrollpoint": "vitalsignIO"
            },
            {
                "name": "檢驗結果",
                "scrollpoint": "lab"
            },
            {
                "name": "呼吸評估",
                "scrollpoint": "rt"
            },
            {
                "name": "醫藥囑",
                "scrollpoint": "order"
            },
            {
                "name": "行事曆及註記",
                "scrollpoint": "schedule"
            }
        ]
        const { caseinfo } = this.props
        const TimeStampPickerDate = this.state.PickerDate
        const endTime = TimeStampPickerDate + (31 * 60 * 60 * 1000)
        const startTime = endTime - (3 * 24 * 60 * 60 * 1000)
        const PickerDateRange = [TimeStampPickerDate - (6 * 24 * 60 * 60 * 1000), TimeStampPickerDate]
        console.log(moment(PickerDateRange[0]).format('YYYYMMDD'), moment(PickerDateRange[1]).format('YYYYMMDD'))

        return (
            <div>
                <Controlbar caseinfo={caseinfo} PickerDate={this.state.PickerDate} parentDateCallback={this.callbackDateFunction} parentCallback={this.callbackFunction} btnlist={crosssectioncontrollist} item_array={this.state.Cross_item}></Controlbar>
                <div id="crossection" className='crossectioncontent'>
                    <div id={"vitalsignIO"} style={this.display_item(0) ? { display: "grid", gridTemplateColumns: "2fr 1fr", gridGap: "10px", marginBottom: "10px" } : { display: "none" }}>
                        <Vitalsign PickerDate={this.state.PickerDate} caseinfo={caseinfo}></Vitalsign>
                        <Crossection_io PickerDate={this.state.PickerDate} caseinfo={caseinfo} title="I/O"></Crossection_io>
                    </div>
                    <div id={"lab"} style={this.display_item(1) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Labviewlayout PickerDate={this.state.PickerDate} caseinfo={caseinfo} RESLAB01={this.state.RESLAB01} RESLAB02={this.state.RESLAB02} RESDBGAS={this.state.RESDBGAS} RESLAB0C={this.state.RESLAB0C} RESDGLU1={this.state.RESDGLU1} RESSECT={this.state.RESSECT}></Labviewlayout>
                        <Nonlabviewlayout caseinfo={caseinfo} StartTime={startTime} EndTime={endTime}></Nonlabviewlayout>
                    </div>
                    <div id={"rt"} style={this.display_item(2) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <GAS caseinfo={caseinfo} PickerDateRange={PickerDateRange} searchbar={false}></GAS>
                        <RTbreathe caseinfo={caseinfo} PickerDateRange={PickerDateRange} searchbar={false}></RTbreathe>
                    </div>
                    <div id={"order"} style={this.display_item(3) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Prescriptionorderlayout UDORDER0={this.state.UDORDER0} caseinfo={caseinfo} searchbar={false} PickerDateRange={PickerDateRange}></Prescriptionorderlayout>
                        <Treatmentorderlayout TRTMNT01={this.state.TRTMNTQ1} caseinfo={caseinfo} searchbar={false} PickerDateRange={PickerDateRange}></Treatmentorderlayout>
                    </div>
                    <div id={"schedule"} style={this.display_item(4) ? null : { display: "none" }}>
                        <Crossection_schedule source={1} PickerDate={this.state.PickerDate} editstatus={false} caseinfo={caseinfo}></Crossection_schedule>
                    </div>
                    <div style={this.display_item(5) ? null : { display: "none" }}>
                        <Crosssection_todo caseinfo={caseinfo} ></Crosssection_todo>
                    </div>
                </div>
            </div>
        );
    }
}

export default Crosssectionlayout;
