import React, { Component } from 'react';
import Prescriptionordertable from './Prescriptionordertable'
import { DatePicker, Select, Input } from 'antd';
import './Prescriptionorder.scss'
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'

class Prescriptionorderlayout extends Component {
    state = {
        classParser: { "mappings": [] },
        class: "全部",
        SelectMode: 0,
        InputMode: null,
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Prescription");
                parser.mappings = [{ "key": "全部", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Prescription OK " + JSON.stringify(parser))
                this.setState({
                    classParser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    onChangeClass = (value) => {
        this.setState({ class: value })
    }

    handleChange = (value) => {
        this.setState({ SelectMode: value })
    }

    onChangeinputKey = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    ControlBarRender() {
        const { Option } = Select;
        return (

            <div class="searchBar" style={{ display: "grid", gridTemplateColumns: "140px 140px auto", gridColumnGap: "10px", marginBottom: "5px" }}>
                <Select defaultValue={this.state.class} style={{ width: 140 }} onChange={this.onChangeClass}>
                    {this.state.classParser.mappings.map((mapping, index) => {
                        return <Option value={mapping.key}>{mapping.key}</Option>
                    })}
                </Select>
                <Select defaultValue={this.state.SelectMode} style={{ width: 140 }} onChange={this.handleChange}>
                    <Option value={0}>全</Option>
                    <Option value={1}>生效中</Option>
                    <Option value={2}>失效/已結束/取消</Option>
                </Select>
                <Input id="prescriptionorderinput" placeholder="輸入藥囑關鍵字" onChange={this.onChangeinputKey} />
            </div >
        );
    }

    parseClass = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部"] = []

        inputs.forEach(input => {
            let content = input.UDGNAME;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部"].push(input)
        });
        return allResult;
    }

    onClickSearch = () => {
        const { UDORDER0 } = this.props
        //const UDORDER0 = [{"UDDCPRSN":"        ","UDDURAT":"001","UDSUMQTY":"1","UDDRGNAM":"Cefotaxime (Claforan)*inj 500 mg","UDGNAME":"Cefotaxime (Claforan)*inj 500 mg","UDORSIGN":"DOC3964C","UDSELF":"N","UDSTATUS":"60","UDPERNAM":"李成彧　　","UDENDDT":"2019-08-13-15.00.00.000000","UDDCPNAM":"　　　　　","UDDOSAGE":"60      ","UDORDSEQ":"0011","UDBGNDT":"2019-08-12-13.04.00.000000","UDDRGNO":"01325","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"MG    ","UDROUTE":"IVA","UDRPNAME":"Claforan iv * inj 500 mg        ","UDFREQN":"STAT    "},{"UDDCPRSN":"DOC3941E","UDDURAT":"014","UDSUMQTY":"2","UDDRGNAM":"Heparin sod \"CCP#* inj 5000u/5ml","UDGNAME":"Heparin sod \"CCP#* inj 5000u/5ml","UDORSIGN":"DOC3934E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"陳裕璇　　","UDENDDT":"2019-08-19-11.45.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"X1      ","UDORDSEQ":"0025","UDBGNDT":"2019-08-12-19.38.00.000000","UDDRGNO":"00607","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"VL    ","UDROUTE":"IVA","UDRPNAME":"Agglutex #* inj 1000 u/ml 5 ml  ","UDFREQN":"QD      "},{"UDDCPRSN":"DOC3941E","UDDURAT":"001","UDSUMQTY":"0","UDDRGNAM":"Immunoglobulin *inj 60mg/ml 50ml","UDGNAME":"Immunoglobulin *inj 60mg/ml 50ml","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-14-10.20.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"1       ","UDORDSEQ":"0054","UDBGNDT":"2019-08-14-08.51.00.000000","UDDRGNO":"03852","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"BOT   ","UDROUTE":"IVA","UDRPNAME":"HM.imm.globulin *inj60mg/ml 50ml","UDFREQN":"STAT    "},{"UDDCPRSN":"DOC3941E","UDDURAT":"007","UDSUMQTY":"110","UDDRGNAM":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDGNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-16-10.44.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"55      ","UDORDSEQ":"0055","UDBGNDT":"2019-08-14-09.17.00.000000","UDDRGNO":"02145","UDCOMMET":"Y","UDVALUE":"                                                                                                    ","UDDUNIT":"CC    ","UDROUTE":"IVD","UDRPNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDFREQN":"QD-TPN  "},{"UDDCPRSN":"DOC3915L","UDDURAT":"005","UDSUMQTY":"4","UDDRGNAM":"Fluconazole inj 100 mg/50 ml    ","UDGNAME":"Fluconazole inj 100 mg/50 ml    ","UDORSIGN":"DOC3964C","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"李成彧　　","UDENDDT":"2019-08-18-08.57.00.000000","UDDCPNAM":"李苡萱　　","UDDOSAGE":"6.6     ","UDORDSEQ":"0053","UDBGNDT":"2019-08-14-10.07.00.000000","UDDRGNO":"01885","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"MG    ","UDROUTE":"IVA","UDRPNAME":"Diflucan iv inj 100 mg/50 ml    ","UDFREQN":"QPM     "},{"UDDCPRSN":"        ","UDDURAT":"001","UDSUMQTY":"1","UDDRGNAM":"Dextrose inj 10% 500 ml         ","UDGNAME":"Dextrose inj 10% 500 ml         ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"60","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-15-15.00.00.000000","UDDCPNAM":"　　　　　","UDDOSAGE":"30      ","UDORDSEQ":"0060","UDBGNDT":"2019-08-14-10.48.00.000000","UDDRGNO":"80051","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"CC    ","UDROUTE":"IVD","UDRPNAME":"Dextrose inj 10% 500 ml \"YF\"    ","UDFREQN":"ST      "},{"UDDCPRSN":"        ","UDDURAT":"001","UDSUMQTY":"0","UDDRGNAM":"Dopamine #* inj 200 mg/5 ml     ","UDGNAME":"Dopamine #* inj 200 mg/5 ml     ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"60","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-15-15.00.00.000000","UDDCPNAM":"　　　　　","UDDOSAGE":"20      ","UDORDSEQ":"0058","UDBGNDT":"2019-08-14-10.48.00.000000","UDDRGNO":"02220","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"MG    ","UDROUTE":"IVA","UDRPNAME":"Dopamin #* inj 200 mg/5 ml \"VPP\"","UDFREQN":"ST      "},{"UDDCPRSN":"DOC3941E","UDDURAT":"014","UDSUMQTY":"5","UDDRGNAM":"Dextrose inj 10% 500 ml         ","UDGNAME":"Dextrose inj 10% 500 ml         ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-19-11.45.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"30      ","UDORDSEQ":"0059","UDBGNDT":"2019-08-14-10.48.00.000000","UDDRGNO":"80051","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"CC    ","UDROUTE":"IVD","UDRPNAME":"Dextrose inj 10% 500 ml \"YF\"    ","UDFREQN":"QD      "},{"UDDCPRSN":"        ","UDDURAT":"001","UDSUMQTY":"1","UDDRGNAM":"Salbutamol inh. sol 5 mg/2.5 ml ","UDGNAME":"Salbutamol inh. sol 5 mg/2.5 ml ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"60","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-15-15.00.00.000000","UDDCPNAM":"　　　　　","UDDOSAGE":"1       ","UDORDSEQ":"0061","UDBGNDT":"2019-08-14-13.51.00.000000","UDDRGNO":"03120","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"AMP   ","UDROUTE":"IH ","UDRPNAME":"Ventolin inh. sol 5 mg/2.5 ml   ","UDFREQN":"ST      "}]
        const parseClass = this.parseClass

        let ListData = [];
        const activeList = UDORDER0.filter(element => element.UDSTATUS === "80")
        console.log("activeList " + JSON.stringify(activeList));

        let clusteredData = {}
        if (this.state.classParser !== undefined) {
            clusteredData = parseClass(activeList, this.state.classParser)
        }
        console.log("clusteredData " + JSON.stringify(clusteredData));

        //filter data by selected class
        let filteredData = clusteredData[this.state.class];

        ListData = this.SortDataList(filteredData)
        return ListData
    }

    SortDataList = (DataList) => {
        const { PickerDateRange } = this.props
        const { InputMode, SelectMode } = this.state
        console.log(moment(PickerDateRange[0]).format('YYYYMMDD'), moment(PickerDateRange[1]).format('YYYYMMDD'))

        if (SelectMode !== 0) {
            console.log(SelectMode)
            /*
            預設
            全部 SelectMode == 0
            已生效 SelectMode == 1, < 現在時間 <
            失效 SelectMode == 2, < 現在時間
            */
            DataList = DataList.filter(data => {
                const createST = moment(data.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const createET = moment(data.UDENDDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const nowDate = moment().valueOf()

                switch (SelectMode) {
                    case 1:
                        return createST <= nowDate && nowDate <= createET
                        break;
                    case 2:
                        // console.log(createET, nowDate)
                        return createET < nowDate
                        break;
                    default: return null;
                }
            })
        }

        if (InputMode !== null) {
            // console.log(InputMode)
            DataList = DataList.filter(data => {
                return data.UDGNAME.match(InputMode) !== null || data.UDRPNAME.match(InputMode) !== null
            })
        }

        var DataFormat = []

        const st = PickerDateRange[0]
        const et = PickerDateRange[1] + (24 * 60 * 60 * 1000)

        DataList.forEach(element => {
            const time = moment(element.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            console.log(time)
            if (st <= time && time <= et) {
                DataFormat.push(element)
            }
        })
        return DataFormat
    }

    render() {
        const { searchbar, caseinfo } = this.props
        const { SelectMode } = this.state

        const filterData = this.onClickSearch()

        return (
            <div>
                <div className={searchbar ? 'PrescriptionOrderSearchBar' : 'PrescriptionOrderNonSearchBar'}>
                    <t1>Prescription Order</t1>
                </div>
                {searchbar ? this.ControlBarRender() : null}
                <Prescriptionordertable caseinfo={caseinfo} UDORDER0={filterData} SelectMode={SelectMode} ></Prescriptionordertable>
            </div>
        );
    }
}

export default Prescriptionorderlayout
