import React, { Component } from 'react'
import rtlogo from '../../Image/svg/RT.svg'
import { Tooltip } from 'antd'
import moment from 'moment'

class Chronicrttablecell extends Component {
    ToolTipRender() {
        const { cellelement } = this.props
        const startTime = moment(cellelement.startTime).format("MM/DD HH:mm")
        const endTime = moment(cellelement.endTime).format("MM/DD HH:mm")
        const RTListRender = cellelement.element.map(function (info, index, arr) {
            const time = moment(info.Date + info.Time, "YYYYMMDDHHmm").format("MM/DD HH:mm")
            const Mode = info.Mode
            const Setting = info.Setting
            return (
                <div>
                    <div>Time:{time}</div>
                    <div>Mode:{Mode}</div>
                    <div>Setting:{Setting}</div>
                </div>
            );
        })

        return (
            <div>
                <div>{startTime}~{endTime}</div>
                {RTListRender}
            </div>
        );
    }
    render() {
        function changetext(text) {
            if (text === 0) {
                return '--'
            }
            return <img src={rtlogo} alt="rtlogo" />
        }
        const { cellelement } = this.props
        if (cellelement.length === 0) {
            return <div style={{ display: "flex", justifyContent: "center", alignItems: 'center', border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>--</div>
        }
        return (
            <Tooltip placement="right" autoAdjustOverflow={true} overlayStyle={{ background: "white" }} title={this.ToolTipRender()} overlayStyle={{ maxWidth: "800px" }}>
                <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                    {changetext(cellelement.length)}
                </centerstyle>
            </Tooltip>
        );
    }
}
export default Chronicrttablecell