import React, { Component } from 'react';
import * as d3 from "d3";
import './Crosssectionvitalsignchart.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import X2JS from 'x2js';

class Crosssectionvitalsignchart extends Component {
    state = {
        change: false,
        sourcedata: [],
        prepickerdate: null,
        presource: null,
        preinterval: null
    }

    centermonitorApiDataGet() {
        function formatTimer(interval, pickerdate) {
            const endTimeStamp = pickerdate + (31 * 60 * 60 * 1000)
            if (interval === 0) {
                return {
                    start: moment(endTimeStamp - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmmss"),
                    interval: 0
                }
            }
            if (interval === 1) {
                return {
                    start: moment(endTimeStamp - (3 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmmss"),
                    interval: 1
                }
            }
            if (interval === 2) {
                return {
                    start: moment(endTimeStamp - (7 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmmss"),
                    interval: 2
                }
            }
            return {
                start: moment(endTimeStamp - (14 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss"),
                end: moment(endTimeStamp).format("YYYYMMDDHHmmss"),
                interval: 3
            }
        }
        const { type, interval, PickerDate, HID, source, CID, svg_id } = this.props
        const PHID = HID.toString().trim()
        const PCID = CID.toString().trim()
        // console.log(interval, PickerDate)
        const optionvalue = formatTimer(interval, PickerDate)
        if (source === "CM") {
            if (type === "abp") {
                ApiProvider.getPatientwardvalueBoxPlot(PHID, optionvalue.start, optionvalue.end, type, optionvalue.interval)
                    .then(data => {
                        console.log(data.data)
                        d3.select("#" + svg_id).remove()
                        this.drawChart(data.data);
                    })
                    .catch(err => { console.log(err) })
            }
            if (type === "hr" || type === "rr" || type === "spo2") {
                ApiProvider.getPatientwardvalueBoxPlot(PHID, optionvalue.start, optionvalue.end, type, optionvalue.interval)
                    .then(data => {
                        console.log(data.data)
                        d3.select("#" + svg_id).remove()
                        this.drawChart(data.data);
                    })
                    .catch(err => { console.log(err) })
            }
            if (type === "nbp") {
                ApiProvider.getVitalSignData(PHID, optionvalue.start, optionvalue.end, type)
                    .then(data => {
                        console.log(data.data)
                        d3.select("#" + svg_id).remove()
                        this.drawChart(data.data);
                    })
                    .catch(err => {
                        console.log(err);
                    })
            }
            if (type === "bt") {
                d3.select("#" + svg_id).remove()
                this.drawChart([]);
            }
        }
        if (source === "NIS") {
            const xm2 = new X2JS()
            const st = moment(optionvalue.start, "YYYYMMDDHHmmss").format("YYYYMMDDHHmm")
            const et = moment(optionvalue.end, "YYYYMMDDHHmmss").format("YYYYMMDDHHmm")
            const QDVS = ApiProvider.getQDVS(PCID, st, et)
                .then(data => {
                    return xm2.xml2js(data.data).outmsg.resconts.rescont
                })
                .catch(err => { console.log(err) })

            const QOXY = ApiProvider.getQOXY(PCID, st, et)
                .then(data => {
                    return xm2.xml2js(data.data).outmsg.resconts.rescont

                })
                .catch(err => { console.log(err) })
            Promise.all([QDVS, QOXY]).then(result => {
                function FormatNISData(QDVS, QOXY, type) {
                    var HRNISLIST = []
                    var RRNISLIST = []
                    var SBPNISLIST = []
                    var DBPNISLIST = []
                    var BTNISLIST = []
                    var Spo2NISLIST = []
                    if (Array.isArray(QDVS)) {
                        for (let index = 0; index < QDVS.length; index++) {
                            const element = QDVS[index];
                            const hr = element.pluse
                            const rr = element.breath
                            const SBP = element.systolic
                            const DBP = element.diastolic
                            const BT = element.temperature
                            const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                            HRNISLIST.push({
                                Data: hr,
                                Time: time
                            })
                            RRNISLIST.push({
                                Data: rr,
                                Time: time
                            })
                            SBPNISLIST.push({
                                Data: SBP,
                                Time: time
                            })
                            DBPNISLIST.push({
                                Data: DBP,
                                Time: time
                            })
                            BTNISLIST.push({
                                Data: BT,
                                Time: time
                            })
                        }
                    }
                    if (Array.isArray(QOXY)) {
                        for (let index = 0; index < QOXY.length; index++) {
                            const element = QOXY[index];
                            const spo2 = element.concen
                            const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                            Spo2NISLIST.push({
                                Data: spo2,
                                Time: time

                            })
                        }
                    }
                    console.log(Spo2NISLIST)
                    if (type === "hr") { return HRNISLIST }
                    if (type === "rr") { return RRNISLIST }
                    if (type === "spo2") { return Spo2NISLIST }
                    if (type === "abp") { return [SBPNISLIST, DBPNISLIST] }
                    if (type === "bt") { return BTNISLIST }
                    return []
                }
                // console.log(FormatNISData(result[0], result[1], type))
                d3.select("#" + svg_id).remove()
                this.drawChart(FormatNISData(result[0], result[1], type))
            })
        }
    }
    componentDidUpdate() {

        const { svg_id, interval, PickerDate, HID, source, CID } = this.props
        const PrePickerDate = this.state.prepickerdate
        if (PrePickerDate !== PickerDate) {
            this.setState({ prepickerdate: this.props.PickerDate })
            this.centermonitorApiDataGet()
        }
        const Presource = this.state.presource
        if (Presource !== source) {
            this.setState({ presource: source })
            this.centermonitorApiDataGet()
        }
        const Preinterval = this.state.preinterval
        if (interval !== Preinterval) {
            this.setState({ preinterval: interval })
            this.centermonitorApiDataGet()
        }
        const PreHID = this.state.prehid
        if (HID !== PreHID) {
            this.setState({ prehid: HID })
            this.centermonitorApiDataGet()
        }
    }

    componentDidMount() {

        this.setState({
            prepickerdate: this.props.PickerDate,
            presource: this.props.source,
            preinterval: this.props.interval,
            prehid: this.props.prehid
        })
        this.centermonitorApiDataGet()

    }
    drawChart(sourcedata) {
        function formatxArray(startTime, interval) {
            var xAxisarray = []
            if (interval === 0) {
                for (let index = 0; index < 25; index++) {
                    if (index % 4 === 0) {
                        xAxisarray.push(startTime + (index * 60 * 60 * 1000))
                    }
                }
            }
            else if (interval === 1) {
                for (let index = 0; index < 19; index++) {
                    if (index % 2 === 0) {
                        xAxisarray.push(startTime + (index * 4 * 60 * 60 * 1000))
                    }
                }
            }
            else if (interval === 2) {
                for (let index = 0; index < 21; index++) {
                    xAxisarray.push(startTime + (index * 60 * 60 * 1000 * 8))
                }
            }
            else {
                for (let index = 0; index < 14; index++) {
                    xAxisarray.push(startTime + (index * 60 * 60 * 1000 * 24))
                }
            }
            return xAxisarray
        }

        function formatbotaxis(time, interval) {
            var xAxisarray = []
            if (interval === 0) {
                xAxisarray.push(startTime + (17 * 60 * 60 * 1000))
            }
            else if (interval === 1) {
                for (let index = 0; index < 3; index++) {
                    xAxisarray.push(startTime + (17 * 60 * 60 * 1000) + (index * 24 * 60 * 60 * 1000))
                }
            }
            else if (interval === 2) {
                for (let index = 0; index < 21; index++) {
                    xAxisarray.push(time + (index * 60 * 60 * 1000 * 8))
                }
            }
            else {
                for (let index = 0; index < 14; index++) {
                    xAxisarray.push(time + (index * 60 * 60 * 1000 * 24))
                }
            }
            return xAxisarray
        }

        var formatHour = d3.timeFormat("%H")

        var formatDay = d3.timeFormat("%b %d")
        function multiFormat(date) {
            return formatHour(date)
        }

        function timeBotmultiFormat(date) {
            return formatDay(date)
        }
        //vitaldays true:threeday
        const {
            id_key,
            svg_id,
            startTime,
            endTime,
            height,
            width,
            interval, //0:1天 1:3天 2:1週 3:2週 
            axisBot,
            axisTop,
            circlrcolor,
            max,
            min,
            upper,
            lower,
            source,
            type
        } = this.props

        // const width = window.screen.availWidth * 0.25, height = 100, viewwidth = width - 50, rect_width = width / 46
        const viewwidth = width - 50
        // const EndTime = formatendtime(defaultdate)
        // var vital_interval = formatintervaldays(EndTime, vitaldays)

        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])

        var x = d3.scaleLinear()
            .domain([startTime, endTime])
            .range([0, viewwidth - 1])


        var yAxis = d3.axisLeft(y)
            .tickValues([min, max])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(15);


        const xAxisvalue = formatxArray(startTime, interval)
        const xAxisbotvalue = formatbotaxis(startTime, interval)

        var xAxis = d3.axisTop(x)
            .tickValues(xAxisvalue)
            .tickSize(0, 0)
            .tickPadding(5)
            .tickFormat(function (d) { return multiFormat(d); });

        var xAxisbot = d3.axisBottom(x)
            .tickValues(xAxisbotvalue)
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) { return moment(d).format("MM/DD") });

        var svg
        if (axisTop) {
            svg = d3.select("#" + id_key)
                .append("svg")
                .attr("id", svg_id)
                .style("width", width + 10)
                .style("height", height + 25)
                .append("g")
                .attr("transform", "translate(" + 50 + "," + 20 + ")");
        }
        else {
            if (axisBot) {
                svg = d3.select("#" + id_key)
                    .append("svg")
                    .attr("id", svg_id)
                    .style("width", width)
                    .style("height", height + 25)
                    .append("g")
                    .attr("transform", "translate(" + 50 + "," + 5 + ")");
            }
            else {

                svg = d3.select("#" + id_key)
                    .append("svg")
                    .attr("id", svg_id)
                    .style("width", width)
                    .style("height", height + 10)
                    .append("g")
                    .attr("transform", "translate(" + 50 + "," + 5 + ")");
            }
        }

        if (axisTop) {
            svg.append("g")
                .call(xAxis)
                .attr("class", "axis");
        }

        if (axisBot) {
            svg.append("g")
                .call(xAxisbot)
                .attr("class", "axis");

        }

        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");


        //上下橫線
        svg.append('line')
            .attr('x1', x(startTime))
            .attr('y1', y(max))
            .attr('x2', x(endTime))
            .attr('y2', y(max))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', x(startTime))
            .attr('y1', y(min))
            .attr('x2', x(endTime))
            .attr('y2', y(min))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);


        if (interval === 0) {
            for (let index = 0; index < 25; index++) {
                if (index % 8 === 0) {
                    svg.append('line')
                        .attr('x1', x(startTime + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(startTime + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 2);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(startTime + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(startTime + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }

            drawshadow(startTime, interval, lower, upper, type)
            drawData(type, source, sourcedata, interval, 10)

        }
        if (interval === 1) {
            for (let index = 0; index < 19; index++) {
                if (index % 2 === 0) {
                    svg.append('line')
                        .attr('x1', x(startTime + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(startTime + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 2);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(startTime + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(startTime + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }

            drawshadow(startTime, interval, lower, upper, type)
            drawData(type, source, sourcedata, interval, 10)
        }
        if (interval === 2) {
            for (let index = 0; index < 22; index++) {
                svg.append('line')
                    .attr('x1', x(startTime + (index * 60 * 60 * 1000 * 8)))
                    .attr('y1', 0)
                    .attr('x2', x(startTime + (index * 60 * 60 * 1000 * 8)))
                    .attr('y2', height)
                    .style('stroke', 'black')
                    .style('stroke-width', 1);
            }

            drawshadow(startTime, interval, lower, upper, type)
            drawData(type, source, sourcedata, interval, 10)
        }
        if (interval === 3) {
            for (let index = 0; index < 15; index++) {
                svg.append('line')
                    .attr('x1', x(startTime + (index * 60 * 60 * 1000 * 24)))
                    .attr('y1', 0)
                    .attr('x2', x(startTime + (index * 60 * 60 * 1000 * 24)))
                    .attr('y2', height)
                    .style('stroke', 'black')
                    .style('stroke-width', 1);
            }

            drawshadow(startTime, interval, lower, upper, type)
            drawData(type, source, sourcedata, interval, 10)
        }

        function formatBox(startTime, interval) {
            function interFormat(startTime, interval) {
                var TimeArray = []
                for (let index = 0; index < interval; index++) {
                    if (interval === 24) {
                        const boxTime = startTime + (30 * 60 * 1000) + (index * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else if (interval === 18) {
                        const boxTime = startTime + (2 * 60 * 60 * 1000) + (index * 4 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else if (interval === 21) {
                        const boxTime = startTime + (4 * 60 * 60 * 1000) + (index * 8 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else {
                        const boxTime = startTime + (12 * 60 * 60 * 1000) + (index * 24 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                }
                return TimeArray
            }
            if (interval === 0) {
                return interFormat(startTime, 24)
            }
            if (interval === 1) {
                return interFormat(startTime, 18)
            }
            if (interval === 2) {
                return interFormat(startTime, 21)
            }
            return interFormat(startTime, 14)
        }
        function formatBoxWidth(viewwidth, interval) {
            if (interval === 0) {
                return viewwidth / 24
            }
            if (interval === 1) {
                return viewwidth / 18
            }
            if (interval === 2) {
                return viewwidth / 21
            }
            return viewwidth / 14
        }
        function drawshadow(startTime, interval, lower, upper, type) {
            const LimitBoxTimer = formatBox(startTime, interval)
            for (let index = 0; index < LimitBoxTimer.length; index++) {
                const boxwidth = formatBoxWidth(width, interval)
                // console.log(boxwidth)
                const element = LimitBoxTimer[index];
                svg.append('line')
                    .attr('x1', x(element))
                    .attr('y1', y(lower))
                    .attr('x2', x(element))
                    .attr('y2', y(upper))
                    .style('stroke', circlrcolor)
                    .style('opacity', 0.7)
                    .style('stroke-width', boxwidth)

                if (type === "nbp" || type === "abp") {
                    svg.append('line')
                        .attr('x1', x(element))
                        .attr('y1', y(35))
                        .attr('x2', x(element))
                        .attr('y2', y(55))
                        .style('stroke', circlrcolor)
                        .style('opacity', 0.3)
                        .style('stroke-width', boxwidth)
                }
            }
        }
        function drawData(type, source, sourcedata, interval, boxwidth) {
            function formatShift(interval) {
                if (interval === 0) {
                    return 30 * 60 * 1000
                }
                if (interval === 1) {
                    return 2 * 60 * 60 * 1000
                }
                if (interval === 2) {
                    return 4 * 60 * 60 * 1000
                }
                return 12 * 60 * 60 * 1000
            }
            // console.log(source)
            if (source === "CM") {
                if (type === "abp") {
                    const dataAbps = sourcedata.abps
                    const dataAbpd = sourcedata.abpd
                    for (let index = 0; index < dataAbpd.length; index++) {
                        const element = dataAbpd[index];
                        const q1 = element.Q1
                        const q2 = element.Q2
                        const q3 = element.Q3
                        const q4 = element.Q4
                        const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf() + formatShift(interval)
                        svg.append("line")
                            .attr("x1", x(Xtime))
                            .attr("x2", x(Xtime))
                            .attr("y1", y(q1))
                            .attr("y2", y(q4))
                            .attr("stroke", circlrcolor)
                            .style('opacity', 0.5)
                            // .attr("stroke", "blcak")
                            .style('stroke-width', 1)

                        svg.append("rect")
                            .attr("x", x(Xtime) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q2) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style('opacity', 0.5)
                            .style("fill", "rgba(255,255,255,1)")
                            .style('stroke-width', 1);

                        svg
                            .selectAll("toto")
                            .data([q1, q4])
                            .enter()
                            .append("line")
                            .attr("x1", x(Xtime) - boxwidth / 2)
                            .attr("x2", x(Xtime) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('opacity', 0.5)
                            .style('stroke-width', 1);
                    }
                    for (let index = 0; index < dataAbps.length; index++) {
                        const element = dataAbps[index];
                        const q1 = element.Q1
                        const q2 = element.Q2
                        const q3 = element.Q3
                        const q4 = element.Q4
                        const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf() + formatShift(interval)
                        svg.append("line")
                            .attr("x1", x(Xtime))
                            .attr("x2", x(Xtime))
                            .attr("y1", y(q1))
                            .attr("y2", y(q4))
                            .attr("stroke", circlrcolor)
                            // .attr("stroke", "blcak")
                            .style('stroke-width', 1)

                        svg.append("rect")
                            .attr("x", x(Xtime) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q2) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style("fill", "rgba(255,255,255,1)")
                            .style('stroke-width', 1);

                        svg
                            .selectAll("toto")
                            .data([q1, q4])
                            .enter()
                            .append("line")
                            .attr("x1", x(Xtime) - boxwidth / 2)
                            .attr("x2", x(Xtime) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1);
                    }
                }
                if (type === "hr" || type === "rr" || type === "spo2") {
                    for (let index = 0; index < sourcedata.length; index++) {
                        const element = sourcedata[index];
                        const q1 = element.Q1
                        const q2 = element.Q2
                        const q3 = element.Q3
                        const q4 = element.Q4
                        const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf() + formatShift(interval)
                        svg.append("line")
                            .attr("x1", x(Xtime))
                            .attr("x2", x(Xtime))
                            .attr("y1", y(q1))
                            .attr("y2", y(q4))
                            .attr("stroke", circlrcolor)
                            // .attr("stroke", "blcak")
                            .style('stroke-width', 1)

                        svg.append("rect")
                            .attr("x", x(Xtime) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q2) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style("fill", "rgba(255,255,255,1)")
                            .style('stroke-width', 1);

                        svg
                            .selectAll("toto")
                            .data([q1, q4])
                            .enter()
                            .append("line")
                            .attr("x1", x(Xtime) - boxwidth / 2)
                            .attr("x2", x(Xtime) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1);

                    }
                }
                if (type === "nbp") {
                    for (let index = 0; index < sourcedata.length; index++) {
                        const element = sourcedata[index];
                        const Xtime = moment(element.Time, "YYYY-MM-DD HH:mm:ss").valueOf()
                        const SBP = element.SBP
                        const DBP = element.DBP
                        // console.log(SBP)
                        if (SBP === 0 || DBP === 0) {
                            continue
                        }
                        else {
                            // console.log(element)
                            svg.append('circle')
                                .attr('cx', x(Xtime))
                                .attr('cy', y(SBP))
                                .attr('r', 3)
                                .style('fill', circlrcolor)


                            svg.append('circle')
                                .attr('cx', x(Xtime))
                                .attr('cy', y(DBP))
                                .attr('r', 3)
                                .style('opacity', 0.5)
                                .style('fill', circlrcolor)
                        }

                    }
                }
            }
            else {
                if (type === "abp" || type === "nbp") {
                    if (sourcedata === undefined) {
                        return null
                    }
                    if (sourcedata.length === 0) {
                        return null
                    }
                    const SBP = sourcedata[0]
                    const DBP = sourcedata[1]
                    for (let index = 0; index < SBP.length; index++) {
                        const element = SBP[index];
                        svg.append('circle')
                            .attr('cx', x(element.Time))
                            .attr('cy', y(element.Data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)

                    }

                    for (let index = 0; index < DBP.length; index++) {
                        const element = DBP[index];
                        svg.append('circle')
                            .attr('cx', x(element.Time))
                            .attr('cy', y(element.Data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)
                            .style('opacity', 0.5)

                    }
                }
                else {
                    for (let index = 0; index < sourcedata.length; index++) {
                        const element = sourcedata[index];
                        svg.append('circle')
                            .attr('cx', x(element.Time))
                            .attr('cy', y(element.Data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)

                    }
                }

            }
        }
    }

    changestate() {
        this.setState({
            change: !this.state.change
        })
    }
    render() {
        const { svg_id, source, id_key } = this.props
        return (
            <div id={id_key}>
            </div>
        );
    }
}

export default Crosssectionvitalsignchart;