import React, { Component } from 'react'
import Infectionlabtable from './Infectionlabtable'

class Infectionlab extends Component {
    render() {
        const { WBCList, BandList, SegList, LymList, PCTList, CRPList,PickerDate } = this.props
        const item_title = [{ title: "WBC" }, { title: "Band" }, { title: "Seg" }, { title: "Lym" }, { title: "PCT" }, { title: "CRP" }]
        return (
            <div style={{margin:"10px 0px"}}>
                <Infectionlabtable PickerDate={PickerDate} title={item_title}  WBCList={WBCList} BandList={BandList} SegList={SegList} LymList={LymList} PCTList={PCTList} CRPList={CRPList}></Infectionlabtable>
            </div>
        );
    }
}

export default Infectionlab