import React, { Component } from 'react';
import './Flootsheetio.scss'
import * as RegFilter from '../../Function/FiliterReg'
import moment from 'moment';
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider'
import * as QDCR from '../../Resource/QDC'

class Flootsheetio extends Component {
    state = {
        itemNumber: "day",
        preCID: null,
        prePickerDate: null,
        QDC: []
    }
    componentDidMount() {
        const { caseinfo, PickerDate } = this.props
        const CID = caseinfo.CASEID
        this.setState({ preCID: CID, prePickerDate: PickerDate })
        this.getQDC(CID, PickerDate)
    }
    componentDidUpdate() {
        const { caseinfo, PickerDate } = this.props
        const CID = caseinfo.CASEID
        const preCID = this.state.preCID
        const prePickerDate = this.state.prePickerDate
        console.log(prePickerDate, PickerDate)
        if (CID !== preCID || PickerDate !== prePickerDate) {
            this.setState({ preCID: CID, prePickerDate: PickerDate })
            this.getQDC(CID, PickerDate)
        }
    }
    getQDC(CID, PickerDate) {
        const xm2 = new X2JS()
        const st = moment(PickerDate + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(PickerDate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        ApiProvider.getQDC(CID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                this.setState({
                    QDC: parsedata
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
    }
    SecondDataCell(dataJson, Title) {
        const { PickerDate } = this.props
        const st = PickerDate + (7 * 60 * 60 * 1000)
        var cellRender = []
        for (let index = 0; index < 24; index++) {
            const startTime = st + (index * 60 * 60 * 1000)
            const endTime = st + ((index + 1) * 60 * 60 * 1000)
            const timeObjectString = dataJson.rec_dat.toString() + dataJson.rec_tim.toString()
            const time = moment(timeObjectString, 'YYYYMMDDHHmm').valueOf()
            if (time > startTime && time < endTime) {
                cellRender.push(dataJson.value)
            }
            else {
                cellRender.push("--")
            }
        }
        const data = cellRender.map((info, index) =>
            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                <t2>{info}</t2>
            </centerstyle>
        )
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <centerstyle style={{ background: "#cccccc" }}>
                    <t4>{Title}</t4>
                </centerstyle>
                {data}
            </div>
        );
    }
    FourthDataCell(dataJson, Title) {
        function nullvalue(value) {
            if (value === undefined || value === null) {
                return "--"
            }
            if (value === 0) {
                return "##"
            }
            return value.toString().trim()
        }
        const { PickerDate } = this.props
        const st = PickerDate + (7 * 60 * 60 * 1000)
        var cellRender = []
        for (let index = 0; index < 24; index++) {
            const startTime = st + (index * 60 * 60 * 1000)
            const endTime = st + ((index + 1) * 60 * 60 * 1000)
            const filterData = dataJson.filter(function (value, index, arr) {
                const timeObjectString = value.rec_dat.toString() + value.rec_tim.toString()
                const time = moment(timeObjectString, 'YYYYMMDDHHmm').valueOf()
                return time > startTime && time < endTime
            })
            if (filterData.length === 0) {
                cellRender.push("--")
            }
            var sum = 0
            for (let index = 0; index < filterData.length; index++) {
                const element = filterData[index];
                if (Title === "滲液") {
                    cellRender.push(element.out_val1)
                }
                if (Title === "嘔吐") {
                    const string = element.out_val1.toString() + "/" + element.nam.toString() + "/" + element.color.toString()
                    cellRender.push(string)
                }
                if (Title === "引流") {
                    const string = element.out_val1.toString() + "/" + element.nam.toString() + "/" + element.color.toString()
                    cellRender.push(string)
                }
                if (Title === "穿刺出") {
                    cellRender.push(element.out_val1)

                }
                if (Title === "穿刺入") {
                    cellRender.push(element.inp_val1)

                }
                if (Title === "透析出") {
                    cellRender.push(element.out_val1)

                }
                if (Title === "透析入") {
                    cellRender.push(element.inp_val1)

                }
                if (Title === "透析加總") {
                    const sum = Number(element.out_val1) - Number(element.inp_val1)
                    cellRender.push(sum)

                }
                if (Title === "沖洗出") {
                    cellRender.push(element.out_val1)

                }
                if (Title === "沖洗入") {
                    cellRender.push(element.inp_val1)

                }
                if (Title === "排尿") {
                    cellRender.push(element.inp_val1)

                }
            }

        }
        const data = cellRender.map((info, index) =>
            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                <t2>{nullvalue(info)}</t2>
            </centerstyle>
        )
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <centerstyle style={{ background: "#cccccc" }}>
                    <t4>{Title}</t4>
                </centerstyle>
                {data}
            </div>
        );
    }

    Thirdtablecell(dataJson, Title) {
        const { PickerDate } = this.props
        const st = PickerDate + (7 * 60 * 60 * 1000)
        var cellRender = []

        for (let index = 0; index < 24; index++) {
            const startTime = st + (index * 60 * 60 * 1000)
            const endTime = st + ((index + 1) * 60 * 60 * 1000)
            const time = moment(dataJson.time, 'YYYYMMDDHHmm').valueOf()
            if (time > startTime && time < endTime) {
                cellRender.push(dataJson.value)
            }
            else {
                cellRender.push("--")
            }
        }

        const data = cellRender.map((info, index) =>
            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                <t2>{info}</t2>
            </centerstyle>
        )
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <centerstyle style={{ background: "#cccccc" }}>
                    <t4>{Title}</t4>
                </centerstyle>
                {data}
            </div>
        );
    }

    Firsttablecell(dataList, Title) {
        const { PickerDate } = this.props
        const st = PickerDate + (7 * 60 * 60 * 1000)
        var cellRender = []

        for (let index = 0; index < 24; index++) {
            const startTime = st + (index * 60 * 60 * 1000)
            const endTime = st + ((index + 1) * 60 * 60 * 1000)
            console.log(dataList)
            const datafilter = dataList.filter(function (value, index, arr) {
                const time = moment(value.rec_dat.toString() + value.rec_tim.toString(), 'YYYYMMDDHHmm').valueOf()
                return time > startTime && time < endTime
            })
            var sum = 0
            for (let index = 0; index < datafilter.length; index++) {
                const element = datafilter[index];
                sum = sum + Number(element.inp_val1)
            }
            cellRender.push(sum)
        }

        const data = cellRender.map((info, index) =>
            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                <t2>{info}</t2>
            </centerstyle>
        )
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <centerstyle style={{ background: "#cccccc" }}>
                    <t4>{Title}</t4>
                </centerstyle>
                {data}
            </div>
        );
    }

    RVList(cell) {
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <div style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', backgroundColor: "#cccccc", display: "flex", alignItems: "center", fontSize: "12px" }}>{"RV"}</div>
                {cell}
            </div>
        );
    }
    render() {
        function groupelement(array) {
            function groupvalue(arr) {
                const mapvalue = arr.map(function (value, index, arr) {
                    return value.element
                })
                return mapvalue
            }
            var dataList = []
            for (let index = 0; index < array.length; index++) {
                const element = array[index];
                const data = groupvalue(element)
                if (element.length !== 0) {
                    if (element[0].title === "滲液") {
                        dataList.push({
                            "title": element[0].title,
                            "element": data
                        })
                    }
                    if (element[0].title === "嘔吐") {
                        dataList.push({
                            "title": element[0].title,
                            "element": data
                        })
                    }
                    if (element[0].title === "引流") {
                        const stringElement = element[0].element
                        const string = stringElement.line + "/" + stringElement.pos
                        dataList.push({
                            "title": string,
                            "element": data
                        })
                    }
                    if (element[0].title === "穿刺") {
                        const stringElement = element[0].element
                        if (stringElement.out_val1 !== undefined) {
                            dataList.push({
                                "title": "穿刺出",
                                "element": data
                            })
                        }
                        if (stringElement.inp_val1 !== undefined) {
                            dataList.push({
                                "title": "穿刺入",
                                "element": data
                            })
                        }
                    }
                    if (element[0].title === "透析") {
                        const stringElement = element[0].element
                        if (stringElement.out_val1 !== undefined) {
                            dataList.push({
                                "title": "透析出",
                                "element": data
                            })
                        }
                        if (stringElement.inp_val1 !== undefined) {
                            dataList.push({
                                "title": "透析入",
                                "element": data
                            })
                        }
                        dataList.push({
                            "title": "透析加總",
                            "element": data
                        })
                    }
                    if (element[0].title === "沖洗") {
                        const stringElement = element[0].element
                        if (stringElement.out_val1 !== undefined) {
                            dataList.push({
                                "title": "沖洗出",
                                "element": data
                            })
                        }
                        if (stringElement.inp_val1 !== undefined) {
                            dataList.push({
                                "title": "沖洗入",
                                "element": data
                            })
                        }
                    }
                    if (element[0].title === "排便") {
                        dataList.push({
                            "title": "Stool",
                            "element": data
                        })
                        dataList.push({
                            "title": "Enema/Stimulation",
                            "element": data
                        })
                    }
                    if (element[0].title === "排尿") {
                        const stringElement = element[0].element
                        const string = stringElement.inp_val1
                        dataList.push({
                            "title": "Urine" + string,
                            "element": data
                        })
                    }
                }
            }
            return dataList
        }
        function groupfirstarray(arrayList) {
            var result = arrayList.filter(function (value, index, arr) {
                const res = arr.map(function (value, index, arr) {
                    return value.compare
                })
                return res.indexOf(value.compare) === index;
            });
            var arrList = []
            for (let index = 0; index < result.length; index++) {
                const element = result[index];
                const comparevalue = element.compare
                const filterarray = arrayList.filter(function (value, index, arr) {
                    return value.compare === comparevalue
                })
                arrList.push(filterarray)
            }
            return arrList
        }
        function nullvalue(value) {
            if (value === undefined || value === null) {
                return "--"
            }
            if (value === 0) {
                return "#"
            }
            return value.toString().trim()
        }
        function formatQDC(QDC) {
            var FirstArray = []
            var SecondArray = []
            var ThirdArray = []
            var FourthArray = []
            if (Array.isArray(QDC)) {
                for (let index = 0; index < QDC.length; index++) {
                    const element = QDC[index];
                    if (RegFilter.firstarea_Reg_IO().find(function (value, index, array) {
                        return value === element.item
                    })) {
                        FirstArray.push({
                            title: nullvalue(element.line) + "/" + nullvalue(element.nam) + "/" + nullvalue(element.pos),
                            compare: nullvalue(element.line) + "/" + nullvalue(element.nam),
                            element: element
                        })
                    }
                    if (RegFilter.Secondarea_reg_IO().find(function (value, index, array) {
                        return value === element.item
                    })) {
                        SecondArray.push({
                            title: nullvalue(element.line) + "/" + nullvalue(element.nam) + "/" + nullvalue(element.pos),
                            compare: nullvalue(element.line) + "/" + nullvalue(element.nam) + "/" + nullvalue(element.pos),
                            element: element
                        })
                    }
                    if (RegFilter.Thirdarea_reg_IO().find(function (value, index, array) {
                        return value === element.item
                    })) {
                        ThirdArray.push({
                            title: nullvalue(element.nam) + "/" + nullvalue(element.lost),
                            compare: nullvalue(element.nam) + "/" + nullvalue(element.lost),
                            element: element
                        })
                    }
                    if (RegFilter.Fourtharea_reg_IO().find(function (value, index, array) {
                        return value === element.item
                    })) {
                        FourthArray.push({
                            title: element.item,
                            compare: element.item,
                            element: element
                        })
                    }
                }
            }
            return {
                "FirstArray": FirstArray,
                "SecondArray": SecondArray,
                "ThirdArray": ThirdArray,
                "FourthArray": FourthArray
            }
        }



        const { PickerDate } = this.props
        const { QDC } = this.state
        const dataList = formatQDC(QDC)
        console.log(dataList.ThirdArray)
        // const DataFirst = dataList.FirstArray
        const FlootSheetFirstAreaData = groupfirstarray(dataList.FirstArray)
        const FlootSheetSecondAreaData = groupfirstarray(dataList.SecondArray)
        const FlootSheetThirdAreaData = groupfirstarray(dataList.ThirdArray)
        const FlootSheetFourthAreaData = groupelement(groupfirstarray(dataList.FourthArray))
        console.log(FlootSheetFirstAreaData)
        console.log(FlootSheetSecondAreaData)
        console.log(FlootSheetThirdAreaData)
        console.log(FlootSheetFourthAreaData)
        const start = PickerDate + 7 * 60 * 60 * 1000
        const Firstdata = FlootSheetFirstAreaData.map(function (value, index, arr) {
            const title = value[0].title
            var timeForList = []
            for (let index = 0; index < 24; index++) {
                const startTime = start + (index * 60 * 60 * 1000)
                const endTime = start + ((index + 1) * 60 * 60 * 1000)
                var sum = 0
                var elementList = []
                for (let index = 0; index < value.length; index++) {
                    const element = value[index].element;
                    const valueSum = Number(element.inp_val1)
                    const elementTime = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                    if (startTime <= elementTime && elementTime < endTime) {
                        sum = sum + valueSum
                        elementList.push(element)
                    }
                }
                timeForList.push({
                    value: sum,
                    startTime: startTime,
                    endTime: endTime,
                    elementList: elementList
                })
            }
            console.log(timeForList)
            const cellMap = timeForList.map(function (info, index, arr) {
                return (
                    <div key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
                );
            })
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                    <div style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', backgroundColor: "#cccccc", display: "flex", alignItems: "center", fontSize: "12px" }}>{title}</div>
                    {cellMap}
                </div>
            );
        })
        const Secondedata = FlootSheetSecondAreaData.map(function (value, index, arr) {
            const title = value[0].title
            var timeForList = []
            for (let index = 0; index < 24; index++) {
                const startTime = start + (index * 60 * 60 * 1000)
                const endTime = start + ((index + 1) * 60 * 60 * 1000)
                var sum = 0
                var elementList = []
                for (let index = 0; index < value.length; index++) {
                    const element = value[index].element;
                    const valueSum = Number(element.inp_val1)
                    const elementTime = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                    if (startTime <= elementTime && elementTime < endTime) {
                        sum = sum + valueSum
                        elementList.push(element)
                    }
                }
                timeForList.push({
                    value: sum,
                    startTime: startTime,
                    endTime: endTime,
                    elementList: elementList
                })
            }
            const cellMap = timeForList.map(function (info, index, arr) {
                return (
                    <div key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
                );
            })
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                    <div style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', backgroundColor: "#cccccc", display: "flex", alignItems: "center", fontSize: "12px" }}>{title}</div>
                    {cellMap}
                </div>
            );
        })

        const Thirddata = FlootSheetThirdAreaData.map(function (value, index, arr) {
            const title = value[0].title
            var timeForList = []
            for (let index = 0; index < 24; index++) {
                const startTime = start + (index * 60 * 60 * 1000)
                const endTime = start + ((index + 1) * 60 * 60 * 1000)
                var sum = 0
                var elementList = []
                for (let index = 0; index < value.length; index++) {
                    const element = value[index].element;
                    const valueSum = Number(element.inp_val1)
                    const elementTime = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                    if (startTime <= elementTime && elementTime < endTime) {
                        sum = sum + valueSum
                        elementList.push(element)
                    }
                }
                timeForList.push({
                    value: sum,
                    startTime: startTime,
                    endTime: endTime,
                    elementList: elementList
                })
            }
            const cellMap = timeForList.map(function (info, index, arr) {
                return (
                    <div key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
                );
            })
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                    <div style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', backgroundColor: "#cccccc", display: "flex", alignItems: "center", fontSize: "12px" }}>{title}</div>
                    {cellMap}
                </div>
            );
        })
        function formatRV(List) {
            var timeForList = []
            for (let index = 0; index < 24; index++) {
                const startTime = start + (index * 60 * 60 * 1000)
                const endTime = start + ((index + 1) * 60 * 60 * 1000)
                var sum = 0
                var elementList = []
                for (let index = 0; index < List.length; index++) {
                    const element = List[index].element;
                    const valueSum = Number(element.inp_val1)
                    const elementTime = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                    if (startTime <= elementTime && elementTime < endTime) {
                        sum = sum + valueSum
                        elementList.push(element)
                    }
                }
                timeForList.push({
                    value: sum,
                    startTime: startTime,
                    endTime: endTime,
                    elementList: elementList
                })
            }
            return timeForList
        }
        const RVCellRender = formatRV(dataList.ThirdArray)
        const cellMap = RVCellRender.map(function (info, index, arr) {
            return (
                <div key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
            );
        })
        const RV = this.RVList(cellMap)
        const FourdataCellRender = FlootSheetFourthAreaData.map((value, index, arr) => {
            const datajson = value.element
            const title = value.title
            return this.FourthDataCell(datajson, title)
        })
        return (
            <div>
                <div style={{ display: 'flex', alignItems: 'center', height: '50px' }}>
                    <t1>I/O</t1>
                </div>
                <div style={{ display: Firstdata.length === 0 ? "none" : "block", border: "1px solid black", borderCollapse: "collapse" }}>
                    {Firstdata}
                </div>
                <div style={{ display: Secondedata.length === 0 ? "none" : "block", border: "1px solid black", borderCollapse: "collapse" }}>
                    {Secondedata}
                </div>
                <div style={{ display: Thirddata.length === 0 ? "none" : "block", border: "1px solid black", borderCollapse: "collapse" }}>
                    {Thirddata}
                    {RV}
                </div>
                <div style={{ display: FourdataCellRender.length === 0 ? "none" : "block", border: "1px solid black", borderCollapse: "collapse" }}>
                    {FourdataCellRender}
                </div>
            </div>
        );
    }
}

export default Flootsheetio;