import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Treatmentordertablecell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

class Treatmentordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false,
        remark: this.props.info.remark,
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { info } = this.props
        ApiProvider.getOneRemark(this.props.caseinfo.TRANSINID, info.ORDSEQNO, "treatment").then(result => {
            // console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    render() {
        const { info } = this.props
        // console.log(info)
        const Name = info.ORPROCED
        const QD = info.ORFREQN
        const Status = info.ORSTATUS
        const ST = moment(info.ORBGNDT.toString(), 'YYYY-MM-DD').format("MM/DD")
        const ET = moment(info.ORENDDT.toString(), 'YYYY-MM-DD').format("MM/DD")


        const remarkValue = {
            "createTime": ST,
            "name": Name,
            "remarkId": info.ORDSEQNO,
            "remark": this.state.remark,
            "transinId": this.props.caseinfo.TRANSINID,
            "type": "treatment",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className="treatmentordercell">
                <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "100px" : "80px 50px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 3fr 1fr 1fr" }}>
                        <t3 className='cellstyle'>{ST}</t3>
                        <t3 className='cellstyle'>{ET}</t3>
                        <t3 className='cellstyle'>{Name}</t3>
                        <t3 className='cellstyle'>{QD}</t3>
                        <t3 className='cellstyle'>{Status}</t3>
                    </div>
                    <t3 className='cellstyle' style={{ display: [this.state.remark === undefined || this.state.remark === "" ? "none" : ""] }}>{this.state.remark}</t3>
                    <div style={this.state.onMove ? { display: this.state.powerLogin >= 2 ? "flex" : "none", justifyContent: "center", alignItems: "center", position: 'absolute', right: 0, height: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "100px" : "130px"], background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}
export default Treatmentordertablecell
