import React, { Component } from 'react';
import Labviewitem from './Labviewitem'
import { labdata } from '../../Data/Wardviewdata'
import * as FiliterReg from '../../Function/FiliterReg'
import * as ApiProvider from '../../API/ApiProvider'
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'

var MG_VALUE, PROC_VALUE;
class Labviewlayout extends Component {
    state = {
        parser: [{ "apply_condition": { "name": "" }, "mappings": [] }],
        dummy_mg: 0,
        dummy_pct: 0
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("Labview.json")
            .then(result => {
                console.log("getGitHubJson Labview.json OK " + JSON.stringify(result.data));
                this.setState({
                    parser: result.data
                })
            })
            .catch(err => {
                console.log("getGitHubJson Labview.json NG " + JSON.stringify(err));
            })
    }

    findLastestTargetValue(arr, target) {
        const { caseinfo } = this.props
        const { parser } = this.state
        let latestTimestamp = 0;
        let latestvalue = undefined;
        const selected_parser = parser.find(item => item.apply_condition.name.match(target) !== null);
        const arrayLength = arr.length;
        arr.forEach((item, index) => {
            ApiProvider.getStoredProcedureRESDISP(caseinfo.HISID, item.ORMM, caseinfo.CASEID, item.ORDSEQNO)
                .then(data => {
                    console.log("getStoredProcedureRESDISP OK " + JSON.stringify(data.data));
                    const RESDISP = data.data
                    //const RESDISP = [{"RECNO":"1","PARTNO":"11 ","HISTNO":"45305666  ","ORBGNDT":"2019-11-12","CONTENT":"    MG++:  (20.00)","ORDSEQ":"1","CASENO":"25358448"},{"RECNO":"2","PARTNO":"11 ","HISTNO":"45305666  ","ORBGNDT":"2019-11-12","CONTENT":"    Procalcitonin:  (25.00)","ORDSEQ":"1","CASENO":"25358448"}]

                    //search for target
                    const selected_mapping = selected_parser.mappings.find(mapping => mapping.key === target)
                    const re = new RegExp(selected_mapping.regex_str, selected_mapping.regex_flags)

                    RESDISP.forEach(element => {
                        const match = element.CONTENT.match(re)
                        if (match !== null) {
                            const currentTimeStr = item.ORRCPDT + "-" + item.ORRCPTM
                            const currentItemTimestamp = moment(currentTimeStr.trim(), "YYYY-MM-DD-HH.mm.ss").valueOf()
                            const matchValue = match[selected_mapping.position]

                            if (matchValue !== "" && matchValue !== undefined && matchValue !== null && currentItemTimestamp > latestTimestamp) {
                                latestvalue = matchValue
                                if (target === "mg") {
                                    MG_VALUE = latestvalue
                                } else if (target === "pct") {
                                    PROC_VALUE = latestvalue
                                }
                            }
                        }
                    })
                    this.updateLabviewIfNeed(arrayLength, index, target)
                })
                .catch(err => {
                    console.log("getStoredProcedureRESDISP NG " + JSON.stringify(err));
                    this.updateLabviewIfNeed(arrayLength, index, target)
                });
        })
    }
    updateLabviewIfNeed(arrayLength, currentIdx, target) {
        const retryMax = 3
        if (currentIdx === arrayLength - 1) {
            if (target === "mg" && this.state.dummy_mg < retryMax) {
                this.setState({ dummy_mg: this.state.dummy_mg + 1 })
            } else if (target === "pct" && this.state.dummy_pct < retryMax) {
                this.setState({ dummy_pct: this.state.dummy_pct + 1 })
            }
        }
    }
    TitleRender() {
        const { Source } = this.props
        if (Source === "Wardview") {
            return null
        }
        else return (
            <div style={{ height: '40px', paddingLeft: "5px", paddingRight: "5px", background: "#cccccc", display: "flex", alignItems: "center" }}>
                <t1>檢驗報告</t1>
            </div>
        );
    }
    render() {
        function ListEndData(array) {
            if (array.length === 0) {
                return "--"
            }
            return array[array.length - 1]
        }
        // const data = labdata.data
        const { RESDGLU1, PickerDate, RESLAB02, RESLAB01, RESDBGAS, RESSECT, Source } = this.props
        console.log(RESDGLU1)

        const RESDGLU1LIST = FiliterReg.ListForTime(RESDGLU1, PickerDate)
        const RESLAB02LIST = FiliterReg.ListForTime(RESLAB02, PickerDate)
        const RESLAB01LIST = FiliterReg.ListForTime(RESLAB01, PickerDate)
        const RESDBGASLIST = FiliterReg.ListForTime(RESDBGAS, PickerDate)
        // const RESLAB0CLIST = FiliterReg.ListForTime(RESLAB0C, PickerDate)

        const RESSECT_MG_LIST = FiliterReg.ListForTimeAndTarget(RESSECT, PickerDate, "Mg")
        this.findLastestTargetValue(RESSECT_MG_LIST, "mg")

        const RESSECT_PROC_LIST = FiliterReg.ListForTimeAndTarget(RESSECT, PickerDate, "Procalcitonin")
        this.findLastestTargetValue(RESSECT_PROC_LIST, "pct")

        const GLUFAST = ListEndData(RESDGLU1LIST.filter(function (value, index, array) {
            return value.GLUFAST !== null
        }))
        const WBC = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV01 !== null
        }))
        const SEG = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV14 !== null
        }))
        const LYMP = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV15 !== null
        }))
        const HGB = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))
        const PLATELET = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV09 !== null
        }))
        const NA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV13 !== null
        }))
        const K = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV14 !== null
        }))
        const CA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            // console.log(value.RSV15)
            return value.RSV15 !== null
        }))
        const P = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV17 !== null
        }))
        const ALB = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV02 !== null
        }))
        const CRP = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV32 !== null
        }))
        const PH = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS07 !== null
        }))
        const PCO2 = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS06 !== null
        }))
        const BE = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS01 !== null
        }))
        const PO2 = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS08 !== null
        }))
        const BUN = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV05 !== null
        }))
        //02
        const CR = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV06 !== null
        }))
        const ALT = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV11 !== null
        }))
        const AST = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV12 !== null
        }))
        const TBIL = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV08 !== null
        }))
        const DBIL = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV25 !== null
        }))
        const ALKP = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV09 !== null
        }))
        const TOTALCA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))
        const IRON = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV26 !== null
        }))
        const TIBC = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV27 !== null
        }))
        const FERRITIN = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))

        function areainterval(source) {
            if (source === "Wardview") {
                return "0px"
            }
            else return "10px"
        }

        function nullvalue(value) {
            if (value === undefined || value === null) {
                return "--"
            }
            return value.toString().trim()
        }
        const TitleR = this.TitleRender()
        return (
            <div style={{ marginBottom: "10px" }}>
                {TitleR}
                <div style={{ display: "grid", gridTemplateColumns: "1fr 5fr", gridColumnGap: areainterval(Source) }}>
                    <div style={{ border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(GLUFAST.GLUFAST)} title={"Glucose"} time={GLUFAST.ORBGNDT}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(5,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(WBC.RSV01)} title={"WBC"} time={WBC.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(SEG.RSV14)} title={"Seg"} time={SEG.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(LYMP.RSV15)} title={"Lymp"} time={LYMP.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(HGB.RSV03)} title={"Hgb"} time={HGB.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PLATELET.RSV09)} title={"Platelet"} time={PLATELET.ORBGNDT}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "5fr 1fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(5,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(NA.RSV13)} title={"Na+"} time={NA.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(K.RSV14)} title={"K+"} time={K.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(CA.RSV15)} title={"Ca+"} time={CA.ORBGNDT}></Labviewitem> {/*parse*/}
                        <Labviewitem Source={Source} value={nullvalue(P.RSV17)} title={"P"} time={P.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(MG_VALUE)} title={"Mg2+"} time={"2019-12-12"}></Labviewitem> {/*parse*/}
                    </div>
                    <div style={{ border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALB.RSV02)} title={"Alb"} time={ALB.ORBGNDT}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 4fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(CRP.RSV32)} title={"CRP"} time={CRP.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PROC_VALUE)} title={"Procalcitionin"} time={"2019-12-12"}></Labviewitem>  {/*parse*/}
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(PH.SS07)} title={"pH"} time={PH.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PCO2.SS06)} title={"PCO2"} time={PCO2.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(BE.SS01)} title={"BE"} time={BE.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PO2.SS08)} title={"PO2"} time={PO2.ORBGNDT}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 2fr 2fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(BUN.RSV05)} title={"BUN"} time={BUN.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(CR.RSV06)} title={"Cr"} time={CR.ORBGNDT}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALT.RSV11)} title={"ALT"} time={ALT.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(AST.RSV12)} title={"AST"} time={AST.ORBGNDT}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(TBIL.RSV08)} title={"TBil"} time={TBIL.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(DBIL.RSV25)} title={"DBil"} time={DBIL.ORBGNDT}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 4fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALKP.RSV09)} title={"ALKP"} time={ALKP.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(TOTALCA.RSV03)} title={"Total Ca"} time={TOTALCA.ORBGNDT}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(IRON.RSV26)} title={"Iron"} time={IRON.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(TIBC.RSV27)} title={"TIBC"} time={TIBC.ORBGNDT}></Labviewitem>
                        <Labviewitem Source={Source} value={"--"} title={"FeSat"} time={"2019-12-12"}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(FERRITIN.RSV03)} title={"Ferritin"} time={FERRITIN.ORBGNDT}></Labviewitem> {/* No Data */}
                    </div>
                </div>
            </div>
        );
    }
}

export default Labviewlayout