import React, { Component } from 'react';
import { Modal, Input } from 'antd';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

export default class RemarkModal extends Component {
    state = {
        remark: this.props.remarkValue.remark,
        editor: this.props.remarkValue.editor,
    }


    
    componentDidMount() {
        console.log("RemarkModal componentDidMount");
        console.log("RemarkModal caseinfo: " + JSON.stringify(this.props.caseinfo));
        this.onInputChange = this.onInputChange.bind(this);
        this.onOkClick = this.onOkClick.bind(this);
    }

    onInputChange(event) {
        this.setState({ remark: event.target.value })
    }

    onOkClick() {
        const { name, transinId, type, remarkId } = this.props.remarkValue
        const { remark, editor, } = this.state;
        let fixTime = moment().format('X');


        // console.log(`editor : ${editor}`)
        // console.log(`name : ${name}`)
        // console.log(`remark : ${remark}`)
        // console.log(`remarkId : ${remarkId}`)
        // console.log(`fixTime: ${fixTime}`)
        // console.log(`transinId : ${transinId}`)
        // console.log(`type : ${type}`)

        ApiProvider.createRemark(name, remark, remarkId, fixTime, transinId, type, editor)
            .then(result => {
                console.log("createRemark OK " + JSON.stringify(result));
                this.props.onOk(result.data.remark);
            })
            .catch(err => {
                console.log("createRemark NG " + JSON.stringify(err));
            });
    }

    render() {
        const { TextArea } = Input;
        const { createTime, name } = this.props.remarkValue;
        const onCancel = this.props.onCancel;
        const onOk = this.onOkClick.bind(this);

        return (
            <Modal
                title="備註"
                visible={this.props.visible}
                onOk={onOk}
                onCancel={onCancel}>
                <div style={{ display: "grid", gridTemplateRows: "30px 30px 30px 30px" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>開立時間: {createTime}</div>
                        <div>上次編輯時間: </div>
                        {/* <div>上次編輯時間: {this.state.editTime}</div> */}
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>處理名稱: {name}</div>
                        <div>上次編輯人: {this.state.editor}</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>頻次:</div>
                        <div>開立醫師: {this.state.editor}</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>備註內容:</div>
                    <TextArea rows={5} placeholder="請輸入備註內容" value={this.state.remark} onChange={this.onInputChange} />
                </div>
            </Modal>
        )
    }
}
