import React, { Component } from 'react';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';
import * as ApiGithub from '../../API/ApiGithub'
import './NonlabviewtableCell.scss'

const STATUS_REPORT = 0

export default class NonlabviewtableCell extends Component {
    state = {
        Result: [],
        parser: { "mappings": [] },
        summary_str: "--",
        RESDISP: []
    }

    componentDidMount() {
        //call API
        const { caseId, hisId, month, seq, name, time, status } = this.props;

        if (caseId !== undefined && hisId !== undefined && month !== undefined && seq !== undefined) {
            ApiProvider.getStoredProcedureRESDISP(hisId.trim(), month, caseId.trim(), seq)
                .then(result => {
                    console.log("getStoredProcedureRESDISP OK " + JSON.stringify(result.data));
                    //const fakeData = [{"RECNO":"1","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"正式報告","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"2","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"                                臺北榮民總醫院兒童醫學部報告","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"3","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  病患資訊：      新生兒加護病房 - 1 黃苡齊 4565531-0 女性 5月大 NBD","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"4","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  申請序號 / 工作號：  0ANWCEA/            開立時間：   20200115    0304","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"5","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  開醫囑者：                     簽收時間：   20200115    0305","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"6","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  報告人 ：       NUR2325 袁藝榛         報告時間：   20200115    0305","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"7","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  檢體 ：        Blood","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"8","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"  醫囑名稱：      BLOOD GAS","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"9","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   Sample:(Capillary)","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"10","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   PH:  (7.37)      Hb:   ()       BUN: ()","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"11","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   PO2:  (43)  mmHg   Hct:  (39)  %     Cr:  ()","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"12","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   PCO2: (59)  mmHg   FIO2:  (40.0) %","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"13","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   HCO3: (34.1) mmol/L  NA+:  (151)  mmol/L","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"14","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   BE:  (7.1)  mmol/L  K+:   (6.0)  mmol/L","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"15","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   SAT:  (77)  %     Cl-:  ()","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"16","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   TCO2: (35.9) mmol/L  iCA++: (1.44) mmol/L","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"17","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"                AnGap: ()","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"18","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"                MG++:  ()","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"19","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"","ORDSEQ":"1220","CASENO":"25026885"},{"RECNO":"20","PARTNO":"01 ","HISTNO":"45655310  ","ORBGNDT":"2020-01-15","CONTENT":"   COMMENT:","ORDSEQ":"1220","CASENO":"25026885"}];

                    this.setState({
                        RESDISP: result.data
                        //RESDISP: fakeData
                    })

                    let input = {
                        item_name: name.trim(),
                        content: "",
                        sample: "",
                        lab: "",
                        datatime: moment(time).format("YYYY-MM-DD HH:mm")
                    }

                    //fakeData.forEach(item => {
                    result.data.forEach(item => {
                        input.content = `${input.content} "\n" ${item.CONTENT}`;
                    });

                    //status 0:正式報告 只有0需要做parse
                    //console.log("status: " + JSON.stringify(status));
                    if(parseInt(status) === STATUS_REPORT) {
                        ApiGithub.getGitHubJson("Summary.json")
                        .then(data => {
                            console.log("get Summary.json OK " + JSON.stringify(data.data));

                            const selected_parser = this.select_parser(input, data.data)
                            console.log("selected_parser " + JSON.stringify(selected_parser));
                            this.setState({
                                parser: selected_parser
                            })

                            if (selected_parser !== undefined) {
                                const result_kv_pairs = this.parse(input, selected_parser)
                                console.log("result_kv_pairs " + JSON.stringify(result_kv_pairs));

                                const summary_str = this.summerize(result_kv_pairs, selected_parser)
                                console.log("summary_str " + JSON.stringify(summary_str));

                                this.setState({
                                    summary_str: summary_str
                                })
                            }
                        })
                        .catch(err => {
                            console.log(err)
                        })
                    }
                })
                .catch(err => {
                    console.log("getStoredProcedureRESDISP NG " + JSON.stringify(err));
                });
        }
    }

    select_parser = (input, parsers) => {
        return parsers.find(x =>
            input.item_name.match(x.apply_condition.name)
            && (!x.apply_condition.sample || input.sample.match(x.apply_condition.sample))
            && (!x.apply_condition.lab || input.lab.match(x.apply_condition.lab))
            && (!x.apply_condition.start_time || input.datetime >= x.apply_condition.start_time)
            && (!x.apply_condition.end_time || input.datetime < x.apply_condition.end_time)
        )
    }

    parse = (input, parser) => {
        let mappings = parser.mappings
        let content = input.content
        let result = {}
        mappings.forEach(mapping => {
            const re = new RegExp(mapping.regex_str, mapping.regex_flags)
            const match = content.match(re)
            result[mapping.key] = match && match[mapping.position]
        });
        return result;
    }

    summerize = (kv_pairs, parser) => {
        let summary_str = parser.summary
        for (const key in kv_pairs) {
            let match_replacer = summary_str.match(RegExp("\\[" + key + "(;(.*?))?\\]"))
            let replacer = ""
            if (match_replacer && match_replacer.length === 3) {
                replacer = match_replacer[2]
            }
            summary_str = summary_str.replace(RegExp("\\[" + key + "(;.*?)?\\]"), kv_pairs[key] || replacer)
        }
        return summary_str;
    }

    render() {
        function statusFormat(status) {
            if (status === 0) {
                return "正式報告"
            }
            if (status === 1) {
                return "已簽收"

            }
            if (status === 2) {
                return "待送檢"
            }
            return status
        }
        //status 0:正式報告 只有0需要做parse
        const { time, name, status, come, showModal } = this.props;
        const { summary_str, RESDISP } = this.state;
        const statusRender = statusFormat(status)
        const canShowReport = summary_str !== "--"
        return (
            <div style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 200px 1fr 1fr 1fr" }}>
                <t3 className='nonlabteblecell'>{moment(time).format('MM/DD HH:mm')}</t3>
                <t3 className='nonlabteblecell'>{name}</t3>
                <t3 className='nonlabteblecell'>{come}</t3>
                <t3 className='nonlabteblecell'>{statusRender}</t3>
                {canShowReport  && <t3 className='nonlabteblecell' onClick={() => showModal(RESDISP)} style={{ color: "blue", textDecoration: "underline", cursor: "pointer" }}>{summary_str}</t3>}
                {!canShowReport && <t3 className='nonlabteblecell'>{summary_str}</t3>}
            </div>
        )
    }
}
