import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//Component
import * as Wardstate from '../../Function/Wardstate'
import * as Wardcardvalue from '../WardCardvalue-k'
import { bednumberformat, WardCardgender, pregancyweeksanddays } from '../../Function/Wardbedfunction'
import './Wardcarddetail.scss'
//image
import editlogo from '../../Image/svg/edit2.svg'
import fileLogo from '../../Image/svg/file1.svg'
import file2Logo from '../../Image/svg/file2.svg'
//antdesign
import { Modal, Input, Button } from 'antd';
import * as ApiProvider from '../../API/ApiProvider';
import X2JS from 'x2js';
import moment from 'moment';

class Wardcarddetail extends Component {
    state = {
        hoverstate: "none",
        clickstate: false,
        mouse_hover: false,
        edit_hover: false,
        visible: false,
        pbasignfo: null,
        birthdayweight: "N",
        weight: 0,
        bornweek: 0,
        bornday: 0,
        nowweight: 0,
        report: 0,
        nonreport: 0,
        remark: "",
        TRANSINID: "",
        TRANSINDT: "",
        TRANSINTM: ""
    };

    componentDidMount() {
        this.API_DATA()
    }

    API_DATA() {
        const { data } = this.props
        const source = data
        const HID = source.PHISTNUM
        const TRANSINDT = source.TRANSINDT
        const TRANSINID = source.TRANSINID
        // const CID = source.PCASENO
        const beforeOneDay = new Date().getTime() - (24 * 60 * 60 * 1000)
        ApiProvider.getStoredProcedureRESSECT(HID, 1, "ALL", "DOC3924B")
            .then(data => {
                const ReportList = data.data
                const mapList = ReportList.filter(function (value, index, array) {
                    const DTstring = value.ORRCPDT.toString()
                    const TMstring = value.ORRCPTM.toString()
                    const time = DTstring + TMstring
                    return moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf() > beforeOneDay
                })
                this.setState({
                    report: mapList.length
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESSECT NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureORDLIST(HID, moment(TRANSINDT, "YYYYMMDD").format('YYYY-MM-DD'), "ALL", "DOC3924B")
            .then(data => {
                function datasize(datalist) {
                    const list = []
                    for (let index = 0; index < datalist.length; index++) {
                        const element = datalist[index];
                        if (element.ORSTATUS === "31") {
                            list.push(element)
                        }
                    }
                    return list.length
                }
                this.setState({
                    nonreport: datasize(data.data)
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err));
            });
        ApiProvider.getOnePatientRemark(TRANSINID)
            .then(data => {
                this.setState({
                    remark: data.data[0].context
                })
            })
            .catch(err => {
                console.log("Andy test getOnePatientRemark NG " + JSON.stringify(err));
            })
    }

    //Module show
    handleOk(string) {
        this.setState({
            visible: false
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };
    showModal = () => {
        this.setState({
            visible: true
        });
    };

    Modelrender() {
        function typefrom(from) {
            if (from === 'Custom') {
                return true
            }
            return false
        }
        const { data } = this.props
        const source = data
        const Birthday = source.PBIRTHDT
        const QTWER = Wardstate.XM2JSON(source.QTWPER)
        const QMNC = Wardstate.XM2JSON(source.QMNC)
        const BornWeight = Wardstate.BornWeight(QTWER, Birthday)
        const BornWeeks = QMNC.weeks
        const BornDays = QMNC.days
        return (
            <Modal
                width={270}
                title="填寫病床資訊"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={
                    <div style={{ display: "flex", justifyContent: typefrom(source.type) ? 'space-between' : 'flex-end', alignItems: "center" }}>
                        <Button key="delete" style={typefrom(source.type) ? { border: "1px solid rgba(208,2,27,1)", color: "rgba(208,2,27,1)" } : { display: "none" }} onClick={this.handleCancel}>清床</Button>
                        <div>
                            <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                            <Button key="ok" type="primary" onClick={this.handleOk}>儲存</Button>
                        </div>
                    </div>}>
                <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr 1fr", gridRowGap: "10px" }}>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>病床號碼:</t2>
                        </centerstyle>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div>{bednumberformat(parseInt(source.HBEDNO))}</div>
                        </div>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>病歷號碼:</t2>
                        </centerstyle>
                        <t2>{source.PHISTNUM}</t2>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>出生週數:</t2>
                        </centerstyle>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div style={{ marginRight: "20px" }}>{BornWeeks}</div>
                            <t2 style={{ marginRight: "20px" }}>週</t2>
                            <div style={{ marginRight: "20px" }}>{BornDays}</div>
                            <t2>日</t2>
                        </div>
                    </div>
                    <div className='modulecontent'>
                        <div style={{ display: 'flex', justifyContent: "space-between", alignItems: "cneter", width: "69px" }}>
                            <t2>生</t2>
                            <t2>日:</t2>
                        </div>
                        <t2>{moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD')}</t2>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>出生體重:</t2>
                        </centerstyle>
                        <div style={{ display: 'flex' }}>
                            <Input placeholder="1200" id="weight" defaultValue={BornWeight} />&nbsp;g
                        </div>
                    </div>
                </div>
            </Modal>
        );
    }
    render() {
        function modedisplay(mode) {
            if (mode) {
                return "block"
            }
            else {
                return "none"
            }
        }
        function valueFormat(value) {
            if (value === "" || value === null || value === undefined) {
                return "--"
            }
            return value.toString()
        }
        function valueBornWeight(value) {
            if (value === "" || value === null || value === undefined || value === 0) {
                return ""
            }
            return value.toString() + "g → "
        }
        const { data, previewmode, hbedlist } = this.props
        const source = data
        const Birthday = source.PBIRHDT
        const QTWER = Wardstate.XM2JSON(source.QTWPER)
        const QMNC = Wardstate.XM2JSON(source.QMNC)
        // const QTWPER = source.QTWER
        // const QMNC = source.QMNC
        const BornWeight = Wardstate.BornWeight(QTWER, Birthday)
        const Weight = Wardstate.NewestWeight(QTWER)
        const BornWeeks = QMNC.weeks
        const BornDays = QMNC.days
        const remark = this.state.remark
        if (source === undefined) {
            return null
        }
        const status = {
            date: moment(Birthday, "YYYYMMDD").valueOf(),
            // week: BornWeeks,
            // day: BornDays
            week: 36,
            day: 5
        }
        console.log(status)
        const AgeStatus = Wardstate.CalculateAge(status)
        return (
            <div>
                <div className='wardcarddetailtitle'>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>{bednumberformat(Number(source.HBEDNO))}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "16px", fontWeight: "600" }}>{source.PNAMEC.toString().trim()}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>{source.PHISTNUM}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{WardCardgender(source.PSEX)}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{moment(Birthday, 'YYYYMMDD').format('MM/DD')}</t3>
                    {/* <div onClick={this.showModal} className='wardcarddetileditbutton'>
                        <img src={editlogo} alt='editlogo'></img>
                    </div> */}
                </div>
                <div>
                    <div className='wardinfoweeks'>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {valueFormat(BornWeeks)}+{valueFormat(BornDays)}&gt;
                            <span style={{ fontWeight: "900" }}>{pregancyweeksanddays(BornWeeks, BornDays, moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD'))}</span>
                        </t3>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {valueBornWeight(BornWeight)}g&gt;
                            <span style={{ fontWeight: "900" }}>{valueFormat(Weight)}g</span>
                        </t3>
                        <div className='wardinforeportgroup'>
                            <img src={fileLogo} alt='fileLogo'></img>
                            <t4>{this.state.report}</t4>
                            <img src={file2Logo} alt='file2Logo'></img>
                            <t4>{this.state.nonreport}</t4>
                        </div>
                    </div>
                    <div style={{ display: modedisplay(previewmode), borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <Wardcardvalue.WardCard centerdata={source.centerMonitorData} previewmode={previewmode} PHISTNUM={source.PHISTNUM} PCASENO={source.PCASENO}></Wardcardvalue.WardCard>
                    </div>
                    <div style={{ width: "319px", height: '30px', display: 'grid', gridTemplateColumns: "auto 100px", justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", position: 'relative' }}>
                        <div style={{ overflow: "hidden", fontSize: "12px", textOverflow: "ellipsis", whiteSpace: "nowrap", color: "rgba(0,0,0,0.7)" }}>{remark}</div>
                        <Link className='patientviewbutton' to={{ pathname: "/nicu/patient/" + source.PHISTNUM, state: { "source": hbedlist, "HISID": source.PHISTNUM, "CASEID": source.PCASENO, "BEDNUMBER": source.HBEDNO, "TYPE": true, "TRANSINID": this.state.TRANSINID } }}>
                            <centerstyle>
                                <t4>進入病人資料頁面</t4>
                            </centerstyle>
                        </Link>
                    </div>
                </div>
                {this.Modelrender()}
            </div>
        );
    }
}

export default Wardcarddetail;
