import React, { Component } from 'react';
import './iopage.scss'
import moment from 'moment';
import * as WardState from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
import * as ApiGithub from '../API/ApiGithub'
import X2JS from 'x2js';
import * as QDCList from '../Resource/QDC'

class IOpage extends Component {
    state = {
        QDC: [],
        PreStatus: null,
        PreIntervalStatus: null,
        PreCID: null,
        PrePickerDate: null,
        RegQDC: null,
        CarloriesCal: []
    }
    getAPI(caseid, pickerdate, intervalStatus, status) {
        if (caseid === undefined || caseid === null) {
            return null
        }
        if (pickerdate === undefined || pickerdate === null) {
            return null
        }
        if (intervalStatus === undefined || intervalStatus === null) {
            return null
        }
        if (status === undefined || status === null) {
            return null
        }
        function formatTimer(pickerdate, interstatus, status) {
            const endTimeStamp = pickerdate + 31 * 60 * 60 * 1000
            if (status === "Crossection") {
                return {
                    start: moment(endTimeStamp - (3 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 0) {
                return {
                    start: moment(endTimeStamp - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 1) {
                return {
                    start: moment(endTimeStamp - (3 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 2) {
                return {
                    start: moment(endTimeStamp - (7 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            return {
                start: moment(endTimeStamp - (14 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                end: moment(endTimeStamp).format("YYYYMMDDHHmm")
            }
        }
        const xm2 = new X2JS()
        const timer = formatTimer(pickerdate, intervalStatus, status)
        ApiProvider.getQDC(caseid, timer.start, timer.end)
            .then(data => {
                const Result = xm2.xml2js(data.data).outmsg
                if (Number(Result.ret) === 0) {
                    const parsedata = Result.resconts.rescont
                    if (Array.isArray(parsedata)) {
                        this.setState({ QDC: parsedata })
                    }
                    else {
                        this.setState({ QDC: [parsedata] })
                    }
                }

            })
            .catch(err => {
                console.log("getQDC NG " + JSON.stringify(err));
            });
        // this.setState({ QDC: QDCList.data.outmsg.resconts.rescont })
        ApiGithub.getGitHubJson("IOReg_QDC.json")
            .then(data => {
                this.setState({ RegQDC: data.data })
            })
            .catch(err => {
                console.log("getGithubNG" + JSON.stringify(err))
            })
    }
    componentDidMount() {
        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props
        this.setState({ PreStatus: Status, PreIntervalStatus: IntervalStatus, PreCID: caseinfo.CASEID, PrePickerDate: PickerDate })
        this.getAPI(caseinfo.CASEID, PickerDate, IntervalStatus, Status)

    }
    componentDidUpdate() {
        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props
        const prestatus = this.state.PreStatus
        const preinterstatus = this.state.PreIntervalStatus
        const precid = this.state.PreCID
        const prepickerdate = this.state.PrePickerDate
        if (prepickerdate !== PickerDate || caseinfo.CASEID !== precid || IntervalStatus !== preinterstatus || Status !== prestatus) {
            console.log(Status, IntervalStatus, caseinfo, PickerDate)
            this.setState({ PreStatus: Status, PreIntervalStatus: IntervalStatus, PreCID: caseinfo.CASEID, PrePickerDate: PickerDate })
            this.getAPI(caseinfo.CASEID, PickerDate, IntervalStatus, Status)
        }
    }
    cellRender(arr) {
        const { Status, IntervalStatus } = this.props
        const mapcell = arr.map(function (value, index, array) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>{parseInt(value)}</t2>
                </div>
            );
        })
        if (Status === "Crossection") {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)" }}>
                    {mapcell}
                </div>
            );
        }
        else {
            if (IntervalStatus === 0) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(24,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 1) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(18,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 2) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(21,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 3) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(14,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
        }
    }
    render() {
        function pickerTimeArray(pickerdate, intervalstatus, status) {
            const et = pickerdate + (31 * 60 * 60 * 1000)
            var timearray = []
            if (status === "Crossection") {
                const st = pickerdate + (7 * 60 * 60 * 1000)
                for (let index = 0; index < 3; index++) {
                    const time = st + (index * 8 * 60 * 60 * 1000)
                    timearray.push(time)
                }
            }
            else {
                //IntervalStatus 0:1天 1:3天 2:一週 3:兩週
                if (intervalstatus === 0) {
                    const st = et - (24 * 60 * 60 * 1000)
                    for (let index = 0; index < 24; index++) {
                        const time = st + (index * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 1) {
                    const st = et - (3 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 18; index++) {
                        const time = st + (index * 4 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 2) {
                    const st = et - (7 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 21; index++) {
                        const time = st + (index * 8 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 3) {
                    const st = et - (14 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 14; index++) {
                        const time = st + (index * 24 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
            }
            return timearray
        }

        function starttime(status, intervalstatus, time) {
            if (status === "Crossection") {
                return time - (24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 0) {
                return time - (24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 1) {
                return time - (3 * 24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 2) {
                return time - (7 * 24 * 60 * 60 * 1000)
            }
            return time - (14 * 24 * 60 * 60 * 1000)

        }

        function IntervalToDataArray(startTime, status, intervalstatus, element) {
            if (Array.isArray(element)) {
                if (status === 'Crossection') {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (8 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 0) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (1 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 1) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (4 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 2) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (8 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                const filterdata = element.filter(function (value, index, arr) {
                    const valueTimeString = value.rec_dat + value.rec_tim
                    const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                    const endTime = startTime + (24 * 60 * 60 * 1000)
                    return valueTime > startTime && valueTime < endTime
                })
                return filterdata
            }
            return []
        }

        function SumforEach(arr) {
            var sum = 0;
            arr.forEach(function (element) {
                sum += Number(element);
            });
            return sum;
        }


        function formatQDCtoIO(QDC) {
            function caloriesCal(lost, value) {
                if (lost === "配方奶") {
                    return Number(value) * 0.8
                }
                if (lost === "母奶") {
                    return Number(value) * 0.7
                }
                return 0
            }
            function caloriesCalonnam(nam, value) {
                if (nam === null || nam === undefined || nam === "") {
                    return 0
                }
                const namstring = nam.toString()
                const valueNumber = Number(value)
                if (valueNumber === null || valueNumber === undefined) { return 0 }
                if (namstring.match("10%TPN") !== null) { return valueNumber * 0.34 }
                if (namstring.match("SMOFliquid") !== null) { return valueNumber * 1.8 }
                if (namstring.match("D10") !== null) { return valueNumber * 0.34 }
                if (namstring.match("D12") !== null) { return valueNumber * 0.34 * 1.2 }
                if (namstring.match("D50") !== null) { return valueNumber * 0.34 * 5 }
                if (namstring.match("taita") !== null) { return valueNumber * 0.34 }
                return 0
            }
            var FormatFeedingList = []
            var FormatIVList = []
            var FormatUrineList = []
            var FormatOtherInList = []
            var FormatOtherOutList = []
            for (let index = 0; index < QDC.length; index++) {
                const element = QDC[index];
                const elementItem = element.item;
                const time = moment(element.rec_dat.toString().trim() + element.rec_tim.toString().trim(), "YYYYMMDDHHmm").valueOf()
                const nam = element.nam
                const FeedingReg = RegQDC.Feeding
                const filterFeedingStatus = FeedingReg.some(function (info, index, arr) {
                    return info === elementItem
                })
                if (filterFeedingStatus) {
                    FormatFeedingList.push({
                        time: time,
                        value: Number(element.inp_val2),
                        calories: caloriesCal(element.lost, element.inp_val2),
                        element: element
                    })
                }
                const IVReg = RegQDC.IV
                const filterIVStatus = IVReg.some(function (info, index, arr) {
                    return info === elementItem
                })
                if (filterIVStatus) {
                    FormatIVList.push({
                        time: time,
                        value: Number(element.inp_val1),
                        calories: caloriesCalonnam(nam, element.inp_val1),
                        element: element
                    })
                }
                const UrineReg = RegQDC.Urine
                const filterUrineStatus = UrineReg.some(function (info, index, arr) {
                    return info === elementItem
                })
                if (filterUrineStatus) {
                    FormatUrineList.push({
                        time: time,
                        value: Number(element.out_val2),
                        calories: caloriesCalonnam(nam, element.out_val2),
                        element: element
                    })
                }
                const OtherReg = RegQDC.Other
                const filterOtherStatus = OtherReg.some(function (info, index, arr) {
                    return info === elementItem
                })
                if (filterOtherStatus) {
                    const input_1 = Number(element.inp_val1)
                    const output_1 = Number(element.out_val1)
                    if (elementItem === "排便") {
                        // FormatOtherInList.push({
                        //     time: time,
                        //     value: 1,
                        //     calories: 0,
                        //     element: element
                        // })
                        FormatOtherOutList.push({
                            time: time,
                            value: 1,
                            calories: 0,
                            element: element
                        })
                    }
                    else {
                        if (input_1 === null || input_1 === undefined) {
                            FormatOtherOutList.push({
                                time: time,
                                value: Number(element.inp_val1),
                                calories: caloriesCalonnam(nam, element.inp_val1),
                                element: element
                            })
                        }
                        if (output_1 === null || output_1 === undefined) {
                            FormatOtherInList.push({
                                time: time,
                                value: Number(element.output_1),
                                calories: 0,
                                element: element
                            })
                        }
                    }
                }

            }
            return {
                Feeding: FormatFeedingList,
                IV: FormatIVList,
                OtherIn: FormatOtherInList,
                Urine: FormatUrineList,
                OtherOut: FormatOtherOutList
            }
        }
        function timerFormat(PickerDate, Interval, Status) {
            const EndTime = PickerDate + (31 * 60 * 60 * 1000)
            console.log(Status)
            if (Status === 'Crossection') {
                return {
                    interval: 3,
                    hourInterval: 8,
                    startTime: EndTime - (24 * 60 * 60 * 1000),
                    endTime: EndTime
                }
            }
            if (Interval === 0) {
                return {
                    interval: 24,
                    hourInterval: 1,
                    startTime: EndTime - (24 * 60 * 60 * 1000),
                    endTime: EndTime
                }
            }
            if (Interval === 1) {
                return {
                    interval: 18,
                    hourInterval: 4,
                    startTime: EndTime - (3 * 24 * 60 * 60 * 1000),
                    endTime: EndTime
                }
            }
            if (Interval === 2) {
                return {
                    interval: 21,
                    hourInterval: 8,
                    startTime: EndTime - (7 * 24 * 60 * 60 * 1000),
                    endTime: EndTime
                }
            }
            return {
                interval: 14,
                hourInterval: 24,
                startTime: EndTime - (14 * 24 * 60 * 60 * 1000),
                endTime: EndTime
            }
        }
        function listTimerFormat(List, timer, status) {
            const inter = timer.interval
            var TimerList = []
            for (let index = 0; index < inter; index++) {
                const st = timer.startTime + (index * timer.hourInterval * 60 * 60 * 1000)
                const et = timer.startTime + ((index + 1) * timer.hourInterval * 60 * 60 * 1000)
                const filterList = List.filter(function (info, index, arr) {
                    const time = info.time
                    return st <= time && time < et
                })
                TimerList.push(filterList)
            }
            if (status === 'Crossection') {
                const st = timer.startTime
                const et = timer.endTime
                const filterListALL = List.filter(function (info, index, arr) {
                    const time = info.time
                    return st <= time && time < et
                })
                TimerList.push(filterListALL)
            }
            return TimerList
        }

        function sumIn(arr1, arr2, arr3) {
            var sumlist = []
            for (let index = 0; index < arr1.length; index++) {
                const element1 = arr1[index];
                const element2 = arr2[index];
                const element3 = arr3[index];
                const sum = element1 + element2 + element3
                sumlist.push(sum)
            }
            return sumlist
        }
        function sumOut(arr1, arr2) {
            var sumlist = []
            for (let index = 0; index < arr1.length; index++) {
                const element1 = arr1[index];
                const element2 = arr2[index];
                const sum = element1 + element2
                sumlist.push(sum)
            }
            return sumlist
        }
        function sumInAndOut(Input, Outout) {
            var sumlist = []
            for (let index = 0; index < Input.length; index++) {
                const element1 = Input[index];
                const element2 = Outout[index];
                const sum = parseFloat(element1 - element2).toFixed(2)
                sumlist.push(sum)
            }
            return sumlist
        }
        function gridNumber(interval, status) {
            if (status === "Crossection") {
                return 4
            }
            return interval
        }
        function cellsum(list) {
            const CellSum = list.map(function (info, index, arr) {
                var sum = 0
                for (let index = 0; index < info.length; index++) {
                    const element = info[index];
                    sum = sum + element.value
                }
                return sum
            })
            return CellSum
        }
        function cellRenderSum(list) {
            const CellRender = list.map(function (info, index, arr) {
                if (info.length === 0) {
                    return (
                        <div key={index} style={{ borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #cccccc", borderCollapse: 'collapse' }}>--</div>

                    );
                }
                return (
                    <div key={index} style={{ borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #cccccc", borderCollapse: 'collapse' }}>{parseFloat(info).toFixed(2)}</div>
                );
            })
            return (
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat( ' + gridNumber(timerStruct.interval, Status) + ' ,1fr)' }}>
                    {CellRender}
                </div>
            );
        }
        function cellRender(list) {
            const CellRender = list.map(function (info, index, arr) {
                var sum = 0
                if (info.length === 0) {
                    return (
                        <div key={index} style={{ borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #cccccc", borderCollapse: 'collapse' }}>--</div>
                    );
                }
                for (let index = 0; index < info.length; index++) {
                    const element = info[index];
                    sum = sum + element.value
                }
                return (
                    <div key={index} style={{ borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #cccccc", borderCollapse: 'collapse' }}>{parseFloat(sum).toFixed(2)}</div>
                );
            })
            return (
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat( ' + gridNumber(timerStruct.interval, Status) + ' ,1fr)' }}>
                    {CellRender}
                </div>
            );
        }


        function formatTime(time) {
            if (Number(time) / 1000 >= 1) {
                return time.toString().trim()
            }
            return "0" + time.toString().trim()
        }

        function stylechange(status) {
            const chronicstyle = "140px 1fr"
            const crossectionstyle = "1fr 4fr"
            if (status === "Crossection") {
                return crossectionstyle
            }
            else return chronicstyle
        }

        function urineforWeightAndHourCrossection(Urine, weight) {
            const datamap = Urine.map(function (info, index, arr) {
                var sum = 0
                for (let index = 0; index < info.length; index++) {
                    const element = info[index];
                    sum = sum + element.value
                }
                if (sum === 0) {
                    return 0
                }
                if (weight === null || weight === undefined) {
                    return "--"
                }
                return parseFloat(sum / weight / 8).toFixed(2)
            })
            for (let index = 0; index < Urine.length; index++) {
                const element = Urine[index];
                console.log(element)
                
            }
            return datamap
        }
        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props
        const timeInterval = pickerTimeArray(PickerDate, IntervalStatus, Status)
        const et = PickerDate + (31 * 60 * 60 * 1000)
        const st = starttime(Status, IntervalStatus, et)
        const WEIGHT = caseinfo.WEIGHT
        const dateWeoght = WardState.selectweight(WEIGHT, PickerDate)
        const QDCLIST = this.state.QDC
        const Weight = 1000
        const RegQDC = this.state.RegQDC
        console.log(RegQDC, PickerDate)
        if (RegQDC === null || RegQDC === undefined) { return null }
        const formatData = formatQDCtoIO(QDCLIST)
        const timerStruct = timerFormat(PickerDate, IntervalStatus, Status)
        const feed = listTimerFormat(formatData.Feeding, timerStruct, Status)
        const IV = listTimerFormat(formatData.IV, timerStruct, Status)
        const OtherIn = listTimerFormat(formatData.OtherIn, timerStruct, Status)
        const sumin = sumIn(cellsum(feed), cellsum(IV), cellsum(OtherIn))
        const Urine = listTimerFormat(formatData.Urine, timerStruct, Status)
        const UrinForUnit = urineforWeightAndHourCrossection(Urine, 1000)
        const OtherOut = listTimerFormat(formatData.OtherOut, timerStruct, Status)
        const sumout = sumOut(cellsum(Urine), cellsum(OtherOut))
        console.log(Urine)
        const sumInTotal = sumInAndOut(sumin, sumout)
        //====================================================================


        const titleInarray = ['Feed', 'IV', 'Other', 'In:Sum']
        const titleOutarray = ['U/O', 'ml/kg/hr', 'Other', 'Out:Sum']
        const TitleCellIn = titleInarray.map(function (value, index, arr) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", backgroundColor: Status === "Crossection" ? "white" : "#cccccc" }}>
                    <t2>{value}</t2>
                </div>
            );
        })

        const TitleCellOut = titleOutarray.map(function (value, index, arr) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", backgroundColor: Status === "Crossection" ? "white" : "#cccccc" }}>
                    <t2>{value}</t2>
                </div>
            );
        })
        return (
            <div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {TitleCellIn}
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {cellRender(feed)}
                        {cellRender(IV)}
                        {cellRender(OtherIn)}
                        {cellRenderSum(sumin)}
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {TitleCellOut}
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {cellRender(Urine)}
                        {cellRenderSum(UrinForUnit)}
                        {cellRender(OtherOut)}
                        {cellRenderSum(sumout)}
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: "center", justifyContent: "center" , backgroundColor: Status === "Crossection" ? "white" : "#cccccc" }}>
                        <t2>I-O</t2>
                    </div>
                    {cellRenderSum(sumInTotal)}
                    {/* {totalcell} */}
                </div>
            </div>
        );
    }

}

export default IOpage;