import React, { Component } from 'react'
import Nurtionfeedingorder from './Nurtionfeeding'
import { Select, Button, DatePicker, Modal } from 'antd';
import WardlifeChart from '../../Wardview/Wardlifechart'
import Wardmonthafter from '../../Wardview/Wardmonthafter'
import Nurtiontpnorder from './Nurtion_TPN'
import Watervaluechart from './Watervaluechart'
import { gender } from '../../Function/Wardbedfunction'
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'
import * as FiliterReg from '../../Function/FiliterReg'
import './Nurtion.scss'

class Nurtionview extends Component {
    state = {
        visible: false,
        test_items_interval: 1,
        UDORDER0: [],
        QDC: [],
        QTWER: [],
        PickerDateRange: moment(moment().format('YYYYMMDD'))
    }
    componentDidMount() {
        this.API_DATA()
    }
    API_DATA() {
        const { caseinfo } = this.props
        const xm2 = new X2JS()
        const st = moment(caseinfo.TRANSINDT.toString(), 'YYYYMMDD').format('YYYYMMDDHHmm')
        const et = moment().format("YYYYMMDDHHmm")
        // ApiProvider.getQDC(caseinfo.CASEID, st, et)
        ApiProvider.getQDC(caseinfo.CASEID, st, et)
            .then(data => {
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                console.log(parsedata)
                this.setState({
                    QDC: parsedata
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    switch_monthy() {
        const { caseinfo } = this.props
        console.log(caseinfo)
        const grewinfo = {
            "WEEK_BORN": Number(caseinfo.WEEK_BORN),
            "DAY_BORN": Number(caseinfo.DAY_BORN),
            "BIRTHDAY": caseinfo.PBASINGNINFO.PBIRTHDT
        }
        const WeightList = caseinfo.WEIGHT
        const sex = gender(caseinfo.PBASINGNINFO.PSEX)
        const switch_data = this.state.test_items_interval
        switch (switch_data) {
            case 1:
                return <WardlifeChart grewinfo={grewinfo} weightList={WeightList} id_key={"wardlife"} svg_key={"wardlife_svg"} gender={sex}></WardlifeChart>
            case 0:
                return <Wardmonthafter grewinfo={grewinfo} weightList={WeightList} id_key={"wardlife_after"} svg_key={"wardlife_after_svg"} gender={sex}></Wardmonthafter>
            default:
                return null;
        }
    }

    handleChange = (checkedValues) => {
        console.log('checked = ', checkedValues);
        this.setState(
            {
                test_items_interval: checkedValues
            }
        )

    }
    onOk = (value) => {
        console.log('onOk: ', value);
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    WaterValueControlBar() {
        return (
            <div style={{ display: "flex", alignItems: "center" }}>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", marginRight: "10px" }}>
                    <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <div style={{ height: "10px", width: "10px", background: "#C3EFFF", marginRight: "5px" }}></div>
                        <t3>IV</t3>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <div style={{ height: "10px", width: "10px", background: "#2E64A4", marginRight: "5px" }}></div>
                        <t3>Feed</t3>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <div style={{ height: "10px", width: "10px", background: "#3B97E1", marginRight: "5px" }}></div>
                        <t3>Other</t3>
                    </div>
                </div>
                {/* <Button type="primary" onClick={this.showModal}>設定營養項目</Button> */}
            </div>
        );
    }
    GrowthCurveControlBar() {
        const { Option } = Select;
        return (
            <div className='titlebutton'>
                <Select defaultValue={1} style={{ width: 120 }} onChange={this.handleChange}>
                    <Option value={0}>滿足月</Option>
                    <Option value={1}>不滿足月</Option>
                </Select>
                <Button type="primary" onClick={this.showModal}>新增體重</Button>
                <Modal
                    title="手動新增體重紀錄"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <div style={{ display: "flex", alignItems: "center" }}>選擇時間:</div>
                        <DatePicker id="nurtion_datepicker" style={{ width: "400px" }} showTime placeholder="選擇時間" onOk={this.onOk} />
                    </div>
                </Modal>
            </div>
        );
    }
    WaterValueRender() {
        function formatTimeArray(startTime) {
            const shiftTimer = parseInt((moment().valueOf() - moment(startTime, "YYYYMMDD").valueOf()) / (23 * 60 * 60 * 1000))
            console.log(startTime, shiftTimer)
            var TimerArray = []
            for (let index = 0; index < shiftTimer; index++) {
                const Time = moment(moment(startTime, "YYYYMMDD").valueOf() + (index * 24 * 60 * 60 * 1000)).format("YYYY/MM/DD")
                TimerArray.push(Time)
            }
            return TimerArray
        }
        const { caseinfo } = this.props
        const QDC = this.state.QDC
        if (QDC.length === 0) {
            return null
        }
        var FeedinfData = []
        var IVData = []
        var OtherData = []
        var Calories = 0
        for (let index = 0; index < QDC.length; index++) {
            const element = QDC[index];
            const feedstatus = FiliterReg.Feedarea_Reg_IO().find(function (value, index, array) {
                return element.item === value
            })
            if (feedstatus) {
                FeedinfData.push(element)
            }
            const IVstatus = FiliterReg.IVarea_reg_IO().find(function (value, index, array) {
                return element.item === value
            })
            if (IVstatus) {
                IVData.push(element)
            }
            const Otherstatus = FiliterReg.Otherarea_reg_IO().find(function (value, index, array) {
                return element.item === value
            })
            if (Otherstatus) {
                const invalue = element.inp_val1
                if (invalue !== undefined) {
                    OtherData.push(element)
                }
            }
            console.log(element)


        }

        console.log(FeedinfData)
        console.log(IVData)
        console.log(OtherData)
        function sumcar(List) {
            var Calories = 0
            for (let index = 0; index < List.length; index++) {
                const element = List[index];
                if (element.item === "進食") {
                    if (element.lost === "配方奶") {
                        Calories = Calories + Number(element.inp_val2) * 0.8
                    }
                    if (element.lost === "母奶") {
                        Calories = Calories + Number(element.inp_val2) * 0.7

                    }
                }
                if (element.nam !== undefined) {
                    if (element.nam.toString().match("10%TPN") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("15%TPN") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 1.5
                    }
                    if (element.nam.toString().match("SMOFliquid") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("D10") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("D12") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 1.2
                    }
                    if (element.nam.toString().match("D5W") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 5
                    }
                    if (element.nam.toString().match("taita") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                }

            }
            return Calories

        }
        const startTime = caseinfo.TRANSINDT.toString()
        const TimerList = formatTimeArray(startTime)
        var watervalueDataList = []
        for (let index = 0; index < TimerList.length; index++) {
            const element = TimerList[index];
            const filterFeedind = FeedinfData.filter(function (value, index, arr) {
                return moment(element, "YYYY/MM/DD").valueOf() === moment(value.rec_dat.toString().trim(), "YYYY/MM/DD").valueOf()
            })
            const filterIV = IVData.filter(function (value, index, arr) {
                return moment(element, "YYYY/MM/DD").valueOf() === moment(value.rec_dat.toString().trim(), "YYYY/MM/DD").valueOf()
            })
            const filterOther = OtherData.filter(function (value, index, arr) {
                return moment(element, "YYYY/MM/DD").valueOf() === moment(value.rec_dat.toString().trim(), "YYYY/MM/DD").valueOf()
            })

            const feedingSum = SumData2(filterFeedind)
            const IVSum = SumData(filterIV)
            const OtherSum = SumData(filterOther)

            const Caloriessum = sumcar(filterFeedind) + sumcar(filterIV) + sumcar(filterOther)
            watervalueDataList.push({
                Feeding: feedingSum,
                IV: IVSum,
                Other: OtherSum,
                Time: element,
                Caloriessum
            })

        }
        function SumData(arr) {
            var sum = 0;
            for (var i = 0; i < arr.length; i++) {
                sum += Number(arr[i].inp_val1);
            };
            return sum;
        }

        function SumData2(arr) {
            var sum = 0;
            for (var i = 0; i < arr.length; i++) {
                sum += Number(arr[i].inp_val2);
            };
            return sum;
        }


        console.log(watervalueDataList)
        return (
            <Watervaluechart Calories={Calories} watervalueData={watervalueDataList} startTime={caseinfo.TRANSINDT} id_key={"water_id"} svg_key={"water_svg"} caseiddays='2019/11/11'></Watervaluechart>
        );
    }

    PickerDateRangeChange = (date, dateString) => {
        this.setState({ PickerDateRange: date })
    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const PickerDate = this.state.PickerDateRange.valueOf()
        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = bTime - (6 * day)
                bronDate <= bTemp && this.PickerDateRangeChange(moment(bTime))
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.PickerDateRangeChange(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.PickerDateRangeChange(moment(aTime))
                break;
            default:
                break;
        }
    }

    render() {
        const GrowthCurve = this.switch_monthy()
        const GrowthCurveControl = this.GrowthCurveControlBar()
        const WaterValueControl = this.WaterValueControlBar()
        const tpndata = this.state.UDORDER0
        const feeddata = this.state.TRTMNTQ1
        const TimeStampPickerDate = this.state.PickerDateRange.valueOf()
        const { caseinfo } = this.props

        if (tpndata.length === 0 || feeddata === 0) {
            return null
        }

        const waterRender = this.WaterValueRender()
        const PickerDateRange = [TimeStampPickerDate - (6 * 24 * 60 * 60 * 1000), TimeStampPickerDate]

        return (
            <div className='nurtionheight'>
                <div className='nurtionlayout'>
                    <div>
                        <div className='nurtioncomponmentboxtitle'>
                            <t1>生長曲線圖</t1>
                            {GrowthCurveControl}
                        </div>
                        <div className='nurtioncomponmentboxcontent'>{GrowthCurve}</div>
                    </div>
                    <div>
                        <div className='nurtioncomponmentboxtitle'>
                            <t1>水量表</t1>
                            <div className='titlebutton'>
                                {WaterValueControl}
                            </div>
                        </div>
                        <div className='nurtioncomponmentboxcontent'>
                            {waterRender}
                        </div>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "250px 120px", gridColumnGap: "5px", margin: "10px 0px 5px 0px" }}>
                    <DatePicker
                        value={this.state.PickerDateRange}
                        format={"YYYY/MM/DD"}
                        allowClear={false}
                        locale={"zh_TW"}
                        onChange={this.PickerDateRangeChange}
                    />
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px" }}>
                        <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                        <Button value="N" shape="circle" icon="pause-circle" onClick={this.trainDay} />
                        <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: '1fr 1fr', gridColumnGap: "10px", marginBottom: "20px" }}>
                    <Nurtionfeedingorder PickerDateRange={PickerDateRange} caseinfo={caseinfo}></Nurtionfeedingorder>
                    <Nurtiontpnorder PickerDateRange={PickerDateRange} caseinfo={caseinfo}></Nurtiontpnorder>
                </div>
            </div>
        );
    }
}

export default Nurtionview
