    apiData() {
        const { caseinfo } = this.props
        //入院並摘
        const ADMLIST = ApiProvider.getStoredProcedureADMLIST(caseinfo.HISID, "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureADMLIST NG " + JSON.stringify(err));
            });
        //出院並摘
        const DISLIST = ApiProvider.getStoredProcedureDISLIST(caseinfo.HISID, "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureDISLIST NG " + JSON.stringify(err));
            });

        //門診紀錄清單
        const DTAROTQ4 = ApiProvider.getStoredProcedureDTAROTQ4(caseinfo.HISID)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureDTAROTQ4 NG " + JSON.stringify(err));
            });


        //急診紀錄清單
        const PCASELIST = ApiProvider.getStoredProcedurePCASELIST(caseinfo.HISID, "DOC3924B", "E")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });




        const CPSLIST = ApiProvider.getStoredProcedureCPSLIST(caseinfo.HISID, "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });

        function nullvalue(value) {
            if (value === null) {
                return "911"
            }
            console.log(value)
            const PRGTXQRYDATE = moment(value.PLOCDT, 'YYYYMMDD').format("YYYYMM")
            const D = PRGTXQRYDATE.substr(3, 3)
            return D
        }

        const LIVEPLOC = nullvalue(caseinfo.PLOCSTART)
        console.log(LIVEPLOC)
        const PRGTXQRY = ApiProvider.getStoredProcedurePRGTXQRY(caseinfo.HISID, LIVEPLOC, caseinfo.CASEID)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });
        const transdt = caseinfo.TRANSINDT
        const ST = moment(transdt, "YYYYMMDD").format("YYYYMMDDHHmm")
        const ET = moment().format("YYYYMMDDHHmm")

        const QPNOT = ApiProvider.getQPNOT(caseinfo.CASEID, ST, ET)
            .then(data => {
                const xm2 = new X2JS()
                const source = data.data
                const parseSource = xm2.xml2js(source).outmsg.resconts.rescont
                return parseSource
            })
            .catch(err => {

            })
        const OPSORAQ1 = ApiProvider.getStoredProcedureOPSORAQ1(caseinfo.HISID)
            .then(data => {
                return data.data
            })
            .catch(err => { console.log(err) })
        Promise.all([ADMLIST, DISLIST, DTAROTQ4, PCASELIST, PRGTXQRY, CPSLIST, QPNOT, OPSORAQ1]).then(result => {
            console.log(result)
            this.setState({
                ADMLIST: result[0],
                DISLIST: result[1],
                DTAROTQ4: result[2],
                PCASELIST: result[3],
                PRGTXQRY: result[4],
                CPSLIST: result[5],
                QPNOT: result[6],
                OPSORAQ1: result[7]
            })
        })
    }



