import React, { Component } from 'react'
import { Tooltip } from 'antd'
import moment from 'moment'
import Chronictablecelltooltip from './ChronicTableCellTooltipCell'

class Chronictablecell extends Component {
    CellText() {
        const { element, caseinfo, summaryParse } = this.props
        const start = moment(element.StartTime).format("YYYY/MM/DD HH:mm:ss")
        const startTimeStamp = moment(element.StartTime).valueOf()
        const end = moment(element.EndTime).format("HH:mm:ss")
        const title = ["報告類別", "採檢時間", "檢驗數據"]
        const titleCell = title.map(function (value, index) {
            return (
                <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                    {value}
                </div>
            );
        })
        const objectElement = element.element
        const objectElementCell = objectElement.map(function (info, index, arr) {
            return (
                <Chronictablecelltooltip key={index} startTimeStamp={startTimeStamp} tooltipelement={info} caseinfo={caseinfo} summaryParse={summaryParse}></Chronictablecelltooltip>
            );
        })
        return (
            <div>
                <div>{start}-{end}</div>
                <div>檢驗檢查項目 : {element.title}類</div>
                <div style={{ display: "grid", gridTemplateColumns: "300px 100px 200px" }}>
                    {titleCell}
                </div>
                {objectElementCell}
            </div>
        );
    }
    render() {
        function valueNull(value) {
            if (value === null || value === undefined || value === 0) {
                return "--"
            }
            return value
        }
        const { element } = this.props
        const TitleRender = this.CellText()
        if (element.element.length === 0) {
            return (
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>--</div>
            );
        }
        return (
            <Tooltip title={TitleRender} autoAdjustOverflow={true} overlayStyle={{ maxWidth: "800px" }}>
                <div style={{ fontSize: "16px", display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                    {valueNull(element.element.length)}
                </div>
            </Tooltip>
        );
    }
}

export default Chronictablecell