import React, { Component } from 'react';
import { Icon, Upload, Table, Input, DatePicker, Button } from 'antd';
import { Link } from 'react-router-dom';
import * as ApiProvider from '../API/ApiProvider';
import moment from 'moment';

class Historyview extends Component {
    state = {
        MouseClick: 0,
        allPatientDisplayData: [],
        allPatientUnset: [],
        allPatientSet: [],
        selectedRowsArray: [],
        hospitalindex: '',
        selectedFile: null,
        fileList: [],
        uploading: false,
        name: '',
        hisid: '',
        doctor: '',
        transOuttime: undefined
    }

    componentDidMount() {
        this.onChangeName = this.onChangeName.bind(this)
        this.onChangeHisid = this.onChangeHisid.bind(this)
        //this.onChangeDoctor = this.onChangeDoctor.bind(this)
        this.onClickSearch = this.onClickSearch.bind(this)
        this.onChangeDate = this.onChangeDate.bind(this)
        console.log("Historyview componentDidMount")
        ApiProvider.getAllPatient()
            .then(result => {
                console.log("getAllPatient OK " + JSON.stringify(result));
                const setedUser = result.data.filter(item => item.transouttime !== "unset");
                const unsetUser = result.data.filter(item => item.transouttime === "unset");
                this.setState({
                    allPatientDisplayData: setedUser,
                    allPatientUnset: unsetUser,
                    allPatientSet: setedUser
                });
            })
            .catch(err => {
                console.log("getAllPatient NG " + JSON.stringify(err));
            });
    }

    MouseClickTable(record) {
        //console.log(this.state.MouseClick)
        this.setState({
            MouseClick: record
        })
    }

    EditStyle(HISID, CASEID, TRANSINID, key) {
        if (key === this.state.MouseClick) {
            return (
                <Link to={{ pathname: "/nicu/patient/" + HISID, state: { "source": [], "HISID": HISID, "CASEID": CASEID, "BEDNUMBER": 0, "TYPE": false, "TRANSINID": TRANSINID } }} style={{ height: "40px", width: "100px", borderRadius: "4px" }}>檢視</Link>
            );
        }
        else return null
    }

    genderswitch(key) {
        if (key === "M") {
            return '男'
        }
        else {
            return '女'
        }
    }

    bornweeks(birthday) {
        //console.log(Math.floor(birthday / 7))
        //console.log(birthday % 7)
        const weeks = Math.floor(birthday / 7)
        const days = birthday % 7
        if (days === 0) {
            return weeks + '週歲'
        }
        else return weeks + '週' + days + '天'
    }

    exportClick() {
        this.state.selectedRowsArray.map((data, index) => {
            ApiProvider.exportJson(data.hospitalindex)
                .then(result => {
                    ApiProvider.downloadFile(result.data.filename)
                        .then(resultD => {
                            const url = window.URL.createObjectURL(new Blob([JSON.stringify(resultD.data)]));
                            const link = document.createElement('a');
                            link.href = url;
                            link.setAttribute('download', result.data.filename);
                            document.body.appendChild(link);
                            link.click();
                        }).catch(err => { })
                })
                .catch(err => { })
        })
    }

    handleUpload = () => {
        const { fileList } = this.state
        const formData = new FormData()
        fileList.forEach(file => {
            formData.append('file', file)
        })

        this.setState({
            uploading: true,
        })

        ApiProvider.uploadFile(formData)
            .then(res => {
                ApiProvider.importJson(res.data)
                    .then(result => {
                        ApiProvider.getAllPatient()
                            .then(result => {
                                const setedUser = result.data.filter(item => item.transouttime !== "unset");
                                const unsetUser = result.data.filter(item => item.transouttime === "unset");
                                this.setState({
                                    allPatientDisplayData: setedUser,
                                    allPatientUnset: unsetUser,
                                    allPatientSet: setedUser
                                });
                            })
                            .catch(err => {
                                console.log("getAllPatient NG " + JSON.stringify(err));
                            });
                    })
                    .catch(err => { })
            })
            .catch(err => { })
    }

    deleteHistoryUser() {
        this.state.selectedRowsArray.map((data, index) => {
            ApiProvider.deletePatient(data.hospitalindex, data.hospitalnumber, data.id)
                .then(res => {
                    ApiProvider.getAllPatient()
                        .then(result => {
                            const setedUser = result.data.filter(item => item.transouttime !== "unset");
                            const unsetUser = result.data.filter(item => item.transouttime === "unset");
                            this.setState({
                                allPatientDisplayData: setedUser,
                                allPatientUnset: unsetUser,
                                allPatientSet: setedUser
                            });
                        })
                        .catch(err => {
                            console.log("getAllPatient NG " + JSON.stringify(err));
                        });
                })
                .catch(err => { })
        })
    }

    onChangeName(event) {
        this.setState({ name: event.target.value })
    }
    onChangeHisid(event) {
        this.setState({ hisid: event.target.value })
    }
    //onChangeDoctor(event) {
    //this.setState({ doctor: event.target.value })
    //}
    onClickSearch() {
        // ApiProvider.getAllPatient()
        //     .then(result => {
        //         const setedUser = result.data.filter(item => item.transouttime !== "unset" && item.hisid === this.state.hisid && item.pnamec === this.state.name);
        //         const unsetUser = result.data.filter(item => item.transouttime === "unset" && item.hisid === this.state.hisid && item.pnamec === this.state.name);
        //         console.log(this.state.transOuttime)
        //         this.setState({
        //             allPatientDisplayData: setedUser,
        //             allPatientUnset: unsetUser,
        //             allPatientSet: setedUser
        //         });
        //     })
        //     .catch(err => {
        //         console.log("getAllPatient NG " + JSON.stringify(err));
        //     });
    }
    onChangeDate(date, dateString) {
        this.setState({ transOuttime: dateString })
    }

    Searchbar() {
        const InputGroup = Input.Group;
        const { RangePicker } = DatePicker;

        return (
            <div style={{ display: 'flex', alignItems: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{ fontSize: '16px' }}>
                        個案姓名:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="輸入個案姓名" onChange={this.onChangeName} value={this.state.name}></Input>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        轉入時間:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        {/* <InputGroup compact> */}
                        {/* <DatePicker placeholder="選擇轉出時間" onChange={this.onChangeDate} /> */}
                        <RangePicker style={{ width: "250px" }} format={"YYYY/MM/DD"} locale={"zh_TW"}
                            placeholder={["請輸入日期", "請輸入日期"]} onChange={this.onChangeDate} allowClear={false} />
                        {/* </InputGroup> */}
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        病歷號碼:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="輸入病歷號碼" onChange={this.onChangeHisid} value={this.state.hisid}></Input>
                    </div>
                </div>
                {/*<div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        主治醫生:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="選擇主治醫師" onChange={this.onChangeDoctor} value={this.state.doctor}></Input>
                    </div>
        </div>*/}
                {/* <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <Button shape="circle" icon="search" onClick={this.onClickSearch} />
                </div> */}
            </div>
        );
    }

    handleDisplaySwitchChange(event) {
        // console.log(event.target.value)
        console.log(this.state.allPatientSet)
        console.log(this.state.allPatientUnset)
        this.setState({
            allPatientDisplayData: event.target.value == 'in' ? this.state.allPatientSet : this.state.allPatientUnset
        })
    }


    render() {
        // rowSelection object indicates the need for row selection
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                //console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
                this.setState({ selectedRowsArray: selectedRows })
            },
            getCheckboxProps: record => ({
                disabled: record.name === 'Disabled User', // Column configuration not to be checked
                name: record.name,
            }),
        };
        const columns = [
            {
                title: '病歷號碼',
                dataIndex: 'id',
                key: 'id',
                width: "10%"
            },
            {
                title: '個案名稱',
                dataIndex: 'name',
                key: 'name',
                width: "15%"
            },
            {
                title: '性別',
                dataIndex: 'gender',
                key: 'gender',
                width: "7%"
            },
            {
                title: '出生體重',
                dataIndex: 'weight',
                key: 'weight',
                width: "8%"
            },
            {
                title: '出生周數',
                dataIndex: 'birweek',
                key: 'birweek',
                width: "8%"
            },
            {
                title: '轉入時年齡',
                dataIndex: 'transage',
                key: 'transage',
                width: "10%"
            },
            {
                title: '院序號',
                dataIndex: 'hospitalnumber',
                key: 'hospitalnumber',
                width: "10%"
            },
            {
                title: '索引號',
                dataIndex: 'hospitalindex',
                key: 'hospitalindex',
                width: "17%"
            },
            {
                title: '轉入ICU時間',
                dataIndex: 'transicutime',
                key: 'transicutime',
                width: "10%"
            },
            //{
            //title: '主治醫師',
            //dataIndex: 'maindoc',
            //key: 'maindoc',
            //width: "10%"
            //},
            {
                dataIndex: 'clickstate',
                key: 'clickstate',
                width: "5%"
            }
        ];
        function search(data, searchName) {
            if (searchName === "" || searchName === undefined || searchName === null) {
                return data
            }
            const filterData = data.filter(function (item, index, arr) {
                console.log(item)
                const fn = item.pnamec.toString().trim()
                return fn.indexOf(searchName) >= 0
            })
            return filterData
        }
        function searchTime(data, searchTime) {
            if (searchTime === "" || searchTime === undefined || searchTime === null) {
                return data
            }
            const filterData = data.filter(function (item, index, arr) {
                // console.log(item)
                const fn = moment(item.transintime.toString().trim(), "YYYYMMDD").valueOf()
                const st = moment(searchTime[0].toString().trim(), "YYYYMMDD").valueOf()
                const et = moment(searchTime[1].toString().trim(), "YYYYMMDD").valueOf()
                return st <= fn && fn <= et
            })
            return filterData
        }

        function searchNO(data, searchNO) {
            if (searchNO === "" || searchNO === undefined || searchNO === null) {
                return data
            }
            const filterData = data.filter(function (item, index, arr) {
                console.log(item)
                const HID = item.hisid.toString().trim()
                return HID.match(searchNO)
            })
            return filterData
        }
        const rowData = this.state.allPatientDisplayData
        const d = search(rowData, this.state.name)
        const f = searchTime(d, this.state.transOuttime)
        const H = searchNO(f, this.state.hisid)
        const data = H.map((item, index) => {
            return {
                key: index,
                id: item.hisid,
                name: item.pnamec,
                gender: this.genderswitch(item.psex),
                weight: "--",
                birweek: "--",
                transage: "--",
                hospitalnumber: item.caseid,
                hospitalindex: item.transinid,
                transicutime: item.transouttime,
                maindoc: "--",
                clickstate: this.EditStyle(item.hisid, item.caseid, item.transinid, index)
            }
        });

        const { uploading, fileList } = this.state;
        const props = {
            onRemove: file => {
                this.setState(state => {
                    const index = state.fileList.indexOf(file);
                    const newFileList = state.fileList.slice();
                    newFileList.splice(index, 1);
                    return {
                        fileList: newFileList,
                    };
                });
            },
            beforeUpload: file => {
                this.setState(state => ({
                    fileList: [...state.fileList, file],
                }));
                return false;
            },
            fileList,
            showUploadList: false
        };
        const handleDisplaySwitchChange = this.handleDisplaySwitchChange.bind(this);

        return (
            <div style={{ paddingLeft: "24px", paddingRight: "24px" }}>
                <div style={{ fontSize: "22px", height: '50px', lineHeight: "50px" }}>
                    歷史病歷管理
                    <Button type="primary" value="in" style={{ borderRadius: "50px", fontSize: "10px", marginLeft: "10px" }} onClick={handleDisplaySwitchChange}>住院中</Button>
                    <Button type="primary" value="out" style={{ borderRadius: "50px", fontSize: "10px", marginLeft: "10px" }} onClick={handleDisplaySwitchChange}>已轉出</Button>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "3fr 1fr", height: '50px', gridColumnGap: "10px" }}>
                    {this.Searchbar()}
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gridGap: "10px" }}>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Upload {...props}>
                                <Button>
                                    <Icon type="upload" /> 選擇檔案
                                </Button>
                            </Upload>
                            <Button
                                onClick={this.handleUpload.bind(this)}
                                disabled={fileList.length === 0}
                                style={{ width: "100%" }}
                            >
                                匯入
                            </Button>
                        </div>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Button onClick={this.exportClick.bind(this)} style={{ width: "100%" }}>匯出</Button>
                        </div>
                        <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                            <Button onClick={this.deleteHistoryUser.bind(this)} style={{ width: "100%" }}>刪除</Button>
                        </div>
                    </div>
                </div>
                <div>
                    <Table rowSelection={rowSelection} columns={columns} dataSource={data} onRow={record => {
                        return {
                            onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                            onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                        };
                    }} />
                </div>
            </div>
        );
    }
}

export default Historyview;
