import React, { Component } from 'react';
import { DatePicker, Button, Select } from 'antd';
import filterlogo from '../../Image/svg/filter.svg'
import Inoutonchronic from './Chronic_io'
import Chroniclab from './ChronicLab'
import Chronicrt from './ChronicRT'
import Orderonchronic from './ChronicOrder'
import Weightonchronic from './ChronicWeight'
import Vitalxaxis from '../../Module/Vitalxaxis'
import Vitalxaxisbot from '../../Module/Vitalxaxisbot'
import moment from 'moment'
import './Chroniclelayout.scss'
import Vitalonchronic from './Chronic_vital'

class Chroniclelayout extends Component {
    state = {
        display_Mode: false,
        item: this.props.item_array,
        scrollstate: 0,
        PickerDate: moment(moment().format('YYYYMMDD'), "YYYYMMDD").valueOf(), //選擇日期
        select_interval: 0, //選擇區間
        statuschange: 0,
    }

    scrollToAnchor = (anchorName) => {
        console.log(anchorName)
        if (anchorName) {
            let anchorElement = document.getElementById(anchorName);
            if (anchorElement) { anchorElement.scrollIntoView(false); }
        }
    }
    scroll_btn_style(index) {
        console.log(index, this.state.scrollstate)
        if (index === this.state.scrollstate) {
            return true
        }
        else return false
    }
    onChange = (date, dateString) => {
        console.log(date, dateString);

        this.setState(
            {
                PickerDate: date.valueOf()
            }
        )
    }
    onSelectchange = (value) => {
        this.setState(
            {
                select_interval: value
            }
        )
    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const { PickerDate } = this.state
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = bTime - (6 * day)
                bronDate <= bTemp && this.onChange(moment(bTime))
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.onChange(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.onChange(moment(aTime))
                break;
            default:
                break;
        }
    }

    render() {
        const { Option } = Select;
        // const dateFormat = 'YYYY/MM/DD';
        const { caseinfo } = this.props
        const crosssectioncontrollist =
            [{ "name": "生命徵象", "scrollpoint": "vital_sign_chronic" },
            { "name": "輸出入", "scrollpoint": "input_output_chronic" },
            { "name": "檢驗結果", "scrollpoint": "lab_chronic" },
            { "name": "呼吸評估", "scrollpoint": "rt_chronic" },
            { "name": "醫藥囑", "scrollpoint": "order_chronic" },
            { "name": "體重", "scrollpoint": "infection_chronic" }]
        console.log(caseinfo.PBASINGNINFO.PBIRTHDT.toString())
        let button = crosssectioncontrollist.map(
            (item, index) => <Button onClick={this.scrollToAnchor.bind(this, item.scrollpoint)} key={index} style={{ borderRadius: "16px", color: "rgba(245, 166, 35, 1)", border: "rgba(245, 166, 35, 1) 1px solid" }}>{item.name}</Button>);
        return (
            <div style={{ paddingLeft: "20px", paddingRight: "20px" }}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", height: "40px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "140px 120px auto", gridColumnGap: "5px" }}>
                        <DatePicker disabledDate={(current) => { return current > moment().startOf("day") || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }} onChange={this.onChange} value={moment(this.state.PickerDate)} allowClear={false} />
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px", marginLeft: "10px" }}>
                            <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                            <Button value="N" shape="circle" icon="pause-circle" onClick={this.trainDay} />
                            <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                        </div>
                        <Select defaultValue={this.state.select_interval} style={{ width: 120, marginLeft: "10px" }} onChange={this.onSelectchange}>
                            <Option value={0}>一天</Option>
                            <Option value={1}>三天</Option>
                            <Option value={2}>一週</Option>
                            <Option value={3}>兩週</Option>
                        </Select>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(8,auto)", gridColumnGap: "15px" }}>
                        <t2 style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>項目切換:</t2>
                        {button}
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={filterlogo} alt='filterlogo'></img>
                        </div>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px", margin: "10px 0px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_chronic" svg_id="xaxis_svg_chronic"></Vitalxaxis>
                </div>
                <div className='boxshadow'></div>
                <div className='chroniclayout'>
                    {/* Vital sign */}
                    <div id="vital_sign_chronic">
                        <Vitalonchronic source={"Chronic"} PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="Vital"></Vitalonchronic>
                    </div>
                    {/* I/O */}
                    <div id="input_output_chronic">
                        <Inoutonchronic PickerDate={this.state.PickerDate} Status={"Chronic"} IntervalStatus={this.state.select_interval} caseinfo={caseinfo} title="輸出入"></Inoutonchronic>
                    </div>
                    {/* Lab */}
                    <div id="lab_chronic">
                        <Chroniclab PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="檢驗結果"></Chroniclab>
                    </div>
                    {/* RT */}
                    <div id="rt_chronic">
                        <Chronicrt PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo}></Chronicrt>
                    </div>
                    {/* Order */}
                    <div id="order_chronic">
                        <Orderonchronic PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title=""></Orderonchronic>
                    </div>
                    {/* 體重 */}
                    <div id="infection_chronic">
                        <Weightonchronic PickerDate={this.state.PickerDate} QTWPER={this.state.QTWPER} select_interval={this.state.select_interval} caseinfo={caseinfo} title=""></Weightonchronic>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px", marginTop: "30px" }}>
                    <div></div>
                    <Vitalxaxisbot max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_bot_chronic" svg_id="xaxis_bot_svg_chronic"></Vitalxaxisbot>
                </div>
            </div>
        );
    }
}

export default Chroniclelayout;
