import React, { Component } from 'react'
import * as ApiProvider from '../../API/ApiProvider'
import { Spin, Icon } from 'antd';
import moment from 'moment'

class Chronictablecelltooltip extends Component {
    state = {
        preStartTimeStamp: null,
        loading: true,
        resultStr: "--"
    }
    componentDidUpdate() {
        const { startTimeStamp } = this.props
        const { preStartTimeStamp } = this.state
        if (startTimeStamp !== preStartTimeStamp) {
            console.log("Chronictablecelltooltip componentDidUpdate")
        }
    }
    componentDidMount() {
        const { startTimeStamp } = this.props
        this.setState({ preStartTimeStamp: startTimeStamp })
        console.log("Chronictablecelltooltip componentDidMount")
        this.apiGet()
    }
    apiGet() {
        const { tooltipelement, caseinfo } = this.props
        const ORMM = tooltipelement.ORMM.toString().trim()
        const ORDSEQNO = tooltipelement.ORDSEQNO.toString().trim()
        //RESDISP
        ApiProvider.getStoredProcedureRESDISP(caseinfo.HISID, ORMM, caseinfo.CASEID, ORDSEQNO)
        .then(result => {
            console.log("getStoredProcedureRESDISP OK " + JSON.stringify(result.data))
            //const fakeData = [{"RECNO":"1","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"正式報告","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"2","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"                                臺北榮民總醫院病理檢驗部報告","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"3","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  病患資訊：         新生兒加護病房 - 8 張恩慈之男 4599819-4 男性 3天大 NBD","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"4","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  申請序號 / 工作號：     0APKHTG/2502091284       開立時間：    20200207  0809","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"5","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  開醫囑者：         DOC3915L 李苡萱         簽收時間：    20200209  1850","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"6","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  報告人 ：          LABE1523 林宜慧         報告時間：    20200209  1850","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"7","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  檢體 ：           Blood","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"8","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"  醫囑名稱：         Crea,ALT,Mg,NA,K,P,","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"9","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"   項目     H/L  結果   前次結果 單位     參考值","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"10","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"   BUN            ()","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"11","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"   Na        145  ()   mmol/L   136~145 mmol/L","ORDSEQ":"41","CASENO":"25663876"},{"RECNO":"12","PARTNO":"02 ","HISTNO":"45998194  ","ORBGNDT":"2020-02-10","CONTENT":"   K         4.0   ()   mmol/L   3.5~5.1 mmol/L","ORDSEQ":"41","CASENO":"25663876"}]

            //parse content
            const parsers = this.props.summaryParse
            const parser  = result.data.length > 0 ? this.select_parser("Chronic table", result.data[0], parsers) : undefined
            
            if(parser !== undefined) {
                //concat all content
                let allContent = ""
                //fakeData.forEach(item => {
                result.data.forEach(item => {
                    allContent = `${allContent} "\n" ${item.CONTENT}`;
                });
                //console.log("allContent" + JSON.stringify(allContent))

                const result = this.parse(allContent, parser)
                //console.log("result" + JSON.stringify(result))

                const resultStr = this.summerize(result, parser)
                //console.log("resultStr" + JSON.stringify(resultStr))

                this.setState({resultStr: resultStr})
            }

            this.setState({loading: false})
        })
        .catch(err => {
            console.log("getStoredProcedureRESDISP NG " + JSON.stringify(err))
            this.setState({loading: false})
        })
    }
    select_parser = (parserName, input, parsers) => {
        const inputBeginTime = moment(input.ORBGNDT, "YYYY-MM-DD").valueOf()
        return parsers.find(x =>
            parserName.match(x.parser_name)
            && (!x.apply_condition.start_time || inputBeginTime >= x.apply_condition.start_time)
        )
    }
    parse = (input, parser) => {
        let mappings = parser.mappings
        let result = {}
        mappings.forEach(mapping => {
            const re = new RegExp(mapping.regex_str, mapping.regex_flags)
            const match = input.match(re)
            result[mapping.key] = match && match[mapping.position]
        });
        return result;
    }
    summerize = (kv_pairs, parser) => {
        let summary_str = parser.summary
        for (const key in kv_pairs) {
            let match_replacer = summary_str.match(RegExp("\\[" + key + "(:(.*?))?\\]"))
            let replacer = ""
            if (match_replacer && match_replacer.length === 3) {
                replacer = match_replacer[2]
            }
            const match = summary_str.match(RegExp("\\[" + key + ":(.*?)?\\]"))
            summary_str = summary_str.replace(match[0], match[0].replace(match[1], kv_pairs[key] || replacer))
        }
        return summary_str;
    }
    render() {
        const { tooltipelement } = this.props
        const { loading, resultStr } = this.state
        const infoTime = tooltipelement.ORRCPTM.toString().trim()
        const name = tooltipelement.ORPROCED.toString().trim()
        const reportname = tooltipelement.ORPROCED.toString().trim() + tooltipelement.ORSPCN1.toString().trim()
        const cellRenderData = [reportname, infoTime, resultStr]
        const Cell = cellRenderData.map(function (value, index) {
            if(loading) {
                const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
                return (
                    <Spin key={index} indicator={antIcon} />
                );
            } else {
                return (
                    <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                        {value}
                    </div>
                );
            }
            
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "300px 100px 200px" }}>{Cell}</div>
        );
    }
}

export default Chronictablecelltooltip