import React, { Component } from 'react';
import RTbreathetablecell from './RTbreathtablecell'
import * as FilterReg from '../../Function/FiliterReg'
import moment from 'moment'

class RTbreathetable extends Component {

    createRTbreathetable = () => {
        const { DataList, caseinfo } = this.props

        const RTbreathetable = DataList.map(
            (value, index, array) =>
                <RTbreathetablecell key={index} value={value} caseinfo={caseinfo}></RTbreathetablecell>
        )
        return RTbreathetable
    }

    render() {
        return (
            <div>
                <div style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 3fr 3fr", backgroundColor: "#E2EDF3", border: "rgba(238,238,238,1) 1px solid" }}>
                    <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>紀錄日期</t3>
                    <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>Mode</t3>
                    <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>Setting</t3>
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {this.createRTbreathetable()}
                </div>
            </div>
        );
    }
}

export default RTbreathetable
