import React, { Component } from 'react'
import { DatePicker, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Nurtiontpncell from './Nurtion_TPN_cell'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'

class Nurtiontpnorder extends Component {
    state = {
        onEdit: false,
        tpndata: [],
        UDORDER0: [],
        tpnParsers: []
    }
    componentDidMount() {
        const getAPITPNData = this.getAPITPNData.bind(this)
        ApiGithub.getGitHubJson("TpnOrder.json")
            .then(data => {
                let parser = data.data
                console.log("getGitHubJson TPN Order OK " + JSON.stringify(parser))
                this.setState({
                    tpnParsers: parser
                }, () => {
                    getAPITPNData()
                })
            })
            .catch(err => {
                console.log(err)
            })

    }
    getAPITPNData() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()
        const { tpnParsers } = this.state
        // console.log(HID, CID)
        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                //const fakeData = [{"UDDCPRSN":"DOC3941E","UDDURAT":"007","UDSUMQTY":"55","UDDRGNAM":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDGNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-14-09.17.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"55      ","UDORDSEQ":"0043","UDBGNDT":"2019-08-13-08.50.00.000000","UDDRGNO":"02145","UDCOMMET":"Y","UDVALUE":"                                                                                                    ","UDDUNIT":"CC    ","UDROUTE":"IVD","UDRPNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDFREQN":"QD-TPN  "},{"UDDCPRSN":"DOC3964C","UDDURAT":"014","UDSUMQTY":"7","UDDRGNAM":"Peditrace \"TPN\" sol for inj 10ml","UDGNAME":"Peditrace \"TPN\" sol for inj 10ml","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-20-10.22.00.000000","UDDCPNAM":"李成彧　　","UDDOSAGE":"1       ","UDORDSEQ":"0044","UDBGNDT":"2019-08-13-08.50.00.000000","UDDRGNO":"05208","UDCOMMET":"Y","UDVALUE":"                                                                                                    ","UDDUNIT":"AMP   ","UDROUTE":"MSC","UDRPNAME":"Peditrace \"TPN\" sol for inj 10ml","UDFREQN":"QD-TPN  "},{"UDDCPRSN":"        ","UDDURAT":"001","UDSUMQTY":"1","UDDRGNAM":"Immunoglobulin *inj 60mg/ml 50ml","UDGNAME":"Immunoglobulin *inj 60mg/ml 50ml","UDORSIGN":"DOC3941E","UDSELF":"Y","UDSTATUS":"60","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-14-15.00.00.000000","UDDCPNAM":"　　　　　","UDDOSAGE":"1       ","UDORDSEQ":"0045","UDBGNDT":"2019-08-13-15.17.00.000000","UDDRGNO":"03852","UDCOMMET":"Y","UDVALUE":" ","UDDUNIT":"BOT   ","UDROUTE":"IVA","UDRPNAME":"HM.imm.globulin *inj60mg/ml 50ml","UDFREQN":"ST      "},{"UDDCPRSN":"DOC3941E","UDDURAT":"007","UDSUMQTY":"110","UDDRGNAM":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDGNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDORSIGN":"DOC3941E","UDSELF":"N","UDSTATUS":"80","UDPERNAM":"何正尹　　","UDENDDT":"2019-08-16-10.44.00.000000","UDDCPNAM":"何正尹　　","UDDOSAGE":"55      ","UDORDSEQ":"0055","UDBGNDT":"2019-08-14-09.17.00.000000","UDDRGNO":"02145","UDCOMMET":"Y","UDVALUE":"                                                                                                    ","UDDUNIT":"CC    ","UDROUTE":"IVD","UDRPNAME":"TPN P-1 # 1 ml VGH,(100 ml/bot) ","UDFREQN":"QD-TPN  "}]

                //create list need to do search
                let tpnList = [] // All TPN data
                let selected_parser = undefined
                //fakeData.forEach(item => {
                data.data.forEach(item => {
                    //get parser
                    selected_parser = this.select_parser(item, tpnParsers)
                    if (selected_parser !== undefined) {
                        const found = item.UDDRGNAM.match(RegExp(selected_parser.apply_condition.name, selected_parser.apply_condition.name_regex_flags))
                        if (found !== null && found !== "") tpnList.push(item)
                    }
                });

                console.log("tpnList" + JSON.stringify(tpnList))
                if (selected_parser !== undefined) {
                    //parser 7 value from each TPN document （AA, Glu, Na, K, Ca, P, Mg）
                    tpnList.forEach((item, index) => {
                        ApiProvider.getStoredProcedureUDTEXTQ1(CID, item.UDORDSEQ)
                            .then(data => {
                                console.log("getStoredProcedureUDTEXTQ1 OK " + JSON.stringify(data.data))

                                //parse every line
                                const result = this.parse(data.data, selected_parser)
                                item.TPNDATA = result;
                                //console.log("parse result: " + JSON.stringify(item))
                                tpnList.splice(index, 1, item)
                                this.setState({
                                    tpndata: tpnList
                                })
                            })
                            .catch(err => {
                                console.log("getStoredProcedureUDTEXTQ1 NG " + err)
                            });
                    });
                }
            })
            .catch(err => {
                console.log("getStoredProcedureUDORDER0 NG " + err)
            });
    }
    select_parser = (input, parsers) => {
        const inputBeginTime = moment(input.UDBGNDT, "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
        const inputEndTime = moment(input.UDENDDT, "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
        return parsers.find(x =>
            input.UDDRGNAM.match(x.apply_condition.name)
            && (!x.apply_condition.start_time || inputBeginTime >= x.apply_condition.start_time)
            && (!x.apply_condition.end_time || inputEndTime < x.apply_condition.end_time)
        )
    }
    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = ""
        });

        inputs.forEach(input => {
            let content = input.UDWORD;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key] = match[mapping.position]
                }
            });
        });
        return allResult;
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    render() {
        const { caseinfo, PickerDateRange } = this.props
        const title = ["開始時間", "結束時間", "狀態", "處置名稱"]
        const titilecell = title.map(function (info, index) {
            return (
                <div key={index} style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                    <t4>{info}</t4>
                </div>
            );
        })
        const ListCellData = this.state.tpndata
        const filterData = ListCellData.filter((element, index, arr) => {
            const time = moment(moment(element.UDBGNDT, 'YYYY-MM-DD-HH.mm.ss.000000').format("YYYYMMDD"), "YYYYMMDD").valueOf()
            return PickerDateRange[0] <= time && time <= PickerDateRange[1]
        })
        const DataCellRender = filterData.map(function (element, index, arr) {
            return (
                <Nurtiontpncell key={index} data={element} caseinfo={caseinfo} ></Nurtiontpncell>
            );
        })

        return (
            <div>
                <t1>TPN order</t1>
                <div style={{ display: "grid", gridTemplateColumns: " 1fr 1fr 60px 450px", background: "#cccccc", height: "50px", marginTop: "10px" }}>
                    {titilecell}
                </div>
                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                    {DataCellRender}
                </div>
            </div>
        );
    }
}

export default Nurtiontpnorder
