import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import wardviewlogo from '../Image/svg/Wardview.svg'
import historylogo from '../Image/svg/Historyview.svg'
import controllogo from '../Image/svg/Control.svg'
import './Sidermenu.scss';


class Sidermenu extends Component {

    state = {
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = Number(sessionStorage.getItem('role'))
        this.setState({ powerLogin: powerLogins })
    }

    Selectkeynumber(key) {

        switch (key) {
            case '/nicu':
                return 0;
            case '/nicu/history':
                return 1;
            case '/nicu/system':
                return 2;
            default:
                if (key.match('patient') != null) {
                    return 0;
                }
                if (key.match('ward') != null) {
                    return 0;
                }
                return '';
        }
    }

    select_hover(value) {
        const { path } = this.props
        const keynumber = this.Selectkeynumber(path)
        if (keynumber === value) {
            return true
        }
        return false
    }
    render() {
        // const linkstate = {
        //     backgroundColor: "rgba(59, 151, 225, 1)",
        //     display: 'flex',
        //     paddingLeft: '15px',
        //     paddingRight: '15px',
        //     height: "40px"
        // }
        // const dislinkstate = {
        //     backgroundColor: "rgba(0, 0, 0, 0)",
        //     display: 'flex',
        //     paddingLeft: '15px',
        //     paddingRight: '15px',
        //     height: "40px"
        // }
        // const linkfont = { color: "rgba(25, 70,90, 1)", fontweight: "500px", fontsize: "18px", margin: "0px 10px 0px 2px" }
        // const dislinkfont = { color: "rgba(46, 100, 164, 1)", fontweight: "500px", fontsize: "18px", margin: "0px 10px 0px 2px" }
        const rule = this.state.powerLogin
        // const rule = 5
        console.log(Number(sessionStorage.getItem('role')))
        //sso custom => custom
        return (
            <div style={{ display: 'flex', alignItems: 'center', marginLeft: "20px", height: '100%' }}>
                <div style={{ display: "grid", gridTemplateColumns: "auto auto auto", gridColumnGap: "10px" }}>
                    <Link to="/nicu/ward/wardindex" class={this.select_hover(0) ? "linkstate" : "dislinkstate"} style={{ display: rule === 5 ? "none" : "flex" }}>
                        <img src={wardviewlogo} alt='wardviewlogo'></img>
                        <centerstyle>
                            <t2 class={this.select_hover(0) ? "linkfont" : "dislinkfont"}>住院病人資訊</t2>
                        </centerstyle>
                    </Link>
                    <Link to="/nicu/history" class={this.select_hover(1) ? "linkstate" : "dislinkstate"} style={{ display: rule >= 2 && rule < 5 ? "flex" : "none" }}>
                        <img src={historylogo} alt='historyviewlogo'></img>
                        <centerstyle>
                            <t2 class={this.select_hover(1) ? "linkfont" : "dislinkfont"} >歷史病歷管理</t2>
                        </centerstyle>
                    </Link>
                    <Link to='/nicu/system' class={this.select_hover(2) ? "linkstate" : "dislinkstate"} style={{ display: rule === 5 || rule === 4 ? "flex" : "none" }}>
                        <img src={controllogo} alt='systemviewlogo'></img>
                        <centerstyle>
                            <t2 class={this.select_hover(2) ? "linkfont" : "dislinkfont"}>系統設定</t2>
                        </centerstyle>
                    </Link>
                </div>
            </div>
        );
    }
}

export default Sidermenu
