import X2JS from "x2js"
import moment from 'moment';


export function CalculateAge(BornStatus) {
    const minus = Math.floor((moment().valueOf() - BornStatus.date) / (24 * 60 * 60 * 1000)) //多少天
    if (BornStatus.week < 37) {
        console.log(minus)
        console.log(BornStatus.week, BornStatus.day)
        console.log(Math.floor(BornStatus.week + (BornStatus.day + minus) / 7))
        const week = Math.floor(BornStatus.week + (BornStatus.day + minus) / 7);
        const day = (BornStatus.day + minus);

        return week < 40 ? `${week}+${day}` : day > 30 ? `${Math.floor(day / 30)}月${Math.floor(day % 30)}日` : `矯齡${day}天大`
    }
    const month = Math.floor((BornStatus.day + minus) / 30);
    const day = (BornStatus.day + minus - 2) % 30;
    return month > 0 ? `${month}+${day}` : `${day}天大`
}


export function statecolor(state) {
    if (state === 'HR') {
        return "rgb(100,140,60)"
    }
    if (state === 'BP') {
        return "rgb(193,37,40) "
    }

    if (state === 'ABP') {
        return "#C12528"
    }
    if (state === 'NBP') {
        return "#C12528"
    }
    if (state === 'SpO2') {
        return "rgb(60,105,160) "
    }
    if (state === 'BT') {
        return "rgb(127,61,181)"
    }
    if (state === 'RR') {
        return "rgb(200,110,40) "
    }
    return null
}

//parse flag
export function Respiratorswitch(Respirator) {
    if (Respirator === '呼(非侵入)') {
        return '呼(非侵入)'
    }
    if (Respirator === '呼(侵入)') {
        return '呼吸'
    }
    return '呼吸氣'
}

export function wardstatestringformat(item) {
    //console.log(item);
    switch (item.item) {
        case "Respirator":
            return Respiratorswitch(item.data)
        case "TPN":
            return "TPN"
        case "NPO":
            return "NPO"
        case "HD":
            return "HD"
        case "Abx":
            return "Abx"
        case "CVC":
            return "CVC"
        case "Xanth":
            return "Xanth"
        case "照光":
            return "照光"
        case "DRN":
            return "DRN"
        default:
            return item
    }
}

