export function simulationCentermonitordata(time, timeInterval) {
    var dataarray = [];
    for (var k = 0; k < (timeInterval * 6); k++) {
        var dataset = []; //建立空的資料陣列
        for (var i = 0; i < timeInterval; i++) {
            var newNum = Math.floor(Math.random() * 50) + 50;
            dataset.push(newNum);
        }
        dataarray.push({
            'data': dataset,
            'time': time[k]
        });
    }
    //console.log(dataarray);
    return dataarray;
}

