import React, { useState, useEffect } from "react";
import './Wardinfouser.scss'
import moment from 'moment'
import { Modal } from 'antd';
import * as Wardstate from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
import * as ApiGithub from '../API/ApiGithub'
import { pregancyweeksanddays, gender } from '../Function/Wardbedfunction'
import fileLogo from '../Image/svg/file1.svg'
import file2Logo from '../Image/svg/file2.svg'
import Wardgrewchart from './Wardgrewchart'
import Notice from '../Patientview/Schedule/Notice'
import Labviewlayout from '../Patientview/CrossSectionView/Labview'
import WardChart from './Wardchart'
import Wardinfousercheckbox from './Wardunfousercheckbox'
import { Link } from "react-router-dom";
import * as Reg from '../Data/RegularData';
import Patientinfo from '../Patientview/Patientviewinfo';
import Wardcarstatus from './Wardcardstatus'


export function Wardinfouser(props) {
    const [Visible, setVisible] = useState(false);
    const [Report, SetReport] = useState(0);
    const [Nonreport, SetNonreport] = useState(0);
    const [RESLAB01, SetRESLAB01] = useState([]);
    const [RESLAB02, SetRESLAB02] = useState([]);
    const [RESDBGAS, SetRESDBGAS] = useState([]);
    const [RESLAB0C, SetRESLAB0C] = useState([]);
    const [RESDGLU1, SetRESDGLU1] = useState([]);
    const [RESSECT, SetRESSECT] = useState([]);
    const [CenterMonitorValue, SetCenterMonitorValue] = useState([])
    const [scheduleList, SetScheduleList] = useState([]);
    const [todoList, SetTodoList] = useState([]);
    const [QENT, SetQENT] = useState([]);
    const [CID, SetCID] = useState(null);
    const [HID, SetHID] = useState(null);
    const [TransInDt, SetTransInDt] = useState(null);
    const [Timer, SetTimer] = useState(0)

    // useEffect(() => {
    //     const caseinfo = props.location.state.data
    //     const CASEID = caseinfo.PCASENO.toString()
    //     const transindt = caseinfo.TRANSINDT.toString().trim()
    //     const HISID = caseinfo.PHISTNUM.toString().trim()
    //     console.log(CASEID, transindt)
    //     SetCID(CASEID);
    //     SetTransInDt(transindt);
    //     SetHID(HISID)
    // })

    useEffect(() => {
        const TimerID = setInterval(() => {
            SetTimer(Timer + 1)
        }, 5000)
        return () => clearInterval(TimerID);
    }, [Timer])

    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString().trim()
        const time = moment(TransInDt, 'YYYYMMDD').format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESLAB0C(hisid, time)
            .then(data => { SetRESLAB0C(data.data) })
    }, [props.location.state.data.PHISTNUM, props.location.state.data.TRANSINDT])

    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString().trim()
        const time = moment(data.TRANSINDT.toString().trim(), 'YYYYMMDD').format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESLAB01(hisid, time)
            .then(data => { SetRESLAB01(data.data) })
    }, [props.location.state.data.PHISTNUM, props.location.state.data.TRANSINDT])

    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString().trim()
        // const caseid = data.PCASENO.toString().trim()
        const time = moment(data.TRANSINDT.toString().trim(), 'YYYYMMDD').format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESLAB02(hisid, time)
            .then(data => { SetRESLAB02(data.data) })
    }, [props.location.state.data.PHISTNUM, props.location.state.data.TRANSINDT])



    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString().trim()
        // const caseid = data.PCASENO.toString().trim()
        const time = moment(data.TRANSINDT.toString().trim(), 'YYYYMMDD').format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESDBGAS(hisid, time)
            .then(data => {
                SetRESDBGAS(data.data)
            })
    }, [props.location.state.data.PHISTNUM, props.location.state.data.TRANSINDT])





    useEffect(() => {
        const data = props.location.state.data
        const caseid = data.PCASENO.toString()
        ApiProvider.getQENT(caseid)
            .then(data => {
                console.log(data.data)
                const parsedata = Wardstate.XM2JSON(data.data)
                SetQENT(parsedata)
            })
            .catch(err => {
                console.log("Andy test getQENT NG " + JSON.stringify(err));
            });
    }, [props.location.state.data.PCASENO]);

    useEffect(() => {
        const data = props.location.state.data
        // const hisid = "TEST1234"
        const hisid = data.PHISTNUM.toString()
        ApiProvider.getHisidwardvalue(hisid)
            .then(data => {
                console.log(data.data)

                SetCenterMonitorValue(data.data[0]);
            })
            .catch(err => {
                console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
            });
    }, [Timer, props.location.state.data.PHISTNUM]);


    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString()
        const beforeOneDay = new Date().getTime() - (24 * 60 * 60 * 1000)

        ApiProvider.getStoredProcedureRESSECT(hisid, 1, "ALL", "DOC3924B")
            .then(data => {
                const ReportList = data.data
                const mapList = ReportList.filter(function (value, index, array) {
                    const DTstring = value.ORRCPDT.toString()
                    const TMstring = value.ORRCPTM.toString()
                    const time = DTstring + TMstring
                    return moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf() > beforeOneDay
                })
                SetReport(mapList.length)
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESSECT NG " + JSON.stringify(err));
            });
    }, [props.location.state.data.PHISTNUM]);

    useEffect(() => {
        const data = props.location.state.data
        const hisid = data.PHISTNUM.toString()
        const TRANSINDT = data.TRANSINDT

        ApiProvider.getStoredProcedureORDLIST(hisid, moment(TRANSINDT, "YYYYMMDD").format('YYYY-MM-DD'), "ALL", "DOC3924B")
            .then(data => {
                const ORDERLIST = data.data
                const ORDERLISTMAP = ORDERLIST.filter(function (element, index, arr) {
                    return element.ORSTATUS === "31"
                })
                SetNonreport(ORDERLISTMAP.length)
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err));
            });
    }, [props.location.state.data.PHISTNUM, props.location.state.data.TRANSINDT]);




    // useEffect(() => {
    //     console.log("query schedule list")

    //     const data = props.location.state.data
    //     const source = data
    //     const PLOC = source.PLOC
    //     const TRANSINID = source.TRANSINID.toString()

    //     const now = new Date();
    //     const todayBegin = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    //     const startTime = todayBegin.toISOString().split(".")[0];
    //     const endTime = new Date(todayBegin.getTime() + 24 * 60 * 60 * 1000).toISOString().split(".")[0];
    //     ApiProvider.getOnePatientSchedule(TRANSINID, startTime, endTime)
    //         .then(result => {
    //             console.log("getOnePatientSchedule OK " + JSON.stringify(result.data));
    //             SetScheduleList(result.data);
    //         })
    //         .catch(err => {
    //             console.log("getOnePatientSchedule NG " + JSON.stringify(err));
    //         });
    // }, [props.location.state.data.TRANSINID]);

    // useEffect(() => {
    //     console.log("query todo list")

    //     const data = props.location.state.data
    //     const source = data
    //     const PLOC = source.PLOC
    //     const TRANSINID = source.TRANSINID.toString()


    //     ApiProvider.getOneTodo(TRANSINID, source.PCASENO.toString())
    //         .then(result => {
    //             console.log("getOneTodo OK " + JSON.stringify(result.data));
    //             SetTodoList(result.data);
    //         })
    //         .catch(err => {
    //             console.log("getOneTodo NG " + JSON.stringify(err));
    //         });
    // }, [CASEIDSTATUS]);

    function renderScheduleItem() {
        if (scheduleList.length === 0) {
            return (
                <div>沒有排程</div>
            )
        } else {
            return scheduleList.map((item, index) => {
                if (item.type === "排程") {
                    const rawDateStr = item.time;
                    const time = moment(item.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss')
                    const localTime = moment.utc(time).toDate()
                    const displayTimeStr = moment(localTime).format("MM/DD HH:mm")
                    return (
                        <div key={index} style={{ height: "50px", display: 'grid', gridTemplateColumns: "35% 65%", borderBottomWidth: "1px", borderBottomColor: "rgba(232, 232, 232, 1)", borderBottomStyle: "solid" }}>
                            <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>{displayTimeStr}</div>
                            <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>{item.context}</div>
                        </div>
                    )
                }
            })
        }
    }
    function renderTodoItem() {
        if (todoList.length === 0) {
            return (
                <div>沒有待辦事項</div>
            )
        } else {
            return todoList.map((item, index) => {
                if (item.type === "未完成") {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={false}></Wardinfousercheckbox>)
                } else if (item.type === "已完成") {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={true}></Wardinfousercheckbox>)
                }

            });
        }
    }





    function valueFormat(value) {
        if (value === "" || value === null || value === undefined) {
            return "--"
        }
        return value.toString()
    }
    function valueBornWeight(value) {
        if (value === "" || value === null || value === undefined || value === 0) {
            return ""
        }
        return value.toString() + "g → "
    }

    function fixpatientDate(data) {
        const transdata = {
            'cause': '住院原因',
            'car_pho': '主要照顧者電話',
            'care': '主要照顧者',
            'con_1': '聯絡人1',
            'con_p11': '聯絡人1電話',
            'con_2': '聯絡人2',
            'con_p21': '聯絡人2電話',
            'dvs': '住院時生命徵象',
            'habit': '飲食狀況',
            'oxy': '住院時氧和評估',
            'h_val': '住院時頭圍',
            'b_val': '住院時胸圍',
            'tall': '住院時身高',
            'weight': '住院時體重'
        }

        const patientDate = []

        for (let index in transdata) {
            if (data[index] !== "") {
                patientDate.push([transdata[index], data[index]])
            }
        }

        return patientDate
    }

    const data = props.location.state.data
    const source = data
    const Birthday = source.PBIRHDT
    const QTWER = Wardstate.XM2JSON(source.QTWPER)
    const QMNC = Wardstate.XM2JSON(source.QMNC)
    const ListWeight = Wardstate.WeightList(QTWER)
    const BornWeight = Wardstate.BornWeight(QTWER, Birthday)
    const Weight = Wardstate.NewestWeight(QTWER)
    const BornWeeks = QMNC.weeks
    const BornDays = QMNC.days
    const TRANSINID = source.TRANSINID
    const DOCLIST = source.PDOCNEW
    const MainDoc = Wardstate.Doc(DOCLIST, "B")
    const LiveDoc = Wardstate.Doc(DOCLIST, "D")
    const MainNur = Wardstate.Doc(DOCLIST, "N")
    const ICUMainDoc = Wardstate.Doc(DOCLIST, "U")

    // const PBASINFO = data.PBASINFO
    const caseinfo = {
        "HISID": source.PHISTNUM.toString(),
        "CASEID": source.PCASENO.toString(),
        "WEEK_BORN": BornWeeks,
        "DAY_BORN": BornDays,
        "TRANSINID": TRANSINID,
        "TRANSINDT": TRANSINID,
        "WEIGHT": QTWER,
        "PBASINGNINFO": source
    }

    function RenderInfo() {
        if (QENT.length === 0) {
            return null
        }
        return (
            <t3 className='wardblacktoindex' onClick={() => setVisible(true)}>查看病人基本資料</t3>
        );
    }
    const grewinfo = {
        "WEEK_BORN": BornWeeks,
        "DAY_BORN": BornDays,
        "BIRTHDAY": Birthday
    }
    const patientDetail = fixpatientDate(QENT)
    return (
        <div className='wardinfouserlayout'>
            <Modal
                width={800}
                title="病人基本資料"
                visible={Visible}
                onOk={() => setVisible(false)}
                onCancel={() => setVisible(false)}
                footer={null}>
                <Patientinfo patientDetail={patientDetail}></Patientinfo>
            </Modal>
            <div className='wardinfopatient'>
                <div style={{ height: '50px', display: 'grid', gridTemplateColumns: "1fr 1fr 1fr" }}>
                    <Link to="/nicu/ward/wardindex" style={{ alignItems: "center", display: "flex" }}>
                        <t2 className='wardblacktoindex'>&larr;返回總覽</t2>
                    </Link>
                    <centerstyle>
                        <t1>病人資訊摘要</t1>
                    </centerstyle>
                </div>
                <div style={{ height: "50px" }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <t2>床號:{Number(source.HBEDNO)},{source.PNAMEC.toString().trim()},{gender(source.PSEX)},{moment(Birthday, "YYYYMMDD").format("YYYY/MM/DD")},{source.PHISTNUM.toString().trim()}</t2>
                        <Wardcarstatus source={false} PCASENO={source.PCASENO} PHISTNUM={source.PHISTNUM}></Wardcarstatus>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <t2 style={{ marginRight: "16px" }}>[{valueFormat(BornWeeks)}+{valueFormat(BornDays)}]&rarr;{pregancyweeksanddays(BornWeeks, BornDays, moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD'))}</t2>
                            <t4>{valueBornWeight(BornWeight)}{valueFormat(Weight)}g</t4>
                            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", width: "70px" }}>
                                <img src={fileLogo} alt='fileLogo'></img>
                                <t4>{Report}</t4>
                                <img src={file2Logo} alt='file2Logo'></img>
                                <t4>{Nonreport}</t4>
                            </div>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "repeat(5,auto)", gridColumnGap: "10px" }}>
                            <t3>主治醫師 {MainDoc}</t3>
                            <t3>加護主治醫師 {ICUMainDoc}</t3>
                            <t3>住院醫師 {LiveDoc}</t3>
                            <t3>專科護理師 {MainNur}</t3>
                            {RenderInfo()}
                        </div>
                    </div>
                </div>
                <div className='wardinfousercontent'>
                    <div style={{ padding: "10px 15px" }}>
                        <WardChart HISID={source.PHISTNUM} CMvalue={CenterMonitorValue}></WardChart>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px" }}>
                        <div style={{ borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2>檢驗項目</t2>
                            </div>
                            <Labviewlayout Source={"Wardview"} PickerDate={moment(moment().format('YYYYMMDD')).valueOf()} RESLAB01={RESLAB01} RESLAB02={RESLAB02} RESDBGAS={RESDBGAS} RESLAB0C={RESLAB0C} RESDGLU1={RESDGLU1} RESSECT={RESSECT}></Labviewlayout>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                            <Wardgrewchart grewinfo={grewinfo} Data={source} WeightList={ListWeight}></Wardgrewchart>
                        </div>
                    </div>
                    <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px", marginTop: "10px" }}>
                        {<Notice caseinfo={caseinfo} disableEdit={true}></Notice>}
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px", marginTop: "10px" }}>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2>排程</t2>
                            </div>
                            <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                {renderScheduleItem()}
                            </div>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2>待辦事項</t2>
                            </div>
                            <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                {renderTodoItem()}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
} 
