import React, { Component } from 'react';
import './Patientviewinfo.scss'
class Patientinfo extends Component {

    state = { visible: false };
    render() {
        const { patientDetail } = this.props
        // console.log(patientDetail)
        const patientData = patientDetail.map(element =>
            <div className='context'>
                <div className='title'>{element[0]} ：</div>
                <div className='item'>{element[1]}</div>
            </div>
        )

        console.log(patientData)

        return (
            <div className='patientinfolayout'>
                {patientData}
            </div>
        );
    }
}

export default Patientinfo
