import React, { Component } from 'react';
import { Table, Input, Select, Button, Modal, Radio } from 'antd';
import * as ApiProvider from '../API/ApiProvider';
const { confirm } = Modal;

class Powersetting extends Component {
    constructor(props) {
        super();

        this.addUserdata = this.addUserdata.bind(this)
        this.adduserhandleChange = this.adduserhandleChange.bind(this)
    }

    state = {
        MouseClick: 0,
        name: undefined,
        role: undefined,
        adduser: undefined,
        userList: [],
        power: ['僅可瀏覽平台、無法瀏覽管理者', '可瀏覽和編輯平台筆記、無法瀏覽管理者', '可瀏覽和編輯平台筆記、可瀏覽權限設定畫面', '可管理所有角色帳號、可瀏覽和編輯平台筆記、可瀏覽權限設定、系統摘要', '可管理所有角色帳號、可瀏覽和編輯平台筆記、可瀏覽權限設定、系統摘要、歷史畫面','僅可瀏覽權限設定、系統摘要'],
        powerName: ['唯讀使用者', '一般使用者', '一般使用者管理員', '系統設定管理員', '超級管理者','系統管理者'],
        visible: false,
        radioValue: 0,
        pwdRadio: true,
        inputUsername: '',
        inputPassword: '',
        inputName: '',
        inputDoctorid: '',
        powerLogin: 0
    }

    componentDidMount() {
        this.nameChange = this.nameChange.bind(this)
        this.queryUserdata = this.queryUserdata.bind(this)
        this.inputuserName = this.inputuserName.bind(this)
        this.inputPassword = this.inputPassword.bind(this)
        this.inputName = this.inputName.bind(this)
        this.inputDoctorid = this.inputDoctorid.bind(this)

        ApiProvider.getAlluser()
            .then(data => {
                this.setState({ userList: data.data })
            })
            .catch(err => { })

        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    radioOnChange = e => {
        this.setState({
            radioValue: e.target.value,
        }, () => {
            if (this.state.radioValue === 1) {
                this.setState({
                    pwdRadio: true,
                })
                console.log(this.state.radioValue)
            } else if (this.state.radioValue === 0) {
                this.setState({
                    pwdRadio: false,
                })
                console.log(this.state.radioValue)
            }
        })
    };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    MouseClickTable(record) {
        this.setState({
            MouseClick: record
        })
    }

    queryUserdata() {
        if (this.state.role !== undefined && this.state.name !== undefined) {
            ApiProvider.searchUser(this.state.name, this.state.role)
                .then(data => {
                    this.setState({ userList: data.data })
                })
                .catch(err => { })
        }
    }

    addUserdata() {//sso 只剩下4種角色 其他先傳空直
        ApiProvider.register(this.state.inputName, '', this.state.adduser, this.state.inputUsername, 'none')
            .then(data => {
                ApiProvider.getAlluser()
                    .then(data => {
                        this.setState({ userList: data.data, inputUsername: '', inputPassword: '', inputName: '', inputDoctorid: '' })
                    })
                    .catch(err => { })
            })
            .catch(err => { })
        this.setState({
            visible: false,
        });
    }


    showDeleteConfirm(name, account, role, roleString) {
        const set_State = this.setState.bind(this)
        confirm({
            title: '請問您確定要刪除此帳號嗎?',
            content: <div>姓名：{name}<br />角色：{roleString}<br />帳號：{account}</div>,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk() {
                ApiProvider.deleteUser(name, role, account).then(data => {
                    ApiProvider.getAlluser()
                        .then(data => {
                            set_State({ userList: data.data })
                        })
                        .catch(err => { })
                }).catch(err => { })
            },
            onCancel() {
            },
        });
    }

    EditStyle(key, name, account, role, roleString) {
        if (key === this.state.MouseClick) {
            return (
                <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gridColumnGap: '10px' }}>
                    <div>
                        <Button onClick={this.showDeleteConfirm.bind(this, name, account, role, roleString)} type="dashed">
                            刪除
                        </Button>
                    </div>
                </div>
            );
        }
        else return null
    }

    handleChange(value) {
        this.setState({ role: value })
    }

    adduserhandleChange(value) {
        this.setState({ adduser: value })
    }

    nameChange(event) {
        this.setState({ name: event.target.value })
    }

    inputuserName(event) {
        this.setState({ inputUsername: event.target.value })
    }

    inputPassword(event) {
        this.setState({ inputPassword: event.target.value })
    }

    inputName(event) {
        this.setState({ inputName: event.target.value })
    }

    inputDoctorid(event) {
        this.setState({ inputDoctorid: event.target.value })
    }

    Searchbar() {
        const { Option } = Select;
        const { Search } = Input;
        return (
            <div style={{ display: 'flex', alignItems: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{ fontSize: '16px' }}>
                        角色:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Select placeholder='選擇角色' style={{ width: 150 }} onChange={this.handleChange.bind(this)}>
                            <Option value={0}>唯讀使用者</Option>
                            <Option value={1}>一般使用者</Option>
                            <Option value={2}>一般使用者管理員</Option>
                            <Option value={3}>系統設定管理員</Option>
                            {/*<Option value={4}>超級管理者</Option>*/}
                        </Select>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        姓名:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Search placeholder="輸入個案姓名" onChange={this.nameChange} value={this.state.name} onSearch={this.queryUserdata}></Search>
                    </div>
                </div>
            </div>
        );
    }
    render() {
 
        const columns = [
            {
                title: '編號',
                dataIndex: 'id',
                key: 'id',
                width: "10%"
            },
            {
                title: '姓名',
                dataIndex: 'name',
                key: 'name',
                width: "10%"
            },
            {
                title: '醫師帳號',
                dataIndex: 'account',
                key: 'account',
                width: "12%"
            },
            {
                title: '角色',
                dataIndex: 'role',
                key: 'role',
                width: "10%"
            },
            {
                title: '權限',
                dataIndex: 'power',
                key: 'power',
                width: "40%"
            },
            {
                dataIndex: 'clickstate',
                key: 'clickstate',
                width: "7%"
            }
        ];

        const userData = this.state.userList.map((data, index) => {
            let roleString = ''
            let powerString = ''

            roleString = this.state.powerName[data.role]
            powerString = this.state.power[data.role]

            return [
                {
                    key: ++index,
                    id: index,
                    name: data.name,
                    role: roleString,
                    power: powerString,
                    account: data.username,
                    doctorid: data.doctorid,
                    clickstate: this.EditStyle(index, data.name, data.username, data.role, roleString)
                }
            ]
        })
        let myNewArray = [].concat.apply([], userData);
        const { Option } = Select;
        return (
            <div>
                <div style={{ display: "flex", justifyContent: 'space-between', alignItems: 'center' }}>
                    {this.Searchbar()}
                    <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                        <Button style={{ width: "100%" }} onClick={this.showModal}>新增帳號</Button>
                        <Modal
                            title="新增帳號"
                            visible={this.state.visible}
                            onOk={this.addUserdata}
                            onCancel={this.handleCancel}
                            width="570px"
                        >
                            <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                                {/*<Radio.Group onChange={this.radioOnChange.bind(this)}>
                                    <Radio value={0}>一般帳號</Radio>
                                    <Radio value={1}>SSO帳號</Radio>
                                </Radio.Group>*/}
                            </div>
                            <div style={{ display: "flex", justifyContent: 'center', alignItems: "center", marginTop: "10px" }}>
                                <div style={{ marginLeft: "10px" }}>
                                    醫師帳號：
                                <Input id="username" value={this.state.inputUsername} onChange={this.inputuserName} placeholder="輸入帳號" style={{ width: '120px' }} />
                                </div>
                                {/*<div style={{ marginLeft: "10px" }}>
                                    密碼：
                                <Input type="password" id="password" value={this.state.inputPassword} onChange={this.inputPassword} disabled={this.state.pwdRadio} placeholder="輸入密碼" style={{ width: '120px' }} />
                                </div>*/}
                                <div style={{ marginLeft: "10px" }}>
                                    姓名：
                                <Input id="name" placeholder="輸入姓名" value={this.state.inputName} onChange={this.inputName} style={{ width: '120px' }} />
                                </div>
                            </div>
                            {/*<div style={{ display: "flex", justifyContent: 'left', alignItems: "left", marginTop: "10px", marginLeft: "13px" }}>
                                <div style={{ marginLeft: "0px" }}>
                                    醫師編號：
                                <Input id="doctorid" disabled={this.state.pwdRadio} placeholder="請輸入醫師編號" value={this.state.inputDoctorid} onChange={this.inputDoctorid} style={{ width: '160px' }} />
                                </div>
                            </div>*/}
                            <div style={{ display: "flex", justifyContent: 'center', alignItems: "center", marginTop: "10px", marginLeft: "13px" }}>
                                <div style={{ marginLeft: "0px" }}>
                                    角色：{this.state.pwdRadio ?
                                        <Select placeholder='選擇角色' style={{ width: 150 }} onChange={this.adduserhandleChange.bind(this)}>
                                            <Option value={0}>唯讀使用者</Option>
                                            <Option value={1}>一般使用者</Option>
                                            <Option value={2} disabled={this.state.powerLogin >= 3 ? false : true}>一般使用者管理員</Option>
                                        </Select> :
                                        <Select placeholder='選擇角色' style={{ width: 150 }} onChange={this.adduserhandleChange.bind(this)}>
                                            <Option value={3}>系統設定管理員</Option>
                                        </Select>}
                                </div>
                            </div>
                            <div>

                            </div>
                        </Modal>
                    </div>
                </div>
                <div style={{ marginTop: '20px' }}>
                    <Table columns={columns} dataSource={myNewArray} onRow={record => {
                        return {
                            onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                            onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                        };
                    }} />
                </div>
            </div>
        );
    }
}

export default Powersetting;