import React, { Component } from 'react';
import WardcardvitalsignChart from './Wardcardvitalsign'
import { statecolor } from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
import './Wardchart.scss'
import moment from 'moment';



class WardChart extends Component {
    state = {
        data: []
    }
    render() {
        function valueFormat(value) {
            if (value === null || value === undefined || value === "") {
                return "--"
            }
            return value
        }
        const data = this.state.data
        const { CMvalue, HISID } = this.props
        const vitalvalue = CMvalue
        if (vitalvalue === undefined || vitalvalue === null) {
            return null
        }
        function limitValue(value, upper, lower) {
            if (value > upper || value < lower) {
                return false
            }
            return true
        }
        return (
            <div className='Wardchartlayout'>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.hr, 160, 100) ? "white" : statecolor('HR') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.hr, 160, 100) ? statecolor('HR') : "white" }}>HR(min)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: limitValue(vitalvalue.hr, 160, 100) ? statecolor('HR') : "white" }}>{valueFormat(vitalvalue.hr)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart MAX={200} MIN={0} Upper={160} Lower={100} HISID={HISID} svg_key={"svg_hr"} id_key={"div_hr"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(126, 211, 33, 0.1)"} circlrcolor={"#779F50"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='secondRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.nbpd, 75, 55) ? "white" : statecolor('BP') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.nbpd, 75, 55) ? statecolor('BP') : "white" }}>NBP(mmHg)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "18px", color: limitValue(vitalvalue.nbpd, 75, 55) ? statecolor('BP') : "white" }}>{valueFormat(vitalvalue.nbps)}/{valueFormat(vitalvalue.nbpd)}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "14px", color: limitValue(vitalvalue.nbpd, 75, 55) ? statecolor('BP') : "white" }}>({valueFormat(vitalvalue.nbpm)})</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart MAX={100} MIN={0} Upper={75} Lower={55} HISID={HISID} svg_key={"svg_bp"} id_key={"div_bp"} data={data} data2={true} axisBot={false} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='thirdRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.abpd, 75, 55) ? "white" : statecolor('BP') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.abpd, 75, 55) ? statecolor('BP') : "white" }}>BP(mmHg)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "18px", color: limitValue(vitalvalue.abpd, 75, 55) ? statecolor('BP') : "white" }}>{valueFormat(vitalvalue.abps)}/{valueFormat(vitalvalue.abpd)}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "14px", color: limitValue(vitalvalue.abpd, 75, 55) ? statecolor('BP') : "white" }}>({valueFormat(vitalvalue.abpm)})</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart MAX={100} MIN={0} Upper={75} Lower={55} HISID={HISID} svg_key={"svg_abp"} id_key={"div_abp"} data={data} data2={true} axisBot={true} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                    </div>
                </div>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.spo2, 100, 90) ? "white" : statecolor('SpO2') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>SpO2(%)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: limitValue(vitalvalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>{valueFormat(vitalvalue.spo2)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div>
                            <WardcardvitalsignChart MAX={100} MIN={60} Upper={100} Lower={90} HISID={HISID} svg_key={"svg_spo"} id_key={"div_spo"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(0, 183, 255, 0.1)"} circlrcolor={"#4773B8"}></WardcardvitalsignChart>
                        </div>
                    </div>
                    <div className='singlewardchart'>
                        <div className='secondRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.bt, 38, 36) ? "white" : statecolor('BT') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.bt, 38, 36) ? statecolor('BT') : "white" }}>BT(<sup>&ordm;</sup>C)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: limitValue(vitalvalue.bt, 38, 36) ? statecolor('BT') : "white" }}>{valueFormat(vitalvalue.bt)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart MAX={43} MIN={33} Upper={38} Lower={36} HISID={HISID} svg_key={"svg_bt"} id_key={"div_bt"} data={data} axisBot={false} axisTop={false} colorshadow={"rgba(199, 162, 244,0.1)"} circlrcolor={"#7F3DB5"}></WardcardvitalsignChart>
                    </div>
                    <div className='singlewardchart'>
                        <div className='thirdRowTitle'>
                            <div style={{ width: "120px", height: "90px", backgroundColor: limitValue(vitalvalue.rr, 60, 20) ? "white" : statecolor('RR') }}>
                                <centerstyle>
                                    <t4 style={{ color: limitValue(vitalvalue.rr, 60, 20) ? statecolor('RR') : "white" }}>RR(min)</t4>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontSize: "40px", color: limitValue(vitalvalue.rr, 60, 20) ? statecolor('RR') : "white" }}>{valueFormat(vitalvalue.rr)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <WardcardvitalsignChart MAX={100} MIN={0} Upper={60} Lower={20} HISID={HISID} svg_key={"svg_rr"} id_key={"div_rr"} data={data} axisBot={true} axisTop={false} colorshadow={"rgba(255, 152, 0, 0.1)"} circlrcolor={"#DE7531"}></WardcardvitalsignChart>
                    </div>
                </div>
            </div>
        );
    }
}

export default WardChart;