import React, { Component } from 'react';
import './ExamNonlabtablecell.scss'
// import { Nonlablist } from '../../Data/Wardviewdata'
import moment from 'moment'
import NonlabviewtableCell from '../CrossSectionView/NonlabviewtableCell'
import { Modal } from 'antd';
import InsReportModal from '../ExamReportModal'

class Examnonlabviewtable extends Component {
    state = {
        visible: false
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部"] = []

        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部"].push(input)
        });

        return allResult;
    }

    render() {
        function listforstatus(ordlist, ressect, pickerdaterange, StateMode, InputMode) {
            if (StateMode !== 0) {
                /*
                預設 
                全部 StateMode == 0
                已開單 StateMode == 1, data.ORSTATUS == 30
                已簽收 StateMode == 2, data.ORSTATUS == 31
                已發報告 StateMode == 3, data.ORSTATUS == 32
                */
                const changeState = { '1': '30', '2': '31', '3': '32' }
                ordlist = ordlist.filter(data => {
                    return data.ORSTATUS == changeState[StateMode.toString()]
                })
            }

            if (InputMode !== null) {
                ordlist = ordlist.filter(data => {
                    return data.ORPROCED.match(InputMode) !== null
                })
            }

            var status_report = []
            console.log(ordlist, ressect, pickerdaterange)
            const et = pickerdaterange[1].valueOf() + (24 * 60 * 60 * 1000)
            const st = pickerdaterange[0].valueOf()
            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    const time = element.ORBGNDT.toString().trim() + element.ORBGNTM.toString().trim()
                    const timeStamp = moment(time, 'YYYY-MM-DDHHmmss').valueOf()
                    if (timeStamp > st && timeStamp < et) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDCLNM === element.ORDCLNM
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }
            return status_report
        }
        function statusflag(status) {
            switch (status) {
                case 0:
                    return '已開單'
                case 1:
                    return '已簽收'
                case 2:
                    return '已發報告'
                default:
                    return '--'
            }
        }
        const { PickerDateRange, ORDLIST, RESSECT, StateMode, InputMode } = this.props

        let clusteredData = {}
        if (this.props.parser !== undefined) {
            clusteredData = this.parse(ORDLIST, this.props.parser)
        }

        //filter data by selected class
        let filteredData = clusteredData[this.props.selectedClass];

        //Picker Date 前三天
        const list = listforstatus(filteredData, RESSECT, PickerDateRange, StateMode, InputMode)
        console.log(list)
        const showModal = this.props.showModal
        const nonlabcell = list.map(function (value, index, array) {
            // console.log(value)
            const time = moment(value.element.ORDATE.toString().trim() + value.element.ORTIME.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
            const come = value.element.ORSPCN1
            const name = value.element.ORPROCED
            const status = value.element.ORSTATUS

            return (
                <NonlabviewtableCell key={"Examnonlabviewtable" + index} come={come} time={time} name={name} status={status} hisId={value.element.ORHISTNO} caseId={value.element.ORDSEQCN} month={value.element.ORMM} seq={value.element.ORDSEQNO} showModal={showModal}></NonlabviewtableCell>
            );
        })

        return (
            <div style={{ marginTop: "10px" }}>
                <div style={{ height: "50px", border: "#E2EDF3 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabtebletitlecell'>檢驗日期</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目名稱</t3>
                    <t3 className='nonlabtebletitlecell'>檢體來源</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目狀態</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗結果</t3>
                </div>
                <div className='nonlabviewtable'>
                    {nonlabcell}
                </div>
            </div>
        );
    }
}

export default Examnonlabviewtable
