import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './RTbreathtablecell.scss'
import * as FiliterReg from '../../Function/FiliterReg'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

class RTbreathetablecell extends Component {
    state = {
        onCell: false,
        onEdit: false,
        remark: '',
        powerLogin: 0
    }
    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }
    Mouse_move() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: true
                }
            )
        }
    }

    Mouse_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: false
                }
            )
        }
    }

    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    }

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    seachRemark = () => {
        const { caseinfo, value } = this.props
        console.log(value)
        const id = `Breath${moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()}`
        ApiProvider.getOneRemark(caseinfo.TRANSINID, id, "RTbreath").then(result => {
            // console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    render() {
        const { value, caseinfo } = this.props
        console.log(value)
        const id = `Breath${moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()}`
        const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").format("MM/DD HH:mm")
        const setting = value.Mode.toString().trim()
        const mode = value.Setting.toString().trim()

        const remarkValue = {
            "createTime": time,
            "name": setting,
            "remarkId": id,
            "remark": this.state.remark,
            "transinId": caseinfo.TRANSINID,
            "type": "RTbreath",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <div>
                <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
                <div onMouseMove={() => this.Mouse_move()} onMouseLeave={() => this.Mouse_leave()} className="RTtablecell">
                    <t3 className='RTBreathDateCell'>{time}</t3>
                    <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "100px" : "50px 50px"] }}>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr" }}>
                            <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{setting}</t3>
                            <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{mode}</t3>
                        </div>
                        <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px", display: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "none" : ""] }}>{this.state.remark}</t3>
                    </div>
                    <div style={this.state.onCell ? { display: this.state.powerLogin >= 2 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: "100px", background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RTbreathetablecell
